// eslint-disable-next-line import/named
import { appReducerFactory, initializeStore, RootState as IRootState } from '@noisli/app/dist/redux';
import { alias, wrapStore } from 'webext-redux';
import createUserLimitMiddleware from '@noisli/app/dist/redux/middleware/UserLimitMiddleware/UserLimitMiddleware';
import aliases from './aliases';
import chromeExtensionReducer from '../modules/chromeExtension';
import { ChromeExtensionState } from '../modules/chromeExtension/chromeExtension.interfaces';
import FavouritesObserverMiddleware from '../middleware/FavouriteObserverMiddleware/FavouriteObserverMiddleware';
import OfflineStateObserverMiddleware
  from '../middleware/OfflineStateObserverMiddleware/OfflineStateObserverMiddleware';
import UserAccountChangeObserver from '../middleware/UserAccountChangeObserver/UserAccountChangeObserver';

export interface RootState extends IRootState {
  chromeExtension: ChromeExtensionState;
}

const store = initializeStore(undefined, [
  alias(aliases),
  FavouritesObserverMiddleware,
  OfflineStateObserverMiddleware,
  UserAccountChangeObserver,
  createUserLimitMiddleware(true),
], appReducerFactory({
  chromeExtension: chromeExtensionReducer,
}));

wrapStore(store);

// eslint-disable-next-line @typescript-eslint/no-explicit-any
(window as any).noisliStore = store;

export default store;
