import axios from './AxiosInstance';
import {AccountType, UserSocketEvent as UserLimitEvents} from '@noisli/common';
import { EventEmitter } from 'events';
import UsersApi from './Users.api';

/**
 * (NOT STATIC, NEED INITIALIZATION) Class that have all connection to API regarding User play limit module
 */
export default class UsersLimitConnectApi {
  private emmiter = new EventEmitter();

  public apiUrl = `${UsersApi.apiUrl}/limit`;

  constructor(isChromeExtension = false) {
    if (isChromeExtension) {
      this.apiUrl = `${UsersApi.apiUrl}/limit/chrome`;
    }
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  async userLimitRequest<T extends Record<string, any>>(type: UserLimitEvents, payload: T) {
    const response = await axios.post<{ limitReached: boolean, accountType: AccountType }>(this.apiUrl, {
      type,
      data: payload,
    }, {
      withCredentials: true,
    });

    if (response.data.limitReached) {
      this.emmiter.emit(UserLimitEvents.LIMIT_REACHED, type);
    }
    this.emmiter.emit('ACCOUNT_TYPE_UPDATE', response.data.accountType);
  }

  addEventListener<T>(event: string, handler: (payload: T) => void) {
    return this.emmiter.on(event, handler);
  }
}
