// eslint-disable-next-line import/no-extraneous-dependencies
import {
  Action, AnyAction, Dispatch, Middleware, MiddlewareAPI,
} from 'redux';
import { RootState } from 'app/redux';
import {
  startTimer, stopTimer, pauseTimer, resumeTimer,
} from 'app/redux/modules/timer';
import { PayloadAction } from 'redux-starter-kit';
import TimerManager from './TimerManager';
import { setActiveComboInTimerThunk } from '../../modules/timer/timer.thunks';
import { setPlayerPlaying, setPlayerVolume, stopAllPlayers } from '../../modules/players';
import { SetPlayerPlayingPayload } from '../../modules/players/players.interfaces';
import { getPlayerInstance } from '../../modules/players/players.selectors';
import { StopTimerPayload } from '../../modules/timer/timer.interfaces';
import { markActiveFavourite } from 'app/redux/modules/favourites';
import { markActivePlaylistCombo } from 'app/redux/modules/playlists';

/**
 * Middleware that is responsible for handling timer actions
 * It is switched off if user is not logged in
 * It starts or update timer when player is started, paused or played again
 * It destroys timer if timer is stopped
 * It tracks active combo that is playing for timer fade out to perform correctly
 *
 * Everything is running in switch/case block with visible actions names
 *
 * @returns {({dispatch, getState}: MiddlewareAPI<Dispatch<AnyAction>, RootState>) => (next: Dispatch) => (action: Action) => void}
 */
// eslint-disable-next-line import/prefer-default-export
export const createTimerMiddleware: () => Middleware = () => {
  const timerManager = new TimerManager();
  // For development purposes
  // (global as any).NoisliTimerManager = timerManager;

  return (
    { dispatch, getState }: MiddlewareAPI<Dispatch<AnyAction>, RootState>,
  ) => (next: Dispatch) => (action: Action) => {
    const state = getState();
    const { timer, players, users } = state;
    const { timerConfig } = state.timer;

    if (!users.user.uuid) {
      next(action);
      return;
    }

    switch (action.type) {
      case (startTimer.toString()): {
        timerManager.createOrUpdate({
          dispatch,
          timerConfig,
          timerType: timer.currentTimerType,
          getState,
        });
        next(action);
        break;
      }

      case (stopTimer.toString()): {
        const {
          isCancelled, isLongBreakSkipped,
        } = (action as PayloadAction<StopTimerPayload>).payload;

        timerManager.destroy(isCancelled || isLongBreakSkipped);
        next(action);
        break;
      }

      case (pauseTimer.toString()): {
        timerManager.update({
          pause: true,
        });
        next(action);
        break;
      }

      case (resumeTimer.toString()): {
        timerManager.update({
          resume: true,
        });
        next(action);
        break;
      }

      case (setPlayerPlaying.toString()):
      case (setPlayerVolume.toString()): {
        const playerId = (action as PayloadAction<SetPlayerPlayingPayload>).payload.id;
        const playerInstance = getPlayerInstance(getState(), { id: playerId });
        next(action);
        if (players.masterController.isPlaying
          && playerInstance
          && !playerInstance.isShuffling
          && !playerInstance.isOscillating
        ) {
          dispatch(setActiveComboInTimerThunk() as unknown as AnyAction);
        }
        break;
      }

      case (stopAllPlayers.toString()): {
        dispatch(setActiveComboInTimerThunk() as unknown as AnyAction);
        next(action);
        break;
      }

      case (markActiveFavourite.toString()):
      case (markActivePlaylistCombo.toString()): {
        next(action);
        dispatch(setActiveComboInTimerThunk() as unknown as AnyAction);
        break;
      }

      default:
        next(action);
        break;
    }
  };
};
