import React from 'react';
import { SVGIcon } from '../index';

import account from './account.svg';
import accountSmall from './account-small.svg';
import audioPreview from './audio-preview.svg';
import check from './check.svg';
import decrease from './decrease.svg';
import deleteIcon from './delete.svg';
import download from './download.svg';
import edit from './edit.svg';
import fbFooter from './fb-footer.svg';
import increase from './increase.svg';
import info from './info.svg';
import leftArrow from './left-arrow.svg';
import markdownPreview from './markdown-preview.svg';
import muted from './muted.svg';
import mutedSmall from './muted-small.svg';
import note from './note.svg';
import oscillation from './oscillation.svg';
import remove from './remove.svg';
import rename from './rename.svg';
import rightArrow from './right-arrow.svg';
import saved from './saved.svg';
import share from './share.svg';
import shuffle from './shuffle.svg';
import sound from './sound.svg';
import soundSmall from './sound-small.svg';
import textEditor from './text-editor.svg';
import textEditorSmall from './text-editor-small.svg';
import twFooter from './tw-footer.svg';

import email from './email.svg';
import fb from './fb.svg';
import tw from './tw.svg';

import cancel from './cancel.svg';
import pause from './pause.svg';
import play from './play.svg';
import replay from './replay.svg';
import settings from './settings.svg';
import skip from './skip.svg';

const Icons = {
  account,
  accountSmall,
  audioPreview,
  decrease,
  delete: deleteIcon,
  download,
  edit,
  fbFooter,
  increase,
  info,
  leftArrow,
  markdownPreview,
  muted,
  mutedSmall,
  note,
  oscillation,
  remove,
  rename,
  rightArrow,
  saved,
  share,
  shuffle,
  sound,
  soundSmall,
  textEditor,
  textEditorSmall,
  twFooter,
  email,
  fb,
  tw,
};

interface Props {
  name: keyof typeof Icons;
}

export const InterfaceIcon: React.FC<Props> = ({ name }) => {
  if (Object.keys(Icons).some((key) => key === name)) {
    const Icon = Icons[name];
    return <Icon />;
  }

  return <></>;
};


export const AccountIcon: SVGIcon = account;
export const AccountSmallIcon: SVGIcon = accountSmall;
export const AudioPreviewIcon: SVGIcon = audioPreview;
export const CheckIcon: SVGIcon = check;
