// eslint-disable-next-line import/no-extraneous-dependencies
import { Dispatch, AnyAction } from 'redux';

// eslint-disable-next-line import/no-webpack-loader-syntax,import/no-unresolved
import TickWorker from 'worker-loader?name=static/[hash].worker.js!../../../WebWorkers/tick.worker';

import { TickWorkerInterface } from 'app/WebWorkers/tick.worker';
import dayjs, { Dayjs } from 'dayjs';
import { setActualTime, setIsTerminatingSession } from 'app/redux/modules/timer';
import { TimerConfig, TimerTime, TimerType } from '@noisli/common';
import { formatActualTime } from '../../modules/timer/timer.helpers';
import { stopTimerThunk, setActiveComboInTimerThunk } from '../../modules/timer/timer.thunks';
import { RootState } from '../../index';
import { ComboItem } from '../../modules/common/combos.interfaces';
import {
  setPlayersVolume, stopAllPlayers,
} from '../../modules/players';
import { PlayerVolumePayload } from '../../modules/players/players.interfaces';
import { stopPlayersAfterShuffleThunk } from '../../modules/players/players.thunks';

interface TimerConstructorArgs {
  timerConfig: TimerConfig;
  timerType: TimerType;
  times: TimerTime;
  dispatch: Dispatch<AnyAction>;
  getState: () => RootState;
}

export interface TimerUpdateArgs {
  pause?: boolean;
  resume?: boolean;
}

interface ComboVolume {
  id: string;
  step: number;
  startVolume: number;
  actualVolume: number;
  targetVolume: number;
}

/**
 * Class that is responsible for timer operations
 */
export default class Timer {
  config = {
    interval: 250,
    fadeInterval: 200,
    fadeDuration: 5000,
    ticks: Math.floor(5000 / 200),
  };

  timerConfig: TimerConfig;

  times: TimerTime;

  timerType: TimerType;

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  dispatch: Dispatch<any>;

  getState: () => RootState;

  tickWorker: TickWorkerInterface;

  fadeTickWorker?: TickWorkerInterface;

  goalTime?: Dayjs;

  pauseTimestamp?: Dayjs;

  isTerminating = false;

  fadeComboVolume: ComboVolume[] = [];

  /**
   * Set up timer instance
   * @param timerConfig
   * @param times
   * @param timerType
   * @param dispatch
   * @param getState
   */
  constructor({
    timerConfig,
    times,
    timerType,
    dispatch,
    getState,
  }: TimerConstructorArgs) {
    this.timerConfig = timerConfig;
    this.dispatch = dispatch;
    this.getState = getState;
    this.times = times;
    this.timerType = timerType;
    this.tickWorker = new TickWorker();
    this.init();
  }


  /**
   * Method that set timestamp of goal time
   * We are operating on timestamps to make sure we will be precise
   * even if some throttle would happen
   */
  init() {
    this.goalTime = dayjs()
      .add(this.times.hours, 'hour')
      .add(this.times.minutes, 'minute')
      .add(this.times.seconds, 'second');

    this.runTimer();
  }

  /**
   * React to pause and resume to rebase goal time
   * @param pause
   * @param resume
   */
  update({
    pause,
    resume,
  }: TimerUpdateArgs) {
    if (pause) {
      this.tickWorker.postMessage({
        command: 'stop',
      });
      this.pauseTimestamp = dayjs();
      return;
    }

    if (resume) {
      const timestamp = dayjs();

      if (this.pauseTimestamp && this.goalTime) {
        const secondsDiff = timestamp.diff(this.pauseTimestamp, 'second');
        this.goalTime = this.goalTime.add(secondsDiff, 'second');
      }

      this.tickWorker.postMessage({
        command: 'start_interval',
        payload: {
          interval: this.config.interval,
        },
      });
    }
  }

  /**
   * Method that handle cancellation of timer
   * @param isCancelled
   */
  destroy(isCancelled = false) {
    this.tickWorker?.terminate();
    if (isCancelled) {
      this.fadeTickWorker?.terminate();
      setTimeout(() => this.stopPlayers(), 10);
      this.dispatch(setIsTerminatingSession({
        isTerminating: false,
      }));
    }
  }

  /**
   * Method that runs timer
   */
  runTimer() {
    this.tickWorker.onmessage = () => this.controlTime();

    this.tickWorker.postMessage({
      command: 'start_interval',
      payload: {
        interval: this.config.interval,
      },
    });
  }

  /**
   * Method that is run on every tick of timer worker
   */
  controlTime() {
    const timestamp = dayjs();

    if (this.goalTime) {
      //check if timestamp is after the goaltime (check if we passed the goalTime)
      if (this.goalTime.isBefore(timestamp)) {
        this.tickWorker.terminate();
        this.dispatch(stopTimerThunk());
        this.isTerminating = false;
        return;
      }

      // Compute total time to goal time and get hours, minutes and seconds part
      let totalSeconds = this.goalTime.diff(timestamp, 'second');
      const totalHours = Math.floor(totalSeconds / (60 * 60));
      totalSeconds -= (totalHours * 60 * 60);
      const totalMinutes = Math.floor(totalSeconds / 60);
      totalSeconds -= (totalMinutes * 60);

      // if total time to end is less then 5 seconds, begin fading out the sounds
      const shouldFadeOut = totalHours === 0 && totalMinutes === 0 && totalSeconds < 5;

      if (this.timerType !== TimerType.ShortBreak && !this.isTerminating && shouldFadeOut) {
        this.dispatch(setIsTerminatingSession({
          isTerminating: true,
        }));
        this.isTerminating = true;
        this.fadeOutSession();
      }

      // format actual time and pass it to redux store
      const actualTime = formatActualTime(totalHours, totalMinutes, totalSeconds);

      this.dispatch(setActualTime({ actualTime }));
    }
  }

  /**
   * Method that runs fader of sounds when timer is about to end
   * Get all sounds that are playing right now and lock interactions
   * @returns {Promise<void>}
   */
  async fadeOutSession() {
    const state = this.getState();
    const items = state.players.players.filter((player) => player.isPlaying).map((player) => ({
      id: player.id,
      volume: player.volume,
    }));
    this.dispatch(setActiveComboInTimerThunk());
    this.fadeTickWorker = new TickWorker();
    this.computeVolumeSteps(items);
    this.runFadeOut();
  }

  /**
   * Compute volume steps to perform fade out to 0
   * @param comboItems
   */
  computeVolumeSteps(comboItems: ComboItem[]) {
    this.fadeComboVolume = comboItems.map((comboItem) => {
      const delta = -comboItem.volume;

      return {
        id: comboItem.id,
        startVolume: comboItem.volume,
        actualVolume: comboItem.volume,
        targetVolume: 0,
        step: delta / this.config.ticks,
      };
    });
  }

  /**
   * Runs basic fade out loop
   */
  runFadeOut() {
    let tick = 1;

    const step = async () => {
      tick += 1;

      this.setPlayersVolume();

      if (tick === this.config.ticks) {
        this.setPlayersVolume(true);
        setTimeout(() => this.stopPlayers(), 10);
        this.dispatch(setIsTerminatingSession({
          isTerminating: false,
        }));
      }
    };

    if (!this.fadeTickWorker) {
      return;
    }

    this.fadeTickWorker.onmessage = () => {
      step();

      if (tick === this.config.ticks && this.fadeTickWorker) {
        this.fadeTickWorker.terminate();
      }
    };

    this.fadeTickWorker.postMessage({
      command: 'start_interval',
      payload: {
        interval: this.config.fadeInterval,
      },
    });
  }

  /**
   * Set players volume to actual step volume
   * @param toTargetVolume - if true, set target volume (0)
   */
  setPlayersVolume(toTargetVolume = false) {
    this.fadeComboVolume.forEach((item) => {
      // eslint-disable-next-line no-param-reassign
      item.actualVolume += item.step;
    });

    const playersVolume: PlayerVolumePayload[] = this.fadeComboVolume
      .map((item) => ({
        id: item.id,
        volume: toTargetVolume ? item.targetVolume : item.actualVolume,
      }));

    this.dispatch(setPlayersVolume({
      items: playersVolume,
    }));
  }

  /**
   * Stop all players and reset their volume to starting position
   */
  stopPlayers() {
    const stopPlayersArray: PlayerVolumePayload[] = this.fadeComboVolume
      .map((item) => ({
        id: item.id,
        volume: item.startVolume,
      }));

    this.dispatch(stopPlayersAfterShuffleThunk(stopPlayersArray));
    this.dispatch(stopAllPlayers());
  }
}
