// eslint-disable-next-line import/no-extraneous-dependencies
import {
  Action, AnyAction, Dispatch, Middleware, MiddlewareAPI,
} from 'redux';
import Router from 'next/router';
import { resetUser, setUser } from '../../modules/users';
import localStorage from '../../../utils/localStorage';
import { RootState } from '../../index';

const logoutWhitelist = [
  '/delete-account-success',
];


/**
 * Middleware that handles logout across tabs in browser
 * @returns {({getState}: MiddlewareAPI<Dispatch<AnyAction>, RootState>) => (next: Dispatch) => (action: Action) => void}
 */
// eslint-disable-next-line import/prefer-default-export
export const createAuthMiddleware: () => Middleware = () => (
  { getState }: MiddlewareAPI<Dispatch<AnyAction>, RootState>,
) => {
  window.addEventListener('storage', (event) => {
    const { users } = getState();
    if (users.passwordLock
      || logoutWhitelist.some((item) => !!item.match(window.location.pathname))
    ) {
      return;
    }
    if (event.key === 'logout-event' && Boolean(event.newValue)) {
      Router.replace('/logout/passthrough');
    }
  });

  return (next: Dispatch) => (action: Action) => {
    switch (action.type) {
      case (resetUser.toString()): {
        next(action);
        if (!localStorage.getItem('logout-event')) {
          localStorage.setItem('logout-event', `logout${Math.random()}`);
        }
        break;
      }
      case (setUser.toString()): {
        next(action);
        localStorage.removeItem('logout-event');
        break;
      }
      default:
        next(action);
    }
  };
};
