import { createSlice } from 'redux-starter-kit';
import state from './texts.state';
import { setTextReducer, setIsLoadingReducer, resetTextReducer } from './texts.reducers';

const textsSlice = createSlice({
  slice: 'texts',

  initialState: state,

  reducers: {
    setText: setTextReducer,
    resetText: resetTextReducer,
    setIsLoading: setIsLoadingReducer,
  },
});
// Extract the action creators object and the reducer
const { actions, reducer, selectors } = textsSlice;
// Extract and export each action creator by name
export const {
  setText,
  resetText,
  setIsLoading,
} = actions;

export const { getTexts } = selectors;
// Export the reducer, either as a default or named export
export default reducer;
