import { UserSocketEvent, FavouriteSocketEvent, TimerSocketEvent } from '@noisli/common';
import {
  onLimitReached, onLimitReachedCheck, onTerminateSession, onReloadSession, onUpdateUserDetails,
} from '../../modules/users/users.socketHandlers';
import { onUpdateTimerConfig } from '../../modules/timer/timer.socketHandlers';
import { onUpdateFavourites } from '../../modules/favourites/favourites.socketHandlers';

// Map of handlers for socket events
const socketHandlers = {
  [UserSocketEvent.LIMIT_REACHED]: onLimitReached,
  [UserSocketEvent.LIMIT_REACHED_CHECK]: onLimitReachedCheck,
  [UserSocketEvent.RELOAD_SESSION]: onReloadSession,
  [UserSocketEvent.TERMINATE_SESSION]: onTerminateSession,
  [UserSocketEvent.JOIN_ROOM]: onTerminateSession,
  [UserSocketEvent.UPDATE_USER_DETAILS]: onUpdateUserDetails,
  [FavouriteSocketEvent.UPDATE_FAVOURITES]: onUpdateFavourites,
  [TimerSocketEvent.UPDATE_TIMER_CONFIG]: onUpdateTimerConfig,
};

export type SocketHandlers = typeof socketHandlers;

export default socketHandlers;
