import { createSlice } from 'redux-starter-kit';
import state from './subscriptions.state';
import {
  setSubscriptionPlansReducer,
  setIsLoadingPlansReducer,
  setSubscriptionReducer,
  setPaymentHistoryReducer,
  setTeamMembersReducer,
  addTeamMemberReducer,
  removeTeamMemberReducer,
  resetSubscriptionsReducer,
  setHighRiskTransactionReducer,
  setHighRiskTransactionModalIsOpenReducer,
  setUserWasSubscriberReducer,
} from './subscriptions.reducers';

const subscriptionsSlice = createSlice({
  slice: 'subscriptions',

  initialState: state,

  reducers: {
    setSubscriptionPlans: setSubscriptionPlansReducer,
    setIsLoadingPlans: setIsLoadingPlansReducer,
    setSubscription: setSubscriptionReducer,
    setPaymentHistory: setPaymentHistoryReducer,
    setTeamMembers: setTeamMembersReducer,
    addTeamMember: addTeamMemberReducer,
    removeTeamMember: removeTeamMemberReducer,
    resetSubscriptions: resetSubscriptionsReducer,
    setHighRiskTransaction: setHighRiskTransactionReducer,
    setHighRiskTransactionModalIsOpen: setHighRiskTransactionModalIsOpenReducer,
    setUserWasSubscriber: setUserWasSubscriberReducer,
  },
});
// Extract the action creators object and the reducer
const { actions, reducer, selectors } = subscriptionsSlice;
// Extract and export each action creator by name
export const {
  setSubscriptionPlans,
  setIsLoadingPlans,
  setSubscription,
  setPaymentHistory,
  setTeamMembers,
  addTeamMember,
  removeTeamMember,
  resetSubscriptions,
  setHighRiskTransaction,
  setHighRiskTransactionModalIsOpen,
  setUserWasSubscriber,
} = actions;

export const { getSubscriptions } = selectors;
// Export the reducer, either as a default or named export
export default reducer;
