import homepagePlaylistsList from 'app/assets/playlists_homepage.json';
import playlistsList from 'app/assets/playlists.json';

import {
  Playlist,
  PlaylistListItem,
} from './playlists.interfaces';

// eslint-disable-next-line import/prefer-default-export
export const generatePlaylistsState = (): Playlist[] => playlistsList.map(
  (playlist: PlaylistListItem) => ({
    id: playlist.id,
    name: playlist.name,
    icon: playlist.icon,
    isPlaying: false,
    isShuffling: false,
    disabled: playlist.disabled,
    pro: playlist.pro || false,
    items: playlist.items.map((item) => ({
      id: item.id,
      items: item.items,
      isPlaying: false,
      isShuffling: false,
      pro: item.pro,
    })),
  }),
);

// eslint-disable-next-line import/prefer-default-export
export const generateHomepagePlaylistsState = (): Playlist[] => homepagePlaylistsList.map(
  (playlist: PlaylistListItem) => ({
    id: playlist.id,
    name: playlist.name,
    icon: playlist.icon,
    isPlaying: false,
    isShuffling: false,
    disabled: playlist.disabled,
    pro: playlist.pro || false,
    items: playlist.items.map((item) => ({
      id: item.id,
      items: item.items,
      isPlaying: false,
      isShuffling: false,
    })),
  }),
);

export const generateBackgroundVariant = (backgroundVariant: string) => {
  let variant = backgroundVariant;
  while (variant === backgroundVariant) {
    const variantsCount = 10;
    const randomNumber = Math.floor(Math.random() * variantsCount + 1);
    variant = randomNumber < 10 ? `0${randomNumber}` : `${randomNumber}`;
  }
  return variant;
};
