import config from '../../../config/config';
import webAudioTouchUnlock from '../../../utils/webAudioTouchUnlock';
import {isSafariDesktop} from "../../../utils/mobileDetect";

const cachedBuffers: {[key: string]: AudioBuffer | null} = {
  timeEndNotification: null,
  breakEndNotification: null,
};

let audioContext: AudioContext;

/**
 * Function that initialize audio buffer for sound from url
 * @param url
 * @returns {Promise<null | AudioBuffer>}
 */
const prepareWebAudioContext = async (url: string) => {
  if (!audioContext) {
    return null;
  }
  const resp = await fetch(url);
  const buffer = await resp.arrayBuffer();

  const decodedBuffer = await new Promise<AudioBuffer>(
    resolve => audioContext.decodeAudioData(buffer, decodedData => resolve(decodedData)),
  );

  return decodedBuffer;
};

/**
 * Function that get audio buffer and cache it
 * Also it connects buffer to audio context
 * @param key
 * @param url
 * @returns {Promise<AudioBufferSourceNode | null>}
 */
const getSourceBuffer = async (key: keyof typeof cachedBuffers, url: string) => {
  let buffer;
  if (!cachedBuffers[key]) {
    buffer = await prepareWebAudioContext(url);
    if (!buffer) {
      return null;
    }
    cachedBuffers[key] = buffer;
  }
  buffer = cachedBuffers[key];
  const sourceBuffer = audioContext.createBufferSource();
  sourceBuffer.buffer = buffer;
  sourceBuffer.connect(audioContext.destination);
  if (audioContext.state === 'suspended') {
    audioContext.resume();
  }
  try {
    return sourceBuffer;
  } catch (e) {
    return null;
  }
};

export const playWebAudioTimeEndNotification: () => Promise<AudioBufferSourceNode | null> = () => {
  if (isSafariDesktop()) {
    // Need to use the audio el and audio el is mocked as audio buffer source node for compability
    return new Promise((resolve) => {
      const audioEl = document.getElementById('noisli-timer-end');
      if (audioEl === null) {
        return resolve(null);
      }
      (audioEl as any).start = (audioEl as HTMLAudioElement).play;
      resolve(audioEl as unknown as AudioBufferSourceNode);
    });
  }
  return getSourceBuffer('timeEndNotification', config.timeEndNotificationUrl);
}

export const playWebAudioBreakEndNotification: () => Promise<AudioBufferSourceNode | null> = () => {
  if (isSafariDesktop()) {
    // Need to use the audio el and audio el is mocked as audio buffer source node for compability
    return new Promise((resolve) => {
      const audioEl = document.getElementById('noisli-break-end');
      if (audioEl === null) {
        return resolve(null);
      }
      (audioEl as any).start = (audioEl as HTMLAudioElement).play;
      resolve(audioEl as unknown as AudioBufferSourceNode);
    });
  }
  return getSourceBuffer('breakEndNotification', config.breakEndNotificationUrl);
}

if (typeof window !== 'undefined') {
  try {
    // Fix up for prefixing
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    window.AudioContext = window.AudioContext || (window as any).webkitAudioContext;
    audioContext = new AudioContext();
    webAudioTouchUnlock(audioContext).then(() => {
      getSourceBuffer('timeEndNotification', config.timeEndNotificationUrl);
      getSourceBuffer('breakEndNotification', config.breakEndNotificationUrl);
    });
  } catch (e) {
    // eslint-disable-next-line no-console
    console.error('Web Audio API is not supported in this browser');
  }
}
