// eslint-disable-next-line import/no-extraneous-dependencies
import { AnyAction } from 'redux';
// eslint-disable-next-line import/no-extraneous-dependencies
import { ThunkAction } from 'redux-thunk';
import { RootState } from 'app/redux';
import {
  SetFavouritePlayingPayload,
} from './favourites.interfaces';

import {
  markActiveFavourite, unmarkActiveFavourite, setSharedFavourite, setSharedFavouritePlaying,
} from '.';

import { stopAllPlayers, setPlayerVolume, setActiveCombo } from '../players';
import { setPlayerPlayingThunk } from '../players/players.thunks';
import FavouritesApi from '../../../Api/Favourites.api';

/**
 * Thunk that handle playing of favourite
 * @param payload
 */
// eslint-disable-next-line import/prefer-default-export
export const setFavouritePlayingThunk = (
  payload: SetFavouritePlayingPayload,
): ThunkAction<void, RootState, null, AnyAction> => (dispatch, getState) => {
  const { favourites } = getState();
  const favourite = favourites.favourites.find((item) => item.id === payload.id);

  if (!favourite) {
    return;
  }

  dispatch(stopAllPlayers());

  if (favourite.isPlaying) {
    dispatch(unmarkActiveFavourite());
    return;
  }

  favourite.items.forEach((item) => {
    dispatch(setPlayerVolume({
      id: item.id,
      volume: item.volume,
    }));
    dispatch(setPlayerPlayingThunk({
      id: item.id,
      isPlaying: true,
    }));
  });

  dispatch(markActiveFavourite(payload));
  dispatch(setActiveCombo({
    combo: favourite,
  }));
  FavouritesApi.play();
};
