import { createSlice } from 'redux-starter-kit';
import state from './base.state';
import {
  setIsMobileReducer,
  setShowHomepageModalReducer,
  setPlayerPageReducer,
  setSharedComboReducer,
  setSharedComboIsLoadingReducer,
  setShowSoundsLockedModalReducer,
  setShowUpgradeModalReducer,
  setShowShareModalReducer,
  setShowSharePlayModalReducer,
  setBaseTitleReducer,
  closeAllModalsReducer,
} from './base.reducers';

const baseSlice = createSlice({
  slice: 'base',

  initialState: state,

  reducers: {
    setIsMobile: setIsMobileReducer,
    setShowHomepageModal: setShowHomepageModalReducer,
    setPlayerPage: setPlayerPageReducer,
    setSharedCombo: setSharedComboReducer,
    setSharedComboIsLoading: setSharedComboIsLoadingReducer,
    setShowSoundsLockedModal: setShowSoundsLockedModalReducer,
    setShowUpgradeModal: setShowUpgradeModalReducer,
    setShowShareModal: setShowShareModalReducer,
    setShowSharePlayModal: setShowSharePlayModalReducer,
    setBaseTitle: setBaseTitleReducer,
    closeAllModals: closeAllModalsReducer,
  },
});
// Extract the action creators object and the reducer
const { actions, reducer, selectors } = baseSlice;
// Extract and export each action creator by name
export const {
  setIsMobile,
  setShowHomepageModal,
  setPlayerPage,
  setSharedCombo,
  setSharedComboIsLoading,
  setShowSoundsLockedModal,
  setShowUpgradeModal,
  setShowShareModal,
  setShowSharePlayModal,
  setBaseTitle,
  closeAllModals,
} = actions;

export const { getBase } = selectors;
// Export the reducer, either as a default or named export
export default reducer;
