// eslint-disable-next-line import/no-extraneous-dependencies
import { Action, Dispatch } from 'redux';
import { setShowShareModal } from '@noisli/app/dist/redux/modules/base';
import { setShowLimitReachedModal } from '@noisli/app/dist/redux/modules/users';
import { PayloadAction } from 'redux-starter-kit';
// eslint-disable-next-line import/named
import { SetTimerConfigPayload } from '@noisli/app/dist/redux/modules/timer/timer.interfaces';
import { setTimerConfig, reloadTimerConfig } from '@noisli/app/dist/redux/modules/timer';
import store from './index';
import { performAuthCheck } from '../../background/auth';

export const CHECK_USER_AUTH = 'users/check-auth';

export const checkUserAuth = (originalAction: Action) => {
  performAuthCheck();
  store.dispatch(setShowShareModal({
    showShareModal: false,
  }));
  store.dispatch(setShowLimitReachedModal({
    showModal: false,
  }));
  return originalAction;
};

export const reloadTimerConfigAlias = (
  originalAction: PayloadAction<SetTimerConfigPayload>,
) => (dispatch: Dispatch) => {
  dispatch(reloadTimerConfig({
    timerConfig: originalAction.payload.timerConfig,
  }));
};

export default {
  [CHECK_USER_AUTH]: checkUserAuth,
  [setTimerConfig.toString()]: reloadTimerConfigAlias,
};
