/* eslint-disable no-param-reassign */
import { PayloadAction } from 'redux-starter-kit';
import { IsLoadingPayload } from '../common/isLoading.interface';
import { SetTextPayload, TextsState } from './texts.interfaces';

export const setTextReducer = (
  state: TextsState, action: PayloadAction<SetTextPayload>,
) => {
  state.text = action.payload.text;
};

export const setIsLoadingReducer = (
  state: TextsState, action: PayloadAction<IsLoadingPayload>,
) => {
  state.isLoading = action.payload.isLoading;
};

export const resetTextReducer = (
  state: TextsState,
) => {
  state.text = '';
};
