import { hex2Rgb } from '../../styles/utils';

export interface RGBParts {
  r: number;
  g: number;
  b: number;
}

/**
 * Convert rgb to color parts
 * @param color {string} - color in rgb format
 * @returns {{r: number; b: number; g: number} | null}
 */
export const rgbToParts = (color: string): RGBParts | null => {
  const rgb = color.match(/^rgb\((\d+),\s*(\d+),\s*(\d+)\)$/);
  if (rgb) {
    return {
      r: parseInt(rgb[1], 10),
      g: parseInt(rgb[2], 10),
      b: parseInt(rgb[3], 10),
    };
  }

  return null;
};

/**
 * Convert parts to rgb color
 * @param r
 * @param g
 * @param b
 * @returns {string}
 */
export const partsToRgb = (
  { r, g, b }: RGBParts,
): string => `rgb(${Math.floor(r)}, ${Math.floor(g)}, ${Math.floor(b)})`;


/**
 * Function that convert color to rgb parts
 * @param color {string} - hexadecimal or rgb notation
 * @returns {RGBParts | null}
 */
export const colorToRGBParts = (color: string) => {
  if (color[0] === '#') {
    return rgbToParts(hex2Rgb(color));
  }
  return rgbToParts(color);
};

/**
 * Calculate delta between 2 rgb parts
 * @param startParts
 * @param endParts
 * @returns {{r: number; b: number; g: number}}
 */
export const getPartsDelta = (startParts: RGBParts, endParts: RGBParts): RGBParts => ({
  r: endParts.r - startParts.r,
  g: endParts.g - startParts.g,
  b: endParts.b - startParts.b,
});

/**
 * Get step value of each part
 * @param partsDelta
 * @param steps
 * @returns {{r: number; b: number; g: number}}
 */
export const getPartsStep = (partsDelta: RGBParts, steps: number): RGBParts => ({
  r: partsDelta.r / steps,
  g: partsDelta.g / steps,
  b: partsDelta.b / steps,
});

/**
 * Sum rgb parts
 * @param basePart
 * @param addPart
 * @returns {{r: number; b: number; g: number}}
 */
export const sumParts = (basePart: RGBParts, addPart: RGBParts): RGBParts => ({
  r: basePart.r + addPart.r,
  g: basePart.g + addPart.g,
  b: basePart.b + addPart.b,
});
