// eslint-disable-next-line import/no-extraneous-dependencies
import {
  MiddlewareAPI, Dispatch, AnyAction, Action,
} from 'redux';
import { RootState } from 'app/redux';
import {
  setPlayerPlaying, setPlayerVolume, stopAllPlayers, setActiveCombo, setShufflingTranstionState,
} from 'app/redux/modules/players';
import { PayloadAction } from 'redux-starter-kit';
import { SetPlayerVolumePayload } from 'app/redux/modules/players/players.interfaces';
import { getPlayerInstance } from 'app/redux/modules/players/players.selectors';
import { unmarkActivePlaylistCombo, setPlaylistBackground } from 'app/redux/modules/playlists';
import { getActivePlaylistCombo } from 'app/redux/modules/playlists/playlists.selectors';
import { SetShufflePayload } from '../../modules/common/shuffle.interfaces';
import { generateBackgroundVariant } from '../../modules/playlists/playlists.utils';

/**
 * Middleware that is responsible for handling playlists changes
 * It tracks changes to volume, players and stoping players to unmark active playlist
 * It also checks if volume change is caused by oscillation/shuffle
 * @param dispatch
 * @param getState
 * @returns {(next: Dispatch) => (action: Action) => void}
 * @constructor
 */
const PlaylistsMiddleware = (
  { dispatch, getState }: MiddlewareAPI<Dispatch<AnyAction>, RootState>,
) => (next: Dispatch) => (action: Action) => {
  const state = getState();
  const activePlaylist = state.playlists.playlists.find((item) => item.isPlaying);

  switch (action.type) {
    case (setPlayerVolume.toString()): {
      const { payload } = (action as PayloadAction<SetPlayerVolumePayload, string>);
      const playerInstance = getPlayerInstance(state, { id: payload.id });
      const activePlaylistCombo = getActivePlaylistCombo(state);

      next(action);

      if (!playerInstance || !activePlaylistCombo) {
        break;
      }

      if (playerInstance.isShuffling || activePlaylistCombo.isShuffling) {
        break;
      }

      const isPlayerInCombo = activePlaylistCombo.items.find(
        (item) => item.id === playerInstance.id,
      );

      if (!playerInstance.isOscillating && isPlayerInCombo) {
        dispatch(unmarkActivePlaylistCombo());
      }

      if (playerInstance.isOscillating && isPlayerInCombo) {
        const { activeCombo } = getState().players;
        if (!activeCombo) {
          return;
        }
        dispatch(setActiveCombo({
          combo: {
            ...activeCombo,
            items: activeCombo.items.map((item) => {
              let { volume } = item;
              if (item.id === playerInstance.id) {
                // eslint-disable-next-line prefer-destructuring
                volume = payload.volume;
              }

              return {
                id: item.id,
                volume,
              };
            }),
          },
        }));
      }

      break;
    }

    case (setPlayerPlaying.toString()): {
      next(action);
      if (activePlaylist && activePlaylist.isShuffling) {
        break;
      }
      dispatch(unmarkActivePlaylistCombo());
      break;
    }

    case (stopAllPlayers.toString()): {
      next(action);
      dispatch(unmarkActivePlaylistCombo());
      break;
    }

    case (setShufflingTranstionState.toString()): {
      next(action);
      const { payload } = (action as PayloadAction<SetShufflePayload, string>);
      if (payload.isShuffling) {
        dispatch(setPlaylistBackground({
          playlistBackground: generateBackgroundVariant(state.playlists.playlistBackground),
        }));
      }
      break;
    }

    default:
      next(action);
      break;
  }
};

export default PlaylistsMiddleware;
