/* eslint-ignore react/jsx-curly-brace-presence */
import React, { MouseEvent } from 'react';
import { Playlist } from '@noisli/app/dist/redux/modules/playlists/playlists.interfaces';
import { useSelector } from 'react-redux';
import { getUsers } from '@noisli/app/dist/redux/modules/users';
import { getTimer } from '@noisli/app/dist/redux/modules/timer';
import { AccountType } from '@noisli/common';
import ArrowTooltip from '@noisli/app/dist/components/Tooltip/Tooltip';
import { PlaylistIcon, ShareIcon, SkipIcon } from '@noisli/icons';
import {
  Container, Wrapper, Title, IconContainer, ButtonsContainer, IconButton,
} from './PlaylistsItem.styled';
import { MessageText } from '../../Layout/Header/Header.styled';

const tooltipText = (
  <MessageText>
    <a
      href={`${process.env.REACT_APP_PAGE_URL}/billing/change-plan?plan=Pro&cycle=year`}
      target="_blank"
      rel="noreferrer noopener"
    >
      Upgrade
    </a>
    {' to '}
    <strong>Pro</strong>
    {' to'}
    <br />
    enjoy all Playlists.
  </MessageText>
);

interface Props {
  playlist: Playlist;
  onClick: () => void;
  onNextClick: () => void;
  onShareButtonClick: (e: MouseEvent<HTMLDivElement>) => void;
}

const PlaylistsItem: React.FC<Props> = ({
  playlist, onClick, onNextClick, onShareButtonClick,
}) => {
  const { user } = useSelector(getUsers);
  const { isTerminatingSession } = useSelector(getTimer);

  const [isTooltipOpen, setIsTooltipOpen] = React.useState(false);

  const isDisabled = playlist.pro && user.accountType === AccountType.FREE;

  const handleMouseEnter = () => {
    if (isDisabled) {
      setIsTooltipOpen(true);
    }
  };

  const handleMouseLeave = () => {
    setIsTooltipOpen(false);
  };

  return (
    <Wrapper
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
    >
      <ArrowTooltip
        title={tooltipText}
        white
        placement="top"
        open={isTooltipOpen}
      >
        <Container
          active={playlist.isPlaying}
          disabled={isDisabled}
          onClick={() => {
            if (isDisabled || isTerminatingSession) {
              return;
            }
            onClick();
          }}
        >
          <IconContainer>
            <PlaylistIcon name={playlist.icon} />
          </IconContainer>
          <Title>
            { playlist.name }
          </Title>
          { playlist.isPlaying && (
            <ButtonsContainer>
              <IconButton onClick={onShareButtonClick}>
                <ShareIcon />
              </IconButton>
              <IconButton
                active
                onClick={(e) => {
                  e.stopPropagation();
                  if (!playlist.isPlaying) {
                    return;
                  }
                  if (isTerminatingSession) {
                    return;
                  }
                  onNextClick();
                }}
              >
                <SkipIcon />
              </IconButton>
            </ButtonsContainer>
          )}
        </Container>
      </ArrowTooltip>
    </Wrapper>
  );
};

export default PlaylistsItem;
