import axios from './AxiosInstance';
import {
  TimerConfig, TimerStats, TimerStatsSaveDto, TimerSessionCount,
} from '@noisli/common';

/**
 * Static class that have all connection to API regarding Timer module
 */
export default class TimerApi {
  static apiUrl = `${process.env.REACT_APP_API_URL}/timer`;

  static async getConfig() {
    const response = await axios.get<TimerConfig>(`${TimerApi.apiUrl}`, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async updateConfig(body: TimerConfig) {
    const response = await axios.put<string>(`${TimerApi.apiUrl}`, body, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async saveCompletedSession(body: TimerStatsSaveDto) {
    const response = await axios.post<TimerStats>(`${TimerApi.apiUrl}/save-completed-session`, body, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async getTimerStats(body: TimerStatsSaveDto) {
    const response = await axios.post<TimerStats[]>(`${TimerApi.apiUrl}`, body, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async getSessionCountForDay(body: TimerStatsSaveDto) {
    const response = await axios.post<TimerSessionCount>(`${TimerApi.apiUrl}/session-count`, body, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }
}
