import Router from 'next/router';
import { UpdateUserDetailsResponseEventData } from '@noisli/common';
import { setLimitReached } from './index';
import { SocketHandler, SocketHandlerWithData } from '../../middleware/SocketMiddleware/Socket.interface';
import { setUserDataWithIncludesThunk } from './users.thunks';

export const onLimitReached: SocketHandler = ({ dispatch }) => {
  dispatch(setLimitReached({
    limitReached: true,
  }));
};

export const onLimitReachedCheck: SocketHandler = ({ dispatch }) => {
  dispatch(setLimitReached({
    limitReached: true,
  }));
};

export const onTerminateSession: SocketHandler = ({ getState }) => {
  const curState = getState();
  if (curState.users.passwordLock) {
    return;
  }
  Router.replace('/logout/passthrough');
};

export const onReloadSession: SocketHandler = () => {
  window.location.reload();
};

export const onUpdateUserDetails: SocketHandlerWithData<UpdateUserDetailsResponseEventData> = (
  { dispatch },
  data,
) => {
  dispatch(setUserDataWithIncludesThunk(data.userData));
};
