import thunkMiddleware from 'redux-thunk';
import { Store, applyMiddleware } from 'webext-redux';
import { CHECK_USER_AUTH } from './background/aliases';

const store = new Store();
const storeWithMiddleware = applyMiddleware(store, thunkMiddleware);

// eslint-disable-next-line @typescript-eslint/no-explicit-any
(window as any).noisliStore = storeWithMiddleware;

storeWithMiddleware.dispatch({
  type: CHECK_USER_AUTH,
});

export default storeWithMiddleware;
