// eslint-disable-next-line import/no-extraneous-dependencies
import { AnyAction } from 'redux';
// eslint-disable-next-line import/no-extraneous-dependencies
import { ThunkAction } from 'redux-thunk';
import sample from 'lodash/sample';
import { RootState } from 'app/redux';
import { AccountType } from '@noisli/common';
import { PlaylistIdPayload } from './playlists.interfaces';

import { markActivePlaylistCombo, setPlaylistBackground } from '.';

import { setActiveCombo, setPlayerVolume, stopAllPlayers } from '../players';
import { setPlayerPlayingThunk } from '../players/players.thunks';
import { generateBackgroundVariant } from './playlists.utils';
import PlaylistsStatsApi from "../../../Api/PlaylistsStats.api";

/**
 * Thunk that plays next combo in playlist
 * First get a playlist
 * Then select combo that is currently not playing nad not shuffling
 * Also remove pro combos for free user
 * Stop all players and run players for selected combo
 * @param payload
 * @param changeBackground
 * @returns {(dispatch, getState) => void}
 */
export const playNextComboInPlaylistThunk = (
  payload: PlaylistIdPayload, changeBackground = false,
): ThunkAction<void, RootState, null, AnyAction> => (dispatch, getState) => {
  const { playlists, users } = getState();
  const playlist = playlists.playlists.find((item) => item.id === payload.id);

  if (!playlist) {
    return;
  }

  const comboToPlay = sample(playlist.items.filter((item) => {
    if (users.user && users.user.accountType === AccountType.FREE && item.pro) {
      return false;
    }
    return !item.isPlaying && !item.isShuffling;
  }));

  if (!comboToPlay) {
    return;
  }

  dispatch(stopAllPlayers());

  comboToPlay.items.forEach((item) => {
    dispatch(setPlayerVolume({
      id: item.id,
      volume: item.volume,
    }));
    dispatch(setPlayerPlayingThunk({
      id: item.id,
      isPlaying: true,
    }));
  });

  dispatch(markActivePlaylistCombo({
    comboId: comboToPlay.id,
    playlistId: playlist.id,
  }));

  dispatch(setActiveCombo({
    combo: comboToPlay,
  }));

  if (changeBackground) {
    dispatch(setPlaylistBackground({
      playlistBackground: generateBackgroundVariant(playlists.playlistBackground),
    }));
  }
};

/**
 * Thunk that plays or pause playlist
 * @param payload
 * @returns {(dispatch, getState) => void}
 */
export const setPlaylistPlayingThunk = (
  payload: PlaylistIdPayload,
): ThunkAction<void, RootState, null, AnyAction> => (dispatch, getState) => {
  const { playlists } = getState();

  const playlist = playlists.playlists.find((item) => item.id === payload.id);

  if (!playlist) {
    return;
  }

  if (playlist.isPlaying) {
    dispatch(stopAllPlayers());
    dispatch(setPlaylistBackground({
      playlistBackground: generateBackgroundVariant(playlists.playlistBackground),
    }));
    return;
  }

  dispatch(playNextComboInPlaylistThunk(payload));
  PlaylistsStatsApi.saveStat(playlist.name);
};
