import React from 'react';
import Link from 'next/link';
import { useSelector } from 'react-redux';
import { getUsers } from 'app/redux/modules/users';
import { StyledLogo, StyledLogoProps } from './Logo.styled';

type Props = StyledLogoProps;

const Logo: React.FC<Props> = (props) => {
  const { user } = useSelector(getUsers);
  let href = '/';

  if (user.uuid) {
    href = '/playlists';
  }

  return (
    <Link href={href} passHref>
      <StyledLogo {...props}>
        Noisli
      </StyledLogo>
    </Link>
  );
};

export default Logo;
