import React from 'react';
import { Dot, DotsCounterContainer } from '../OnboardingModal.styled';

interface Props {
  activeDot: number;
  dotsCount: number;
  setSectionId: (sectionId: number) => void;
}

const DotsCounter: React.FC<Props> = ({ activeDot, dotsCount, setSectionId }) => (
  <DotsCounterContainer>
    {[...new Array(dotsCount)].map((_, i) => i).map((index) => (
      <Dot key={index} active={activeDot === index} onClick={() => setSectionId(index)} />
    ))}
  </DotsCounterContainer>
);

export default DotsCounter;
