/* eslint-disable no-param-reassign */
import { PayloadAction } from 'redux-starter-kit';
import { ChromeExtensionState, SetChromeExtensionPage, SetIsOfflinePayload } from './chromeExtension.interfaces';

// eslint-disable-next-line import/prefer-default-export
export const setChromeExtensionPageReducer = (
  state: ChromeExtensionState, action: PayloadAction<SetChromeExtensionPage>,
) => {
  state.page = action.payload.page;
};

export const setOfflineStateReducer = (
  state: ChromeExtensionState, action: PayloadAction<SetIsOfflinePayload>,
) => {
  state.isOffline = action.payload.isOffline;
};

export const setHasAskForPermissionInSessionReducer = (
  state: ChromeExtensionState,
) => {
  state.hasAskForPermission = true;
};
