import React, { MouseEvent } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getPlaylists } from '@noisli/app/dist/redux/modules/playlists';
import { getUsers, setShowLimitReachedModal } from '@noisli/app/dist/redux/modules/users';
import {
  playNextComboInPlaylistThunk,
  setPlaylistPlayingThunk,
} from '@noisli/app/dist/redux/modules/playlists/playlists.thunks';
import { createSharedComboThunk } from '@noisli/app/dist/redux/modules/base/base.thunks';
import { Playlist } from '@noisli/app/dist/redux/modules/playlists/playlists.interfaces';
import PlaylistsItem from './PlaylistsItem/PlaylistsItem';
import { Container } from './Playlists.styled';

const PlaylistsContainer = () => {
  const dispatch = useDispatch();
  const { playlists } = useSelector(getPlaylists);
  const { limitReached } = useSelector(getUsers);

  const handlePlaylistClick = (id: string) => {
    if (limitReached) {
      dispatch(setShowLimitReachedModal({
        showModal: true,
      }));
      return;
    }
    dispatch(setPlaylistPlayingThunk({
      id,
    }));
  };

  const handlePlaylistNextClick = (id: string) => {
    dispatch(playNextComboInPlaylistThunk({
      id,
    }));
  };

  const handleShareButtonClick = (e: MouseEvent<HTMLDivElement>, playlist: Playlist) => {
    e.stopPropagation();
    if (playlist.isPlaying) {
      dispatch(createSharedComboThunk());
    }
  };

  return (
    <Container>
      {playlists.map(item => (
        <PlaylistsItem
          key={item.id}
          playlist={item}
          onClick={() => handlePlaylistClick(item.id)}
          onNextClick={() => handlePlaylistNextClick(item.id)}
          onShareButtonClick={e => handleShareButtonClick(e, item)}
        />
      ))}
    </Container>
  );
};

export default PlaylistsContainer;
