// eslint-disable-next-line import/no-extraneous-dependencies
import { Dispatch, AnyAction } from 'redux';
import sample from 'lodash/sample';
import { Combo } from 'app/redux/modules/common/combos.interfaces';
import {
  ShuffleConfig, SetComboIsShuffling, MarkActive, UnmarkActive,
} from 'app/redux/modules/common/shuffle.interfaces';
import { setFavouriteIsShuffling, markFavourite, unmarkFavourite } from 'app/redux/modules/favourites';
import { setPlaylistComboIsShuffling, markActiveCombo, unmarkActiveCombo } from 'app/redux/modules/playlists';
import { setPlayerVolume, setShuffledCombos } from 'app/redux/modules/players';
import Shuffler from './Shuffler';

export enum ShuffleType {
  PLAYLIST,
  FAVOURITES,
}

export interface ShuffleReducers {
  setComboIsShuffling: SetComboIsShuffling;
  setPlayerVolume: typeof setPlayerVolume;
  markActive: MarkActive;
  unmarkActive: UnmarkActive;
}

interface ShuffleManagerCreateArgs {
  dispatch: Dispatch<AnyAction>;
  shuffleConfig: ShuffleConfig;
  combosList: Combo[];
  activeCombo: Combo;
  type: ShuffleType;
}

/**
 * Class that is responsible of managing the shuffler of playing players
 * It creates or updates shuffler when shuffling is on or favourite or playlist is activated
 * It ends transition and destroy the shuffler
 * It destroys shuffler immediately
 *
 * This class have different redux bindings depending on favourite or playlists playing
 */
export default class ShuffleManager {
  shuffler: Shuffler | null = null;

  shuffleFavouriteReducers: ShuffleReducers = {
    setComboIsShuffling: setFavouriteIsShuffling,
    setPlayerVolume,
    markActive: markFavourite,
    unmarkActive: unmarkFavourite,
  }

  shufflePlaylistReducers: ShuffleReducers = {
    setComboIsShuffling: setPlaylistComboIsShuffling,
    setPlayerVolume,
    markActive: markActiveCombo,
    unmarkActive: unmarkActiveCombo,
  }

  createOrUpdate({
    dispatch,
    shuffleConfig,
    combosList,
    activeCombo,
    type,
  }: ShuffleManagerCreateArgs) {
    if (!this.shuffler) {
      const notActiveCombos = combosList.filter((combo) => !combo.isPlaying);
      const nextCombo = sample(notActiveCombos);

      if (!nextCombo) {
        return;
      }

      const reducers = type === ShuffleType.FAVOURITES
        ? this.shuffleFavouriteReducers
        : this.shufflePlaylistReducers;

      this.shuffler = new Shuffler({
        dispatch,
        fromCombo: activeCombo,
        toCombo: nextCombo,
        shuffleConfig,
        reducers,
      });

      dispatch(setShuffledCombos({
        fromCombo: activeCombo.id,
        toCombo: nextCombo.id,
      }));
    } else {
      this.shuffler.update(activeCombo);
    }
  }

  endTransitionAndDestroy() {
    if (this.shuffler) {
      this.shuffler.endTransitionAndDestroy().then(() => {
        this.shuffler = null;
      });
    }
  }

  destroy(revertToOriginal = false) {
    if (this.shuffler) {
      this.shuffler.destroy(revertToOriginal);
      this.shuffler = null;
    }
  }
}
