import React from 'react';
import { NoteIcon, RemoveIcon } from '@noisli/icons';
import {
  StyledBanner, ErrorIcon, CloseIcon, StyledBannerProps,
} from './ErrorBanner.styled';
import Portal from '../Portal/Portal';

interface ErrorBannerProps extends StyledBannerProps {
  message?: string;
  onCloseClick?: () => void;
}

/**
 * Error banner component that appear on top of page if something happen
 * @param message
 * @param show
 * @param onCloseClick
 * @returns {any}
 * @constructor
 */
const ErrorBanner: React.FC<ErrorBannerProps> = ({
  message, show, onCloseClick,
}) => {
  const [timeoutId, setTimeoutId] = React.useState(0);

  React.useEffect(() => {
    if (show) {
      clearTimeout(timeoutId);

      const id = setTimeout(() => {
        if (onCloseClick) {
          onCloseClick();
        }
      }, 5000);

      setTimeoutId(id);
    }
  }, [show]);

  return (
    <Portal selector="#portal">
      <StyledBanner show={show}>
        <ErrorIcon>
          <NoteIcon />
        </ErrorIcon>
        {message}
        <CloseIcon onClick={onCloseClick}>
          <RemoveIcon />
        </CloseIcon>
      </StyledBanner>
    </Portal>
  );
};

export default ErrorBanner;
