// eslint-disable-next-line import/no-extraneous-dependencies
import createSelector from 'selectorator';
import { RootState } from 'app/redux';
import { PlayersState, Player } from './players.interfaces';
import { getPlayers } from '.';

// eslint-disable-next-line import/prefer-default-export
export const getPlayerInstance = createSelector<[RootState, { id: string }], Player | undefined>(
  [getPlayers, { path: 'id', argIndex: 1 }],
  ({ players, playersHomepage, useHomepage }: PlayersState, playerId: string) => {
    if (useHomepage) {
      return playersHomepage.find((player) => player.id === playerId);
    }
    return players.find((player) => player.id === playerId);
  },
);

// eslint-disable-next-line import/prefer-default-export
export const getPlayersAreLoaded = createSelector<RootState, boolean>(
  [getPlayers],
  ({ players }: PlayersState) => players.every((player) => player.isLoaded),
);
