import { css, keyframes } from 'styled-components';
import { Arrow } from 'app/components/Tooltip/Tooltip.styled';

const tooltipBound = keyframes`
 0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-15px);
  }
  100% {
    transform: translateY(0);
  }
`;

const styles = css`
  /* Arrows */
  .MuiTooltip-popper {
    position: relative;
    pointer-events: all !important;
    z-index: 2 !important;
    
    .MuiTooltip-tooltip {
      position: relative;
      background: ${(props) => props.theme.colors.lightGrey1};
      box-shadow: 0 2px 4px ${(props) => props.theme.colors.black20};
      border-radius: 11px;
      padding: 16px;
      
      &--homepage {
        box-shadow: 0 2px 4px ${(props) => props.theme.colors.black05};
        background: ${(props) => props.theme.colors.black16};
        padding: 10px 32px;
        border-radius: 18px;
        animation: ${tooltipBound} 1.2s ease-in-out infinite;
      }
      
      &--white {
        background: ${(props) => props.theme.colors.white};
        text-align: center;
        padding: 16px;
      }
      
      &--hidden {
        display: none;
      }
      
      &--halfSpaceTop {
        margin-top: 16px;
      }
    }

    &[x-placement*="bottom"] {
      ${Arrow} {
        top: 0;
        left: 0;
        margin-top: -0.95em;
        width: 2em;
        height: 1em;
        
        &::before {
          border-width: 0 1em 1em 1em;
          border-color: transparent transparent ${(props) => props.theme.colors.lightGrey1} transparent;
        }
      }
      
      .MuiTooltip-tooltip--homepage ${Arrow} {
        margin-top: -10px;
        ::before {
          border-width: 0 10px 10px 10px;
          border-color: transparent transparent ${(props) => props.theme.colors.black16} transparent;
        }
      }
      
      .MuiTooltip-tooltip--white ${Arrow}::before {
        border-color: transparent transparent ${(props) => props.theme.colors.white} transparent;
      }
    }

    &[x-placement*="top"] {
      ${Arrow} {
        bottom: 0;
        left: 0;
        margin-bottom: -0.95em;
        width: 2em;
        height: 1em;
  
        &::before {
          border-width: 1em 1em 0 1em;
          border-color: ${(props) => props.theme.colors.lightGrey1} transparent transparent transparent;
        }
      }
      
      .MuiTooltip-tooltip--homepage ${Arrow} {
        margin-bottom: -10px;
        width: 20px;
        height: 10px;
        ::before {
          border-width: 10px 10px 0 10px;
          position: relative;
          z-index: 1;
          border-color: ${(props) => props.theme.colors.black16} transparent transparent transparent;
        }
      }
      
      .MuiTooltip-tooltip--homepage ${Arrow}::after {
        content: '';
        margin: auto;
        display: block;
        width: 0;
        height: 0;
        border-style: solid;
        margin-top: -10px;
        z-index: 0;
        border-width: 10px 10px 0 10px;
        border-color: ${(props) => props.theme.colors.primary} transparent transparent transparent;
      }
      
      .MuiTooltip-tooltip--white ${Arrow}::before {
        border-color: ${(props) => props.theme.colors.white} transparent transparent transparent;
      }
    }

    &[x-placement*="right"] {
      ${Arrow} {
        left: 0;
        margin-left: -0.95em;
        height: 2em;
        width: 1em;
  
        &::before {
          border-width: 1em 1em 1em 0;
          border-color: transparent ${(props) => props.theme.colors.lightGrey1} transparent transparent;
        };
      }
      
      .MuiTooltip-tooltip--white ${Arrow}::before {
        border-color: transparent ${(props) => props.theme.colors.white} transparent transparent;
      }
    };

    &[x-placement*="left"] {
      ${Arrow} {
        right: 0;
        margin-right: -0.95em;
        height: 2em;
        width: 1em;
  
        &::before {
          border-width: 1em 0 1em 1em;
          border-color: transparent transparent transparent ${(props) => props.theme.colors.lightGrey1};
        };
      }
      
      .MuiTooltip-tooltip--white ${Arrow}::before {
        border-color: transparent transparent transparent ${(props) => props.theme.colors.white};
      }
    };
  }
`;

export default styles;
