import { createSlice } from 'redux-starter-kit';
import state from './timer.state';
import {
  startTimerReducer,
  stopTimerReducer,
  setActualTimeReducer,
  setCurrentTimerTypeReducer,
  pauseTimerReducer,
  resumeTimerReducer,
  setTimerConfigReducer,
  updateTimerConfigReducer,
  setIsTerminatingSessionReducer,
  setActiveTimerComboReducer,
  setSessionsCompletedReducer,
  setSessionsForDayReducer,
  setTimerIsActiveReducer,
  setTimerTitleReducer,
  reloadTimerConfigReducer,
  setAllSessionsCompletedReducer,
  incrementAllSessionsCompletedReducer,
  resetSavedSessionStateReducer,
} from './timer.reducers';

const textsSlice = createSlice({
  slice: 'timer',

  initialState: state,

  reducers: {
    startTimer: startTimerReducer,
    stopTimer: stopTimerReducer,
    pauseTimer: pauseTimerReducer,
    resumeTimer: resumeTimerReducer,

    setActualTime: setActualTimeReducer,

    setTimerConfig: setTimerConfigReducer,
    updateTimerConfig: updateTimerConfigReducer,
    reloadTimerConfig: reloadTimerConfigReducer,

    setCurrentTimerType: setCurrentTimerTypeReducer,

    setIsTerminatingSession: setIsTerminatingSessionReducer,
    setActiveTimerCombo: setActiveTimerComboReducer,

    setSessionsCompleted: setSessionsCompletedReducer,
    setSessionsForDay: setSessionsForDayReducer,

    setTimerIsActive: setTimerIsActiveReducer,

    setTimerTitle: setTimerTitleReducer,

    setAllSessionsCompleted: setAllSessionsCompletedReducer,
    incrementAllSessionsCompleted: incrementAllSessionsCompletedReducer,

    resetSavedSessionState: resetSavedSessionStateReducer,
  },
});
// Extract the action creators object and the reducer
const { actions, reducer, selectors } = textsSlice;
// Extract and export each action creator by name
export const {
  startTimer,
  stopTimer,
  setCurrentTimerType,
  setActualTime,
  pauseTimer,
  resumeTimer,
  setTimerConfig,
  updateTimerConfig,
  setActiveTimerCombo,
  setIsTerminatingSession,
  setSessionsCompleted,
  setSessionsForDay,
  setTimerIsActive,
  setTimerTitle,
  reloadTimerConfig,
  setAllSessionsCompleted,
  incrementAllSessionsCompleted,
  resetSavedSessionState,
} = actions;

export const { getTimer } = selectors;
// Export the reducer, either as a default or named export
export default reducer;
