import axios from './AxiosInstance';
import { IncomingMessage, OutgoingHttpHeaders } from 'http';
import {
  LoginUserDto,
  UserRO,
  CreateUserDto,
  MessageRO,
  ResetPasswordDto,
  ResetPasswordChangeDto,
  DeleteAccountDto,
  DeleteAccountFeedbackDto,
  PasswordChangeDto,
  ProfileUpdateDto,
  WithMessage,
  PreferencesUpdateDto, UserIncludes, WithToken, DeleteAccountConfirmDto,
} from '@noisli/common';
import { setCookie } from 'nookies';
import dayjs from 'dayjs';
import { setAuthHeader } from 'app/components/withAuth/withAuth';

/**
 * Static class that have all connection to API regarding Users module
 */
export default class UsersApi {
  static apiUrl = `${process.env.REACT_APP_API_URL}/user`;

  static async login(payload: LoginUserDto) {
    const response = await axios.post<WithToken<UserRO>>(`${UsersApi.apiUrl}/login`, payload, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async logout() {
    const response = await axios.get<string>(`${UsersApi.apiUrl}/logout`, {
      withCredentials: true,
    });

    return response;
  }

  static async register(payload: CreateUserDto) {
    const response = await axios.post<UserRO>(`${UsersApi.apiUrl}`, payload);
    const { data } = response;

    return data;
  }

  static async resetPassword(payload: ResetPasswordDto) {
    const response = await axios.post<MessageRO>(`${UsersApi.apiUrl}/reset-password`, payload);
    const { data } = response;

    return data;
  }

  static async resetPasswordChange(payload: ResetPasswordChangeDto) {
    const response = await axios.post<MessageRO>(`${UsersApi.apiUrl}/reset-password/change`, payload);
    const { data } = response;

    return data;
  }

  static async resetPasswordCheck(payload: ResetPasswordChangeDto) {
    const response = await axios.post<boolean>(`${UsersApi.apiUrl}/reset-password/check`, payload);
    const { data } = response;

    return data;
  }

  static async deleteAccount(payload: DeleteAccountDto) {
    const response = await axios.post<MessageRO>(`${UsersApi.apiUrl}/delete-account`, payload, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async deleteAccountConfirm(payload: DeleteAccountConfirmDto) {
    const response = await axios.get<boolean>(`${UsersApi.apiUrl}/delete-account/confirm`, {
      params: payload,
    });
    const { data } = response;

    return data;
  }

  static async deleteAccountFeedback(payload: DeleteAccountFeedbackDto) {
    await axios.post<MessageRO>(`${UsersApi.apiUrl}/delete-account/feedback`, payload);
  }

  static async getUser(headers?: OutgoingHttpHeaders, includes?: UserIncludes[]) {
    const response = await axios.get<UserRO>(`${UsersApi.apiUrl}`, {
      params: {
        includes,
      },
      withCredentials: true,
      headers,
    });
    const { data } = response;

    return data;
  }

  static async passwordChange(payload: PasswordChangeDto) {
    localStorage.setItem('passwordChangeRequest', 'true');

    const { data } = await axios.post<WithToken<{}>>(`${process.env.REACT_APP_API_URL}/user/change-password`, payload, {
      withCredentials: true,
    });

    setCookie({}, 'jwt', data.token.accessToken, {
      expires: dayjs().add(data.token.expiresIn, 'second').toDate(),
      secure: true,
      httpOnly: false,
      path: '/',
    });

    setAuthHeader(data.token.accessToken);
  }

  static async profileUpdate(payload: ProfileUpdateDto) {
    const response = await axios.put<WithMessage<UserRO>>(`${UsersApi.apiUrl}/profile-update`, payload, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async preferencesUpdate(payload: PreferencesUpdateDto) {
    const response = await axios.put<UserRO>(`${UsersApi.apiUrl}/preferences-update`, payload, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  /**
   * Api call that can be done from frontend server and from client browser
   * It gets different includes depending on context
   * @param req
   * @param includes {string[]} - list of data to pull additionaly to user data (refer to Typescript type)
   * @param token
   * @returns {Promise<T>}
   */
  static async checkUser(req?: IncomingMessage, includes?: UserIncludes[], token?: string) {
    const headers: OutgoingHttpHeaders = {};

    if (req && req.headers) {
      headers.cookie = req.headers.cookie;

      if (token) {
        headers.Authorization = `Bearer ${token}`;
      }

      // eslint-disable-next-line no-param-reassign
      includes = [
        'favourites',
        'userDetails',
        'userMetadata',
        'subscriptions',
        'teamMember',
        'text',
        'timer',
        'highRiskTransactions',
      ];
    }

    return UsersApi.getUser(headers, includes);
  }
}
