import { CreateTeamMemberDto, HighRiskTransaction, HighRiskTransactionStatus } from '@noisli/common';
// eslint-disable-next-line import/no-extraneous-dependencies
import { AnyAction } from 'redux';
// eslint-disable-next-line import/no-extraneous-dependencies
import { ThunkAction } from 'redux-thunk';
import { RootState } from '../../index';
import {
  addTeamMember,
  removeTeamMember,
  setHighRiskTransaction,
  setHighRiskTransactionModalIsOpen,
  setIsLoadingPlans,
  setPaymentHistory,
  setSubscription,
  setSubscriptionPlans,
  setTeamMembers,
} from '.';
import SubscriptionsApi from '../../../Api/Subscriptions.api';
import TeamApi from '../../../Api/Team.api';
import UsersApi from "../../../Api/Users.api";
import {setUserDataWithIncludesThunk} from "../users/users.thunks";

export const getSubscriptionPlansThunk = (
): ThunkAction<Promise<void>, RootState, null, AnyAction> => async (dispatch) => {
  try {
    dispatch(setIsLoadingPlans({
      isLoading: true,
    }));

    const data = await SubscriptionsApi.getPlans();

    dispatch(setSubscriptionPlans({
      plans: data.data,
    }));
  } finally {
    dispatch(setIsLoadingPlans({
      isLoading: false,
    }));
  }
};

export const getPaymentHistoryThunk = (
): ThunkAction<Promise<void>, RootState, null, AnyAction> => async (dispatch, getState) => {
  const { subscriptions } = getState();

  if (subscriptions.paymentHistory.length) {
    return;
  }

  const data = await SubscriptionsApi.getPaymentHistory();
  dispatch(setPaymentHistory({
    paymentHistory: data.data,
  }));
};

export const getUserSubscriptionFromPaddle = (
): ThunkAction<Promise<void>, RootState, null, AnyAction> => async (dispatch) => {
  try {
    dispatch(setIsLoadingPlans({
      isLoading: true,
    }));
    const data = await SubscriptionsApi.getUserSubscription();
    dispatch(setSubscription({
      subscription: data,
    }));
    const userData = await UsersApi.getUser();
    dispatch(setUserDataWithIncludesThunk(userData));
  } finally {
    dispatch(setIsLoadingPlans({
      isLoading: false,
    }));
  }
};


export const getTeamMembersThunk = (
): ThunkAction<Promise<void>, RootState, null, AnyAction> => async (dispatch) => {
  const data = await SubscriptionsApi.getTeamList();

  dispatch(setTeamMembers({
    teamMembers: data.data,
  }));
};

export const createTeamMemberThunk = (
  body: CreateTeamMemberDto,
): ThunkAction<Promise<void>, RootState, null, AnyAction> => async (dispatch) => {
  const teamMember = await TeamApi.createTeamMember(body);

  dispatch(addTeamMember({
    teamMember,
  }));
};

export const deleteTeamMemberThunk = (
  uuid: string,
): ThunkAction<Promise<void>, RootState, null, AnyAction> => async (dispatch) => {
  await TeamApi.deleteTeamMember(uuid);

  dispatch(removeTeamMember({
    uuid,
  }));
};

export const setHighRiskTransactionThunk = (
  highRiskTransaction: HighRiskTransaction,
): ThunkAction<void, RootState, null, AnyAction> => (dispatch) => {
  if (highRiskTransaction) {
    dispatch(setHighRiskTransaction({
      highRiskTransaction,
    }));

    if (highRiskTransaction.status === HighRiskTransactionStatus.PENDING) {
      dispatch(setHighRiskTransactionModalIsOpen({
        highRiskTransactionModalIsOpen: true,
      }));
    }
  }
};

export const getHighRiskTransactionThunk = (
): ThunkAction<Promise<void>, RootState, null, AnyAction> => async (dispatch, getState) => {
  const { subscriptions } = getState();

  if (subscriptions.highRiskTransaction) {
    return;
  }

  const data = await SubscriptionsApi.getHighRiskTransaction();

  dispatch(setHighRiskTransactionThunk(data.data));
};
