/* eslint-disable no-param-reassign */
import { PayloadAction } from 'redux-starter-kit';
import {
  PlaylistsState,
  ActivePlaylistComboPayload,
  Playlist,
  SetPlaylistsSliderPositionPayload,
  SetPlaylistBackgroundPayload,
} from './playlists.interfaces';
import { SetComboShufflePayload } from '../common/shuffle.interfaces';
import { ComboIdPayload } from '../common/combos.interfaces';

export const unmarkActivePlaylistComboReducer = (
  state: PlaylistsState,
) => {
  const idx = state.playlists.findIndex((playlist) => playlist.isPlaying);

  if (idx > -1) {
    state.playlists[idx].isPlaying = false;
    const comboIdx = state.playlists[idx].items.findIndex((combo) => combo.isPlaying);

    if (comboIdx > -1) {
      state.playlists[idx].items[comboIdx].isPlaying = false;
    }
  }
};

export const markActivePlaylistComboReducer = (
  state: PlaylistsState, action: PayloadAction<ActivePlaylistComboPayload>,
) => {
  const { playlistId, comboId } = action.payload;
  const idx = state.playlists.findIndex((playlist) => playlist.id === playlistId);
  state.playlists[idx].isPlaying = true;

  if (idx > -1) {
    const comboIdx = state.playlists[idx].items.findIndex((combo) => combo.id === comboId);

    if (comboIdx > -1) {
      state.playlists[idx].items[comboIdx].isPlaying = true;
    }
  }
};

export const setPlaylistComboIsShufflingReducer = (
  state: PlaylistsState, action: PayloadAction<SetComboShufflePayload>,
) => {
  const activePlaylistIdx = state.playlists.findIndex((playlist) => playlist.isPlaying);
  const { isShuffling, comboId } = action.payload;

  if (activePlaylistIdx > -1) {
    state.playlists[activePlaylistIdx].items.forEach((combo) => {
      if (combo.id === comboId) {
        combo.isShuffling = isShuffling;
      }
    });

    state.playlists[activePlaylistIdx].isShuffling = state.playlists[activePlaylistIdx].items.some(
      (combo) => combo.isShuffling,
    );
  }
};

export const markActiveComboReducer = (
  state: PlaylistsState, action: PayloadAction<ComboIdPayload>,
) => {
  const activePlaylistIdx = state.playlists.findIndex((playlist) => playlist.isPlaying);

  if (activePlaylistIdx > -1) {
    state.playlists[activePlaylistIdx].items.forEach((combo) => {
      if (combo.id === action.payload.id) {
        combo.isPlaying = true;
      }
    });
  }
};

export const unmarkActiveComboReducer = (
  state: PlaylistsState, action: PayloadAction<ComboIdPayload>,
) => {
  const activePlaylistIdx = state.playlists.findIndex((playlist) => playlist.isPlaying);

  if (activePlaylistIdx > -1) {
    state.playlists[activePlaylistIdx].items.forEach((combo) => {
      if (combo.id === action.payload.id) {
        combo.isPlaying = false;
      }
    });
  }
};

export const setPlaylistsReducer = (
  state: PlaylistsState, action: PayloadAction<Playlist[]>,
) => {
  state.playlists = action.payload;
};

export const setPlaylistsSliderPositionReducer = (
  state: PlaylistsState, action: PayloadAction<SetPlaylistsSliderPositionPayload>,
) => {
  state.playlistSliderPosition = action.payload.position;
};

export const setPlaylistBackgroundReducer = (
  state: PlaylistsState, action: PayloadAction<SetPlaylistBackgroundPayload>,
) => {
  state.playlistBackground = action.payload.playlistBackground;
};
