import throttle from 'lodash/throttle';
import random from 'lodash/random';
// eslint-disable-next-line import/no-extraneous-dependencies
import { Dispatch, AnyAction } from 'redux';
import { OscillationConfig, Player } from 'app/redux/modules/players/players.interfaces';
import { setPlayerVolume, updateOscillator, setPlayerOscillating } from 'app/redux/modules/players';

// eslint-disable-next-line import/no-webpack-loader-syntax,import/no-unresolved
import TickWorker from 'worker-loader?name=static/[hash].worker.js!../../../WebWorkers/tick.worker';

import { TickWorkerInterface } from 'app/WebWorkers/tick.worker';

export type OscillatorCreateArgs = ({
  dispatch: Dispatch<AnyAction>;
  playerInstance: Player;
  oscillationConfig: OscillationConfig;
});

export type OscillatorUpdateArgs = Pick<OscillatorCreateArgs, 'playerInstance'>;

/**
 * Class that is performing oscillation of sound
 * It is created and managed by {OscillatorManager}
 */
export default class Oscillator {
  config = {
    interval: 200,
  };

  targetVolume = 1;

  oscillationDelay = 0;

  oscillationDuration = 0;

  playerInstance: Player;

  oscillationConfig: OscillationConfig;

  dispatch: Dispatch<AnyAction>;

  tickWorker: TickWorkerInterface;

  constructor({
    dispatch,
    playerInstance,
    oscillationConfig,
  }: OscillatorCreateArgs) {
    this.dispatch = dispatch;
    this.playerInstance = playerInstance;
    this.oscillationConfig = oscillationConfig;
    this.tickWorker = new TickWorker();

    this.init();
  }

  init() {
    this.setTargetVolume();
    this.setOscillationDelayAndDuration();
    this.runOscillatorCycle();
  }

  /**
   * Method that handles update of player instance and is reinitializing oscillation
   */
  update = throttle(({ playerInstance }: OscillatorUpdateArgs) => {
    this.playerInstance = playerInstance;
    this.tickWorker.postMessage({
      command: 'stop',
    });
    this.init();
  }, 100, { trailing: true });

  returnToTargetVolumeAndDestroy(playerInstance: Player, targetVolume: number): Promise<void> {
    this.tickWorker.postMessage({
      command: 'stop',
    });
    this.playerInstance = playerInstance;
    this.targetVolume = targetVolume;
    this.oscillationDelay = 0;
    this.oscillationDuration = this.oscillationConfig.comboVolumeReturnTime;
    return this.runOscillatorCycle(true);
  }

  destroy() {
    this.tickWorker.postMessage({
      command: 'stop',
    });
    this.tickWorker.terminate();
    this.dispatch(setPlayerOscillating({
      playerId: this.playerInstance.id,
      isOscillating: false,
    }));
  }

  /**
   * Method that is randomizing and checking the edge conditions
   * to determine the ongoing volume progression
   */
  setTargetVolume() {
    const rand = random(true);
    const customRules = this.oscillationConfig.customPlayerSettings.find(
      (rules) => rules.playerId === this.playerInstance.id,
    );
    const { minVolumeValue, maxVolumeValue } = customRules || this.oscillationConfig;
    const haveMinVolume = minVolumeValue >= this.playerInstance.volume;
    const haveMaxVolume = maxVolumeValue <= this.playerInstance.volume;

    let targetVolume = this.playerInstance.volume;

    if ((rand > 0.5 || haveMinVolume) && !haveMaxVolume) {
      targetVolume += this.oscillationConfig.changeValue;

      if (targetVolume > maxVolumeValue) {
        targetVolume = maxVolumeValue;
      }
    } else {
      targetVolume -= this.oscillationConfig.changeValue;

      if (targetVolume < minVolumeValue) {
        targetVolume = minVolumeValue;
      }
    }

    this.targetVolume = targetVolume;
  }

  /**
   * Method that is setting random oscillation delay
   * and set oscillation duration
   */
  setOscillationDelayAndDuration() {
    const { minTime, maxTime, changeTime } = this.oscillationConfig;
    this.oscillationDelay = random(minTime, maxTime);
    this.oscillationDuration = changeTime;
  }

  /**
   * Method responsible for running the oscillatior cycle
   * It return promise that is resolve after all cycle is finished
   * @param returnToDefault
   * @returns {Promise<void>}
   */
  runOscillatorCycle(returnToDefault = false): Promise<void> {
    return new Promise((resolve) => {
      const delta = this.targetVolume - this.playerInstance.volume;
      const ticks = Math.ceil(this.oscillationDuration / this.config.interval);
      const volumeStep = delta / ticks;
      let { volume } = this.playerInstance;
      let tick = 1;

      // If there is no change simply resolve
      if (this.targetVolume === this.playerInstance.volume) {
        resolve();
        return;
      }

      const step = () => {
        if (tick === 1) {
          // At first step, mark player as oscillating
          this.dispatch(setPlayerOscillating({
            playerId: this.playerInstance.id,
            isOscillating: true,
          }));
        }
        tick += 1;
        volume += volumeStep;

        this.dispatch(setPlayerVolume({
          id: this.playerInstance.id,
          volume,
        }));

        if (tick === ticks) {
          // at last step, clear all settings and run next oscillator cycle
          this.tickWorker.postMessage({
            command: 'stop',
          });
          this.dispatch(setPlayerVolume({
            id: this.playerInstance.id,
            volume: this.targetVolume,
          }));
          this.dispatch(setPlayerOscillating({
            playerId: this.playerInstance.id,
            isOscillating: false,
          }));
          if (!returnToDefault) {
            this.dispatch(updateOscillator({
              playerId: this.playerInstance.id,
            }));
          }
          resolve();
        }
      };

      this.tickWorker.onmessage = () => {
        if (tick === 1) {
          // run oscillator cycle ticker
          this.tickWorker.postMessage({
            command: 'start_interval',
            payload: {
              interval: this.config.interval,
            },
          });
        }

        step();
      };

      // Run oscillatior timeout
      this.tickWorker.postMessage({
        command: 'start_timeout',
        payload: {
          timeout: this.oscillationDelay,
        },
      });
    });
  }
}
