import { UpdateTimerConfigResponseEventData } from '@noisli/common';
import { reloadTimerConfigThunk } from './timer.thunks';
import { SocketHandlerWithData } from '../../middleware/SocketMiddleware/Socket.interface';

// eslint-disable-next-line import/prefer-default-export
export const onUpdateTimerConfig: SocketHandlerWithData<UpdateTimerConfigResponseEventData> = (
  { dispatch },
  data,
) => {
  dispatch(reloadTimerConfigThunk(data.timerConfig));
};
