/* eslint-disable no-param-reassign */
import { PayloadAction } from 'redux-starter-kit';
import { User, UserDetails } from '@noisli/common';
import {
  SetLimitReachedPayload, SetShowLimitReachedModalPayload, UsersState, SetPasswordLockPayload,
} from './users.interfaces';
import { IsLoadingPayload } from '../common/isLoading.interface';

export const setIsLoadingReducer = (
  state: UsersState, action: PayloadAction<IsLoadingPayload>,
) => {
  state.isLoading = action.payload.isLoading;
};

export const setUserReducer = (
  state: UsersState, action: PayloadAction<User>,
) => {
  state.user = action.payload;
};

export const setUserDetailsReducer = (
  state: UsersState, action: PayloadAction<UserDetails>,
) => {
  if (action.payload.masterVolume === 0) {
    action.payload.masterVolumeMuted = true;
  }
  state.userDetails = action.payload;
};

export const resetUserReducer = (
  state: UsersState,
) => {
  state.user = {} as unknown as User;
  state.userDetails = {} as unknown as UserDetails;
};

export const completeOnboardingReducer = (
  state: UsersState,
) => {
  state.userDetails.didOnboarding = true;
};

export const checkLimitReachedReducer = (
) => {};

export const setLimitReachedReducer = (
  state: UsersState, action: PayloadAction<SetLimitReachedPayload>,
) => {
  state.limitReached = action.payload.limitReached;
};

export const setShowLimitReachedModalReducer = (
  state: UsersState, action: PayloadAction<SetShowLimitReachedModalPayload>,
) => {
  state.showLimitReachedModal = action.payload.showModal;
};

export const setPasswordLockReducer = (
  state: UsersState, action: PayloadAction<SetPasswordLockPayload>,
) => {
  state.passwordLock = action.payload.passwordLock;
};
