// eslint-disable-next-line import/no-extraneous-dependencies
import {
  MiddlewareAPI, Dispatch, AnyAction, Action,
} from 'redux';
import { RootState } from 'app/redux';
import {
  setPlayerPlaying, setPlayerVolume, stopAllPlayers, setActiveCombo,
} from 'app/redux/modules/players';
import { PayloadAction } from 'redux-starter-kit';
import { SetPlayerVolumePayload } from 'app/redux/modules/players/players.interfaces';
import { getPlayerInstance } from 'app/redux/modules/players/players.selectors';
import { unmarkActiveFavourite } from 'app/redux/modules/favourites';
import { getActiveFavourite } from 'app/redux/modules/favourites/favourites.selectors';

/**
 * Middleware that is responsible for handling favourites changes
 * It tracks changes to volume, players and stoping players to unmark active favourite
 * It also checks if volume change is caused by oscillation/shuffle
 * @param dispatch
 * @param getState
 * @returns {(next: Dispatch) => (action: Action) => void}
 * @constructor
 */
const FavouritesMiddleware = (
  { dispatch, getState }: MiddlewareAPI<Dispatch<AnyAction>, RootState>,
) => (next: Dispatch) => (action: Action) => {
  const state = getState();
  const activeFavourite = getActiveFavourite(state);

  switch (action.type) {
    case (setPlayerVolume.toString()): {
      const { payload } = (action as PayloadAction<SetPlayerVolumePayload, string>);
      const playerInstance = getPlayerInstance(state, { id: payload.id });

      next(action);

      if (!playerInstance || !activeFavourite) {
        break;
      }

      if (activeFavourite.isShuffling) {
        break;
      }

      const isPlayerInFavourite = activeFavourite.items.some(
        (item) => item.id === playerInstance.id,
      );

      if (!playerInstance.isOscillating && isPlayerInFavourite) {
        dispatch(unmarkActiveFavourite());
      }

      if (playerInstance.isOscillating && isPlayerInFavourite) {
        const { activeCombo } = getState().players;
        if (!activeCombo) {
          return;
        }
        dispatch(setActiveCombo({
          combo: {
            ...activeCombo,
            items: activeCombo.items.map((item) => {
              let { volume } = item;
              if (item.id === playerInstance.id) {
                // eslint-disable-next-line prefer-destructuring
                volume = payload.volume;
              }

              return {
                id: item.id,
                volume,
              };
            }),
          },
        }));
      }

      break;
    }

    case (setPlayerPlaying.toString()): {
      dispatch(unmarkActiveFavourite());
      next(action);
      break;
    }

    case (stopAllPlayers.toString()): {
      next(action);
      dispatch(unmarkActiveFavourite());
      break;
    }

    default:
      next(action);
      break;
  }
};

export default FavouritesMiddleware;
