import FavouritesApi from 'app/Api/Favourites.api';
import { Epic, ofType } from 'redux-observable';
import { Action, PayloadAction } from 'redux-starter-kit';
import { RootState } from 'app/redux';
import { from, scheduled } from 'rxjs';
import {
  map, mergeMap, catchError, flatMap,
} from 'rxjs/operators';
import { async } from 'rxjs/internal/scheduler/async';
import catchAuthError from 'app/redux/utils/catchAuthError';
import {
  setFavourites, fetchFavourites, updateFavourite, createFavourite, markFavourite, deleteFavourite,
} from '.';
import { CreateFavouritePayload, DeleteFavouritePayload, UpdateFavouritePayload } from './favourites.interfaces';
import { unmarkActivePlaylistCombo } from '../playlists';

/**
 * Epic that fetch favourites when {fetchFavourites} action goes in store
 * @param action$
 */
// eslint-disable-next-line @typescript-eslint/no-explicit-any
const fetchFavouritesEpic: Epic<Action<string>, any, RootState> = (action$) => action$.pipe(
  ofType(fetchFavourites.toString()),
  mergeMap(() => from(FavouritesApi.getAll()).pipe(
    map((data) => setFavourites(data)),
    catchError(catchAuthError),
  )),
);

/**
 * Epic that send request to api when new favourite is created
 * @param action$
 */
const createFavouriteEpic: Epic<
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  PayloadAction<CreateFavouritePayload, string>, any, RootState
> = (action$) => action$.pipe(
  ofType(createFavourite.toString()),
  mergeMap((action) => from(FavouritesApi.create(action.payload.payload)).pipe(
    mergeMap((savedFavourite) => from(FavouritesApi.getAll()).pipe(
      flatMap((data) => scheduled([
        setFavourites(data),
        markFavourite({ id: savedFavourite.uuid }),
        unmarkActivePlaylistCombo(),
      ], async)),
    )),
    catchError(catchAuthError),
  )),
);

/**
 * Epic that send request to api when favourite is updated
 * @param action$
 */
const updateFavouriteEpic: Epic<
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  PayloadAction<UpdateFavouritePayload, string>, any, RootState
> = (action$) => action$.pipe(
  ofType(updateFavourite.toString()),
  mergeMap((action) => from(FavouritesApi.update(action.payload.uuid, action.payload.payload)).pipe(
    map(() => fetchFavourites()),
    catchError(catchAuthError),
  )),
);

/**
 * Epic that send request to api when favourite is deleted
 * @param action$
 */
const deleteFavouriteEpic: Epic<
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  PayloadAction<DeleteFavouritePayload, string>, any, RootState
> = (action$) => action$.pipe(
  ofType(deleteFavourite.toString()),
  mergeMap((action) => from(FavouritesApi.delete(action.payload.uuid)).pipe(
    map(() => fetchFavourites()),
    catchError(catchAuthError),
  )),
);

export default [
  fetchFavouritesEpic,
  createFavouriteEpic,
  updateFavouriteEpic,
  deleteFavouriteEpic,
];
