import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { throttle } from 'lodash';

import RangeSlider from 'app/components/RangeSlider/RangeSlider';

import { RootState } from 'app/redux';
import { setMasterPlayerIsMutedThunk, setMasterPlayerVolumeThunk } from 'app/redux/modules/players/players.thunks';

import {
  MutedIcon, MutedSmallIcon, SoundIcon, SoundSmallIcon,
} from '@noisli/icons';
import { Container, VolumeIconContainer, VolumeSliderContainer } from './PlayerMasterVolumeController.styled';
import { Breakpoint, DisplayOnBreakpoint } from '../../../../styles/rwd';
import useEnterLeaveEventListeners from "../../../../utils/hooks/useEnterLeaveEventListeners";

interface Props {
  setIsVolumeControllerFocused: (focus: boolean) => void;
  isChromeExtension?: boolean;
}

/**
 * Component that handles master volume and muting
 * @param setIsVolumeControllerFocused
 * @param isChromeExtension
 * @returns {any}
 * @constructor
 */
const PlayerMasterVolumeController: React.FC<Props> = ({
  setIsVolumeControllerFocused, isChromeExtension,
}) => {
  const dispatch = useDispatch();
  const masterController = useSelector((state: RootState) => state.players.masterController);
  const wrapper = React.useRef<HTMLDivElement>(null);

  const [hasFocus, setHasFocus] = React.useState<boolean>(false);

  const handleVolumeChange = throttle((e: React.ChangeEvent<{}>, value: number | number[]) => {
    if (Array.isArray(value)) {
      return;
    }
    dispatch(setMasterPlayerVolumeThunk({
      volume: value,
    }));
  }, 10, { trailing: true });

  const handleMutedClick = React.useMemo(() => throttle((isMuted: boolean) => {
    dispatch(setMasterPlayerIsMutedThunk({
      isMuted,
    }));
  }, 800), []);

  const handleOnMouseEnter = () => {
    setIsVolumeControllerFocused(true);
    setHasFocus(true);
  };

  const handleOnMouseLeave = () => {
    setIsVolumeControllerFocused(false);
    setHasFocus(false);
  };

  useEnterLeaveEventListeners<HTMLSpanElement>(
    wrapper,
    handleOnMouseEnter,
    handleOnMouseLeave,
    handleOnMouseLeave,
  );

  return (
    <Container
      ref={wrapper}
      isChromeExtension={isChromeExtension}
      hasFocus={hasFocus}
    >
      <VolumeSliderContainer>
        <RangeSlider
          min={0}
          max={1}
          step={0.01}
          value={masterController.volume}
          onChange={handleVolumeChange}
        />
      </VolumeSliderContainer>
      <VolumeIconContainer
        onClick={() => handleMutedClick(!masterController.isMuted)}
        fullOpacity={masterController.isMuted}
        isChromeExtension={isChromeExtension}
      >
        { masterController.isMuted ? (
          <>
            { isChromeExtension ? (
              <MutedSmallIcon />
            ) : (
              <>
                <DisplayOnBreakpoint up breakpoint={Breakpoint.MEDIUM}>
                  <MutedSmallIcon />
                </DisplayOnBreakpoint>
                <DisplayOnBreakpoint down breakpoint={Breakpoint.MEDIUM}>
                  <MutedIcon />
                </DisplayOnBreakpoint>
              </>
            )}
          </>
        ) : (
          <>
            { isChromeExtension ? (
              <SoundSmallIcon />
            ) : (
              <>
                <DisplayOnBreakpoint up breakpoint={Breakpoint.MEDIUM}>
                  <SoundSmallIcon />
                </DisplayOnBreakpoint>
                <DisplayOnBreakpoint down breakpoint={Breakpoint.MEDIUM}>
                  <SoundIcon />
                </DisplayOnBreakpoint>
              </>
            )}
          </>
        )}
      </VolumeIconContainer>
    </Container>
  );
};

export default PlayerMasterVolumeController;
