// eslint-disable-next-line import/no-extraneous-dependencies
import { Dispatch, AnyAction } from 'redux';
import { Combo } from 'app/redux/modules/common/combos.interfaces';
import { ShuffleConfig } from 'app/redux/modules/common/shuffle.interfaces';
import {
  setShufflingTranstionState,
  setActiveCombo,
  renewShuffle,
  setPlayersShuffling,
  startupPlayersForShuffle,
  setPlayersVolume,
  revertPlayersVolume,
} from 'app/redux/modules/players';

// eslint-disable-next-line import/no-webpack-loader-syntax,import/no-unresolved
import TickWorker from 'worker-loader?name=static/[hash].worker.js!../../../WebWorkers/tick.worker';

import { TickWorkerInterface } from 'app/WebWorkers/tick.worker';
import { PlayerVolumePayload } from 'app/redux/modules/players/players.interfaces';
import { stopPlayersAfterShuffleThunk } from 'app/redux/modules/players/players.thunks';
import { ShuffleReducers } from './ShuffleManager';


interface ShufflerConstructorArgs {
  fromCombo: Combo;
  toCombo: Combo;
  reducers: ShuffleReducers;
  shuffleConfig: ShuffleConfig;
  dispatch: Dispatch<AnyAction>;
}

interface ComboVolume {
  id: string;
  step: number;
  startVolume: number;
  actualVolume: number;
  targetVolume: number;
  isInNext?: boolean;
  isInPrev?: boolean;
}

/**
 * Class that is performing shuffle of sounds
 * It is created and managed by {ShuffleManager}
 */
export default class Shuffler {
  config = {
    interval: 300,
  };

  // Combo from which we will be transtioning
  fromCombo: Combo;
  // Combo to which we will be transtioning
  toCombo: Combo;
  // Combo volumes object from which we will be transtioning
  fromComboVolume: ComboVolume[] = [];
  // Combo volumes object to which we will be transtioning
  toComboVolume: ComboVolume[] = [];
  // Passed reducers for players or favourites
  reducers: ShuffleReducers;

  ticks = 1;

  transitionDuration = 0;

  transitionDelay = 0;

  shuffleConfig: ShuffleConfig;

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  dispatch: Dispatch<any>;

  tickWorker: TickWorkerInterface;

  isOffline = false;

  onlineHandler: () => void = () => {};

  constructor({
    fromCombo,
    toCombo,
    reducers,
    shuffleConfig,
    dispatch,
  }: ShufflerConstructorArgs) {
    this.fromCombo = fromCombo;
    this.toCombo = toCombo;
    this.reducers = reducers;
    this.shuffleConfig = shuffleConfig;
    this.dispatch = dispatch;
    this.tickWorker = new TickWorker();
    this.init();
    this.setOfflineStateListener();
  }

  init() {
    this.transitionDuration = this.shuffleConfig.changeTime;
    // change from minutes to miliseconds
    this.transitionDelay = this.shuffleConfig.time * 60 * 1000;

    this.setTicks();
    this.runShuffleCycle();
  }

  /**
   * Method that is updating fromCombo
   * @param activeCombo
   */
  update(activeCombo: Combo) {
    this.fromCombo = activeCombo;
  }

  /**
   * Method that destroy the shuffler
   * @param revertToOriginal
   */
  destroy(revertToOriginal = false) {
    this.tickWorker.postMessage({
      command: 'stop',
    });
    this.tickWorker.terminate();
    this.setShuffling(false);
    this.removeOfflineStateListener();
    if (revertToOriginal) {
      setTimeout(() => {
        this.revertVolume();
      }, 600);
    }
  }


  /**
   * Method for graceful shuffle shutdown
   * @returns {Promise<unknown>}
   */
  endTransitionAndDestroy() {
    this.tickWorker.postMessage({
      command: 'stop',
    });

    this.transitionDuration = this.shuffleConfig.returnTime;
    this.transitionDelay = 0;

    this.setTicks();
    this.recomputeVolumeSteps();
    this.removeOfflineStateListener();
    return this.runShuffleCycle(true);
  }

  setTicks() {
    this.ticks = Math.floor(this.transitionDuration / this.config.interval);
  }

  /**
   * Compute volume steps for each combo in fromCombo and toCombo items
   * If fromCombo contains toCombo players, we need to calc difference between from and to
   * Otherwise set fromCombo to target 0 and to Combo to their volume
   */
  computeVolumeSteps() {
    this.fromComboVolume = this.fromCombo.items.map((comboItem): ComboVolume => {
      const delta = -comboItem.volume;

      return {
        id: comboItem.id,
        startVolume: comboItem.volume,
        actualVolume: comboItem.volume,
        targetVolume: 0,
        step: delta / this.ticks,
        isInNext: this.toCombo.items.some((item) => item.id === comboItem.id),
      };
    });

    this.toComboVolume = this.toCombo.items.map((comboItem): ComboVolume => {
      const inComboItem = this.fromCombo.items.find((item) => item.id === comboItem.id);

      let delta = comboItem.volume;
      let actualVolume = 0;

      if (inComboItem) {
        delta = comboItem.volume - inComboItem.volume;
        actualVolume = inComboItem.volume;
      }

      return {
        id: comboItem.id,
        startVolume: actualVolume,
        actualVolume,
        targetVolume: comboItem.volume,
        step: delta / this.ticks,
        isInPrev: !!inComboItem,
      };
    });
  }

  // If change happen, recompute volume steps to match new values
  recomputeVolumeSteps() {
    this.fromComboVolume = this.fromComboVolume.map((comboItem): ComboVolume => {
      const delta = -comboItem.actualVolume;

      return {
        ...comboItem,
        step: delta / this.ticks,
      };
    });

    this.toComboVolume = this.toComboVolume.map((comboItem): ComboVolume => {
      const inComboItem = this.fromComboVolume.find((item) => item.id === comboItem.id);

      let delta = comboItem.targetVolume - comboItem.actualVolume;

      if (inComboItem) {
        delta = comboItem.targetVolume - inComboItem.actualVolume;
      }

      return {
        ...comboItem,
        step: delta / this.ticks,
      };
    });
  }

  /**
   * Method that runs shuffle cycle
   * @param returnToDefault
   * @returns {Promise<unknown>}
   */
  runShuffleCycle(returnToDefault = false) {
    return new Promise((resolve) => {
      let tick = 1;

      const step = async () => {
        if (tick === 1) {
          // At first tick, notify that we are shuffling
          this.setShuffling(true);

          // compute steps to shuffling and startup needed players
          if (!returnToDefault) {
            this.computeVolumeSteps();
            this.startupPlayers();
          }
        }

        tick += 1;

        await Promise.all([
          this.setPlayersVolume(this.fromComboVolume),
          this.setPlayersVolume(this.toComboVolume),
        ]);

        if (tick === this.ticks) {
          // At last tick, change volume to target volume of each group
          await Promise.all([
            this.setPlayersVolume(this.fromComboVolume, true),
            this.setPlayersVolume(this.toComboVolume, true),
          ]);
          // async players stop (only players not needed)
          setTimeout(() => this.stopPlayers(), 10);
          // notify that we stop shuffling
          this.setShuffling(false);

          // Unmark unactive combo
          this.dispatch(this.reducers.unmarkActive({
            id: this.fromCombo.id,
          }));

          // Mark active combo
          this.dispatch(this.reducers.markActive({
            id: this.toCombo.id,
          }));

          // Save combo to active combo
          this.dispatch(setActiveCombo({
            combo: this.toCombo,
          }));

          // If we were not gracefully shutting shuffler, reschedule it
          if (!returnToDefault) {
            this.dispatch(renewShuffle());
          }
          resolve();
        }
      };

      this.tickWorker.onmessage = () => {
        if (tick === 1) {
          this.tickWorker.postMessage({
            command: 'start_interval',
            payload: {
              interval: this.config.interval,
            },
          });
        }

        step();

        if (tick === this.ticks) {
          this.tickWorker.terminate();
        }
      };

      this.tickWorker.postMessage({
        command: 'start_timeout',
        payload: {
          timeout: this.transitionDelay,
        },
      });
    });
  }

  /**
   * Method that craetes object with volume change to pass to store actions
   * @param comboVolume
   * @param toTargetVolume
   * @returns {Promise<void>}
   */
  setPlayersVolume(comboVolume: ComboVolume[], toTargetVolume = false): Promise<void> {
    return new Promise((resolve) => {
      comboVolume.forEach((item) => {
        if (item.isInNext) {
          return;
        }
        // eslint-disable-next-line no-param-reassign
        item.actualVolume += item.step;
      });

      const playersVolume: PlayerVolumePayload[] = comboVolume
        .filter((item) => !item.isInNext)
        .map((item) => ({
          id: item.id,
          volume: toTargetVolume ? item.targetVolume : item.actualVolume,
        }));

      this.dispatch(setPlayersVolume({
        items: playersVolume,
      }));

      resolve();
    });
  }

  /**
   * Method that notify all about shuffling changes
   * @param isShuffling
   */
  setShuffling(isShuffling: boolean) {
    this.dispatch(setShufflingTranstionState({
      isShuffling,
    }));

    this.dispatch(this.reducers.setComboIsShuffling({
      comboId: this.fromCombo.id,
      isShuffling,
    }));

    this.dispatch(this.reducers.setComboIsShuffling({
      comboId: this.toCombo.id,
      isShuffling,
    }));

    const playersIds = [
      ...this.fromCombo.items.map((item) => item.id),
      ...this.toCombo.items.map((item) => item.id),
    ];

    this.dispatch(setPlayersShuffling({
      playersIds,
      isShuffling,
    }));
  }

  /**
   * Method that startup unactive players
   */
  startupPlayers() {
    const startupPlayersArray = this.toComboVolume
      .filter((item) => !item.isInPrev)
      .map((item) => item.id);

    this.dispatch(startupPlayersForShuffle(startupPlayersArray));
  }

  /**
   * Method that stops unused players after shuffle is over
   */
  stopPlayers() {
    const stopPlayersArray: PlayerVolumePayload[] = this.fromComboVolume
      .filter((item) => !item.isInNext)
      .map((item) => ({
        id: item.id,
        volume: item.startVolume,
      }));

    this.dispatch(stopPlayersAfterShuffleThunk(stopPlayersArray));
  }

  /**
   * Method that retrives original volume and revert to it
   */
  revertVolume() {
    const playersVolume: PlayerVolumePayload[] = [
      ...this.fromCombo.items.map((item) => ({
        id: item.id,
        volume: item.volume,
      })),
      ...this.toCombo.items.map((item) => ({
        id: item.id,
        volume: item.volume,
      })),
    ];

    this.dispatch(revertPlayersVolume({
      items: playersVolume,
    }));
  }

  /**
   * Method that sets offline listener. If we are offline, we cant perform shuffle
   * When we go online, we can renew
   */
  setOfflineStateListener() {
    this.onlineHandler = this.handleOnlineEvent.bind(this);
    window.addEventListener('online', this.onlineHandler);
  }

  removeOfflineStateListener() {
    window.removeEventListener('online', this.onlineHandler);
  }

  handleOnlineEvent() {
    if (this.tickWorker) {
      this.tickWorker.terminate();
    }
    this.tickWorker = new TickWorker();
    this.init();
  }
}
