import axios from './AxiosInstance';
import {
  CreateTeamMemberDto,
  TeamMember,
} from '@noisli/common';

/**
 * Static class that have all connection to API regarding Team module
 */
export default class TeamApi {
  static apiUrl = `${process.env.REACT_APP_API_URL}/team`;

  static async createTeamMember(body: CreateTeamMemberDto) {
    const response = await axios.post<TeamMember>(`${TeamApi.apiUrl}/create-team-member`, body, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async deleteTeamMember(uuid: string) {
    const response = await axios.delete<TeamMember>(`${TeamApi.apiUrl}/delete-team-member/${uuid}`, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }
}
