import styled, { css } from 'styled-components';
import { HeaderVariant } from '../Header/Header';

interface PageProps {
  pages?: boolean;
  textEditor?: boolean;
  headerVariant?: HeaderVariant;
}

// eslint-disable-next-line import/prefer-default-export
export const Page = styled.div<PageProps>`
  padding-top: ${(props) => `${props.theme.header.height + props.theme.header.space}px`};
  background-color: ${(props) => props.theme.colors.primary};
  
  ${(props) => props.pages && css`
    background-color: ${props.theme.colors.white};
  `}
  
  ${(props) => props.headerVariant === HeaderVariant.Homepage && css`
    padding-top: ${`${props.theme.header.home.height}px`};
  `}
  
  ${(props) => props.textEditor && css`
    padding-bottom: 52px;
    padding-top: ${`${props.theme.header.height}px`};
  `}
`;

export const LayoutWrapper = styled.div``;
