import React, { MouseEvent } from 'react';
import { Favourite } from '@noisli/app/dist/redux/modules/favourites/favourites.interfaces';
import { ShareIcon } from '@noisli/icons';
import {
  Container, Title, ButtonWrapper, IconButton,
} from './FavoritesItem.styled';

interface Props {
  favourite: Favourite;
  onClick: () => void;
  onShareButtonClick: (e: MouseEvent<HTMLDivElement>) => void;
}

const FavoritesItem: React.FC<Props> = ({
  favourite, onClick, onShareButtonClick,
}) => (
  <Container
    active={favourite.isPlaying || favourite.isShuffling}
    onClick={onClick}
  >
    <Title>{ favourite.name }</Title>
    <ButtonWrapper>
      <IconButton onClick={onShareButtonClick}>
        <ShareIcon />
      </IconButton>
    </ButtonWrapper>
  </Container>
);

export default FavoritesItem;
