import React from 'react';

import airplane from './airplane.svg';
import bonfire from './bonfire.svg';
import brownnoise from './brownnoise.svg';
import bubbles from './bubbles.svg';
import cicadas from './cicadas.svg';
import cityscape from './cityscape.svg';
import coffeeshop from './coffeeshop.svg';
import fan from './fan.svg';
import fire from './fire.svg';
import fireplace from './fireplace.svg';
import forest from './forest.svg';
import leaves from './leaves.svg';
import oceanwaves from './oceanwaves.svg';
import office from './office.svg';
import pinknoise from './pinknoise.svg';
import rain from './rain.svg';
import rainontent from './rainontent.svg';
import seaside from './seaside.svg';
import spaceengine from './spaceengine.svg';
import summernight from './summernight.svg';
import thunderstorm from './thunderstorm.svg';
import train from './train.svg';
import tropicalforest from './tropicalforest.svg';
import underwater from './underwater.svg';
import washingmachine from './washingmachine.svg';
import water from './water.svg';
import waterfall from './waterfall.svg';
import waterstream from './waterstream.svg';
import whitenoise from './whitenoise.svg';
import wind from './wind.svg';
import windintrees from './windintrees.svg';

export const Icons = {
  airplane,
  bonfire,
  brownnoise,
  bubbles,
  cicadas,
  cityscape,
  coffeeshop,
  fan,
  fire,
  fireplace,
  forest,
  leaves,
  oceanwaves,
  office,
  pinknoise,
  rain,
  rainontent,
  seaside,
  spaceengine,
  summernight,
  thunderstorm,
  train,
  tropicalforest,
  underwater,
  washingmachine,
  water,
  waterfall,
  waterstream,
  whitenoise,
  wind,
  windintrees,
};

interface Props {
  id: string;
}

const PlayerIcon: React.FC<Props> = ({ id }) => {
  if (Object.keys(Icons).some((key) => key === id)) {
    const Icon = Icons[(id as keyof typeof Icons)];
    return <Icon />;
  }

  const Icon = Icons.seaside;
  return <Icon />;
};

export default PlayerIcon;
