import React from 'react';
import useOnClickOutside from 'use-onclickoutside';
import {
  StyledDropdown,
  DropdownMenu,
  DropdownMenuItem,
  DropdownMenuSeparator,
  ActivatorContainer,
} from './Dropdown.styled';

interface Props {
  right?: boolean;
  activator: JSX.Element;
  onToggle?: (isOpen: boolean) => void;
}

/**
 * Header dropdown menu component
 * @param right
 * @param activator
 * @param onToggle
 * @param children
 * @returns {any}
 * @constructor
 */
const Dropdown: React.FC<Props> = ({
  right, activator, onToggle, children,
}) => {
  const [isOpen, setIsOpen] = React.useState(false);

  const dropdown = React.useRef<HTMLDivElement>(null);

  const handleClickOutside = () => {
    setIsOpen(false);

    if (onToggle) {
      onToggle(false);
    }
  };

  const handleOnClick = (value: boolean) => {
    setIsOpen(value);

    if (onToggle) {
      onToggle(value);
    }
  };

  useOnClickOutside(dropdown, handleClickOutside);

  return (
    <StyledDropdown ref={dropdown}>
      <ActivatorContainer onClick={() => handleOnClick(!isOpen)}>
        {activator}
      </ActivatorContainer>
      <DropdownMenu isOpen={isOpen} right={right}>
        {children}
      </DropdownMenu>
    </StyledDropdown>
  );
};

export {
  DropdownMenuItem,
  DropdownMenuSeparator,
};

export default Dropdown;
