import React from 'react';
import colors from 'app/styles/_colors';
import { connect } from 'react-redux';
// eslint-disable-next-line import/no-unresolved,import/no-webpack-loader-syntax
import TickWorker from 'worker-loader?name=static/[hash].worker.js!../../WebWorkers/tick.worker';
import BackgroundShuffleContext from './BackgroundShuffleContext';
import { colorCycler } from '../../styles/backgroundColors';
import { RootState } from '../../redux';
import { TickWorkerInterface } from '../../WebWorkers/tick.worker';
import {
  colorToRGBParts, RGBParts, getPartsDelta, getPartsStep, partsToRgb, sumParts,
} from './utils';
import {hex2Rgba, rgbaSetAlpha} from "../../styles/utils";

enum StaticColorState {
  STATIC,
  DYNAMIC,
  NO_USER,
}

interface Props {
  backgroundColor: string;
}

interface State {
  color: string;
  hasStaticColor: StaticColorState;

  backgroundColor: string;
  lastBackgroundColor: string;

  isInitialized: boolean;
}

/**
 * Provider class that implements background shuffle logic and
 * share background shuffle context to components
 */
class BackgroundShuffleProvider extends React.Component<Props, State> {
  readonly colorCyclerGenerator = colorCycler();

  // Cycler config
  readonly config = {
    TRANSITION_DURATION: 10000,
    TIMEOUT_DURATION: 20000,
    INTERVAL: 1000 / 6,
  };

  tickWorker?: TickWorkerInterface;

  constructor(props: Props) {
    super(props);
    this.state = {
      color: colors.primary,
      hasStaticColor: StaticColorState.NO_USER,

      backgroundColor: '',
      lastBackgroundColor: '',

      isInitialized: false,
    };
  }

  static getDerivedStateFromProps(nextProps: Readonly<Props>) {
    // if user is logged of
    if (!nextProps.backgroundColor) {
      return {
        hasStaticColor: StaticColorState.NO_USER,
      };
    }

    const hasStaticColor = nextProps.backgroundColor !== 'multicolor'
      ? StaticColorState.STATIC
      : StaticColorState.DYNAMIC;

    return {
      hasStaticColor,
    };
  }

  componentDidMount(): void {
    this.handleStaticColorChange();
  }

  // Check backgroud ssettings on update and run static or dynamic background
  componentDidUpdate(prevProps: Readonly<Props>, prevState: Readonly<State>): void {
    const { backgroundColor } = this.props;
    const { hasStaticColor } = this.state;

    if (prevState.hasStaticColor !== hasStaticColor) {
      this.handleStaticColorChange();
    }

    if (prevProps.backgroundColor !== backgroundColor
      && hasStaticColor === StaticColorState.STATIC
    ) {
      if (backgroundColor === 'multicolor') return;
      this.changeColor(backgroundColor, true);
    }
  }

  /**
   * Method that generate new background transition target
   */
  setNextBackgroundColor() {
    this.setState((prevState) => ({
      lastBackgroundColor: prevState.backgroundColor,
      backgroundColor: this.colorCyclerGenerator.next().value,
    }));
  }

  /**
   * Method that apply color change
   * @param color
   * @param force
   */
  changeColor = (color: string, force = false) => {
    const { hasStaticColor } = this.state;
    if (hasStaticColor === StaticColorState.STATIC && !force) {
      return;
    }
    this.setState({
      color,
    });
    // direct change of colors
    this.updateColor(color);
  }

  updateColor = (color: string) => {
    const proPlayersGradientMask = document.querySelectorAll('.proPlayersGradientMask');
    const footer = document.querySelector<HTMLDivElement>('.footer');
    const header = document.querySelector<HTMLDivElement>('.header');
    const prevArrow = document.querySelector<HTMLDivElement>('.playlistPrevArrow');
    const nextArrow = document.querySelector<HTMLDivElement>('.playlistNextArrow');
    const textEditorGradient = document.querySelector<HTMLDivElement>('.textEditorGradient');
    const textEditorOptionsWrapper = document.querySelector<HTMLDivElement>('.textEditorOptionsWrapper');

    let transparentColor;
    if (color[0] === '#') {
      transparentColor = hex2Rgba(color, 0);
    } else {
      transparentColor = rgbaSetAlpha(color, 0);
    }

    const proPlayersGradient = `linear-gradient(0deg, ${color} 0%, ${color} 30%, ${transparentColor} 100%)`;
    const prevArrowGradient = `linear-gradient(90deg, ${color} 0%, ${color} 40%, ${transparentColor} 100%)`;
    const nextArrowGradient = `linear-gradient(270deg, ${color} 0%, ${color} 40%, ${transparentColor} 100%)`;
    const textEditorHeaderGradient = `linear-gradient(to bottom, ${color} 0%, ${color} 70%, ${transparentColor} 100%)`;
    const textEditorOptionsWrapperGradient = `linear-gradient(90deg, ${transparentColor} 0%, ${color} 30%, ${color} 100%)`;

    Array.from(proPlayersGradientMask).forEach((player) => {
      (player as HTMLDivElement).style.background = proPlayersGradient;
    });

    if (prevArrow) prevArrow.style.background = prevArrowGradient;
    if (nextArrow) nextArrow.style.background = nextArrowGradient;
    if (textEditorGradient) textEditorGradient.style.background = textEditorHeaderGradient;
    if (textEditorOptionsWrapper) textEditorOptionsWrapper.style.background = textEditorOptionsWrapperGradient;
    if (footer) footer.style.backgroundColor = color;
    if (header) header.style.backgroundColor = color;
    document.body.style.backgroundColor = color;
  }

  /**
   * Method that runs color change after update depending on current static color setup
   */
  handleStaticColorChange() {
    const { hasStaticColor } = this.state;

    switch (hasStaticColor) {
      case StaticColorState.DYNAMIC: {
        this.setState({
          backgroundColor: colors.primary,
        });
        this.changeColor(colors.primary);
        this.updateColor(colors.primary);
        this.runCycler();
        break;
      }

      case StaticColorState.STATIC: {
        this.stopColorCycling();
        this.applyUserSelectedColor();
        break;
      }

      case StaticColorState.NO_USER: {
        this.changeColor('');
        this.updateColor('');
        break;
      }

      default:
        break;
    }

    this.setState({
      isInitialized: true,
    })
  }

  /**
   * Method that run cycle on worker
   */
  runCycler() {
    if (!this.tickWorker) {
      this.tickWorker = new TickWorker();
    }

    this.tickWorker.onmessage = this.cyclerOnMessageHandler.bind(this);

    this.tickWorker.postMessage({
      command: 'start_timeout',
      payload: {
        timeout: this.config.TIMEOUT_DURATION,
      },
    });
  }

  /**
   * Method that is handler of tick from worker
   * it runs new color and prepares parts to animate
   * then it runs method that cycle to target
   */
  cyclerOnMessageHandler() {
    this.setNextBackgroundColor();

    const { lastBackgroundColor, backgroundColor } = this.state;
    const startParts = colorToRGBParts(lastBackgroundColor);
    const nextParts = colorToRGBParts(backgroundColor);

    if (!startParts || !nextParts) {
      return;
    }

    this.runColorChange(startParts, nextParts);
  }

  /**
   * Method that runs color cycling
   * @param startColor
   * @param endColor
   */
  runColorChange(startColor: RGBParts, endColor: RGBParts) {
    // setup
    let step = 1;
    let resultColor: RGBParts = { ...startColor };
    const steps = Math.floor(this.config.TRANSITION_DURATION / this.config.INTERVAL);
    const colorDelta = getPartsDelta(startColor, endColor);
    const colorStep = getPartsStep(colorDelta, steps);

    // Function that is called in every step
    const stepFunction = () => {
      if (step > steps) return;
      // if last step, change color to target color and run next cycle
      if (step === steps) {
        this.changeColor(partsToRgb(endColor));
        this.runCycler();
        return;
      }

      step += 1;
      resultColor = sumParts(resultColor, colorStep);
      this.changeColor(partsToRgb(resultColor));

      // send command to tick worker to start next timeout
      this.tickWorker?.postMessage({
        command: 'start_timeout',
        payload: {
          timeout: this.config.INTERVAL,
        },
      });
    };

    // run step function
    if (this.tickWorker) {
      this.tickWorker.onmessage = stepFunction.bind(this);
      stepFunction();
    }
  }

  /**
   * Stop tick worker
   */
  stopColorCycling() {
    this.tickWorker?.postMessage({
      command: 'stop',
    });
    this.tickWorker?.terminate();
    this.tickWorker = undefined;
  }

  /**
   *  Method that applies user selected static color
   */
  applyUserSelectedColor() {
    const { backgroundColor } = this.props;
    this.changeColor(backgroundColor, true);
    this.updateColor(backgroundColor);
  }

  forceReapplyOfColor() {
    const { backgroundColor } = this.props;
    const { hasStaticColor, color, isInitialized } = this.state;

    if (!isInitialized) {
      setTimeout(() => this.forceReapplyOfColor(), 200);
    }

    switch (hasStaticColor) {
      case StaticColorState.DYNAMIC: {
        this.updateColor(color);
        break;
      }

      case StaticColorState.STATIC: {
        this.updateColor(backgroundColor);
        break;
      }

      case StaticColorState.NO_USER: {
        this.updateColor('');
        break;
      }

      default:
        break;
    }
  }

  render() {
    const { children } = this.props;
    const { color } = this.state;
    const { changeColor, colorCyclerGenerator, forceReapplyOfColor } = this;

    return (
      <BackgroundShuffleContext.Provider
        value={{
          color,
          changeColor,
          colorCycler: colorCyclerGenerator,
          forceColorUpdate: forceReapplyOfColor.bind(this),
        }}
      >
        {children}
      </BackgroundShuffleContext.Provider>
    );
  }
}

const mapStateToProps = (state: RootState) => ({
  backgroundColor: state.users.userDetails.backgroundColor,
});

export default connect(
  mapStateToProps,
)(BackgroundShuffleProvider);
