import React from 'react';
import { Text, Title } from 'app/components/Modal/Modal.styled';

import { SolidButton } from 'app/components/Button/Button.styled';
import { useDispatch, useSelector } from 'react-redux';
import { getBase, setShowUpgradeModal } from 'app/redux/modules/base';
import { GridWrapper } from '../Grid/Grid.styled';
import Modal from '../Modal/Modal';
import { UpgradeImage, ButtonWrapper } from './UpgradeModal.styled';


const UpgradeModal: React.FC = () => {
  const dispatch = useDispatch();
  const { showUpgradeModal } = useSelector(getBase);

  const handleOnCloseClick = () => {
    dispatch(setShowUpgradeModal({
      showUpgradeModal: false,
    }));
  };

  return (
    <Modal
      isOpen={showUpgradeModal}
      onCloseClick={handleOnCloseClick}
    >
      <GridWrapper doubleSpace>
        <UpgradeImage />
      </GridWrapper>
      <GridWrapper>
        <Title>
          Want to upgrade?
        </Title>
      </GridWrapper>
      <GridWrapper doubleSpace>
        <Text center>
          Please access Noisli from a desktop
          <br />
          or a laptop device to upgrade your plan.
        </Text>
      </GridWrapper>
      <ButtonWrapper>
        <SolidButton onClick={handleOnCloseClick} withSimpleShadow>Ok</SolidButton>
      </ButtonWrapper>
    </Modal>
  );
};

export default UpgradeModal;
