/* eslint-disable no-param-reassign */
import { PayloadAction } from 'redux-starter-kit';
import {
  SetShufflePayload,
  SetShuffledCombos,
  SetPlayerShufflePayload,
  SetPlayersShufflingPayload,
  ShuffleTimePayload,
} from 'app/redux/modules/common/shuffle.interfaces';
import {
  PlayersState,
  SetPlayerPlayingPayload,
  SetPlayerVolumePayload,
  SetPlayerIsLoadedPayload,
  SetMasterPlayerPlayingPayload,
  SetMasterPlayerVolumePayload,
  SetMasterPlayerMutedPayload,
  SetOscillationPayload,
  OscillatorPlayerPayload,
  Oscillator,
  SetPlayerOscillationPayload,
  SetPlayersVolumePayload,
  StopPlayersAfterShuffle,
  SetUseHomepagePlayers,
} from './players.interfaces';
import { SetActiveComboPayload } from '../common/combos.interfaces';

/**
 * Reduer to set one player instance playing state
 * @param state
 * @param action
 */
export const setPlayerPlayingReducer = (
  state: PlayersState, action: PayloadAction<SetPlayerPlayingPayload>,
) => {
  if (state.useHomepage) {
    const idx = state.playersHomepage.findIndex((player) => player.id === action.payload.id);
    if (idx > -1) {
      state.playersHomepage[idx].isPlaying = action.payload.isPlaying;
    }
    return;
  }
  const idx = state.players.findIndex((player) => player.id === action.payload.id);
  if (idx > -1) {
    state.players[idx].isPlaying = action.payload.isPlaying;
  }
};

/**
 * Reduer to set one player instance volume state
 * @param state
 * @param action
 */
export const setPlayerVolumeReducer = (
  state: PlayersState, action: PayloadAction<SetPlayerVolumePayload>,
) => {
  if (state.useHomepage) {
    const idx = state.playersHomepage.findIndex((player) => player.id === action.payload.id);
    if (idx > -1) {
      state.playersHomepage[idx].volume = action.payload.volume;
    }
    return;
  }
  const idx = state.players.findIndex((player) => player.id === action.payload.id);
  if (idx > -1) {
    state.players[idx].volume = action.payload.volume;
  }
};

/**
 * Reducer to set players instances passed in action playing state
 * @param state
 * @param action
 */
export const setPlayersVolumeReducer = (
  state: PlayersState, action: PayloadAction<SetPlayersVolumePayload>,
) => {
  const { items } = action.payload;

  state.players.forEach((player) => {
    const item = items.find((el) => el.id === player.id);
    if (item) {
      player.volume = item.volume;
    }
  });
};

/**
 * Reducer that reverts volume of non playing players
 * It is used after actions that modify volume (eg. shuffle, oscillation, timer)
 * @param state
 * @param action
 */
export const revertPlayersVolumeReducer = (
  state: PlayersState, action: PayloadAction<SetPlayersVolumePayload>,
) => {
  const { items } = action.payload;

  state.players.forEach((player) => {
    const item = items.find((el) => el.id === player.id);
    if (item && !player.isPlaying) {
      player.volume = item.volume;
    }
  });
};


/**
 * Reducer to indicate that player is ready to play
 * @param state
 * @param action
 */
export const setPlayerIsLoadedReducer = (
  state: PlayersState, action: PayloadAction<SetPlayerIsLoadedPayload>,
) => {
  const idx = state.players.findIndex((player) => player.id === action.payload.id);
  if (idx > -1) {
    state.players[idx].isLoaded = action.payload.isLoaded;
  }
};

export const stopAllPlayersReducer = (state: PlayersState) => {
  state.playersHomepage.forEach((player) => {
    player.isPlaying = false;
    player.isOscillating = false;
  });
  state.players.forEach((player) => {
    player.isPlaying = false;
    player.isOscillating = false;
  });
  state.masterController.isPlaying = false;
  state.activeCombo = undefined;
};

export const setMasterPlayerPlayingReducer = (
  state: PlayersState, action: PayloadAction<SetMasterPlayerPlayingPayload>,
) => {
  state.masterController.isPlaying = action.payload.isPlaying;
};

export const setMasterPlayerVolumeReducer = (
  state: PlayersState, action: PayloadAction<SetMasterPlayerVolumePayload>,
) => {
  state.masterController.lastVolume = state.masterController.volume;
  state.masterController.volume = action.payload.volume;
};

export const setMasterPlayerIsMutedReducer = (
  state: PlayersState, action: PayloadAction<SetMasterPlayerMutedPayload>,
) => {
  state.masterController.isMuted = action.payload.isMuted;
};

export const setOscillatorStateReducer = (
  state: PlayersState, action: PayloadAction<SetOscillationPayload>,
) => {
  state.masterController.isOscillating = action.payload.isOscillating;
};

export const clearOscillatorReducer = (
  state: PlayersState, action: PayloadAction<OscillatorPlayerPayload>,
) => {
  const idx = state.masterController.scheduledOscillation.findIndex(
    (oscillator) => oscillator.playerId === action.payload.playerId,
  );

  if (idx > -1) {
    state.masterController.scheduledOscillation.splice(idx, 1);
  }
};

/**
 * Reducer to add oscillator to master controller
 * @param state
 * @param action
 */
export const startOscillatorReducer = (
  state: PlayersState, action: PayloadAction<OscillatorPlayerPayload>,
) => {
  const player = state.players.find((playerItem) => playerItem.id === action.payload.playerId);

  if (player) {
    const oscillator: Oscillator = {
      playerId: player.id,
    };

    state.masterController.scheduledOscillation.push(oscillator);
  }
};

export const updateOscillatorReducer = (
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  state: PlayersState, action: PayloadAction<OscillatorPlayerPayload>,
) => {};

export const setPlayerOscillatingReducer = (
  state: PlayersState, action: PayloadAction<SetPlayerOscillationPayload>,
) => {
  const { playerId, isOscillating } = action.payload;

  const idx = state.players.findIndex(
    (player) => player.id === playerId,
  );

  if (idx > -1) {
    state.players[idx].isOscillating = isOscillating;
  }
};

export const setShufflingStateReducer = (
  state: PlayersState, action: PayloadAction<SetShufflePayload>,
) => {
  state.masterController.isShuffling = action.payload.isShuffling;
};

export const setShufflingTranstionStateReducer = (
  state: PlayersState, action: PayloadAction<SetShufflePayload>,
) => {
  state.masterController.isShufflingTransition = action.payload.isShuffling;
};

export const setShuffledCombosReducer = (
  state: PlayersState, action: PayloadAction<SetShuffledCombos>,
) => {
  state.masterController.scheduledShuffle = action.payload;
};

export const updateShuffleReducer = (
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  state: PlayersState,
) => {};

export const renewShuffleReducer = (
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  state: PlayersState,
) => {};

export const destroyShuffleReducer = (
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  state: PlayersState,
) => {};

export const setPlayerShufflingReducer = (
  state: PlayersState, action: PayloadAction<SetPlayerShufflePayload>,
) => {
  const { playerId, isShuffling } = action.payload;

  const idx = state.players.findIndex(
    (player) => player.id === playerId,
  );

  if (idx > -1) {
    state.players[idx].isShuffling = isShuffling;
  }
};

/**
 * Reducer that marked players shuffling
 * @param state
 * @param action
 */
export const setPlayersShufflingReducer = (
  state: PlayersState, action: PayloadAction<SetPlayersShufflingPayload>,
) => {
  const { playersIds, isShuffling } = action.payload;

  state.players.forEach((player) => {
    if (playersIds.includes(player.id)) {
      player.isShuffling = isShuffling;
    }
  });
};

/**
 * Reducer that is run before starting the shuffle
 * @param state
 * @param action
 */
export const startupPlayersForShuffleReducer = (
  state: PlayersState, action: PayloadAction<string[]>,
) => {
  state.players.forEach((player) => {
    if (action.payload.includes(player.id)) {
      player.volume = 0;
      player.isPlaying = true;
    }
  });
};

/**
 * Reducer that stops players after shuffle
 * @param state
 * @param action
 */
export const stopPlayersAfterShuffleReducer = (
  state: PlayersState, action: PayloadAction<StopPlayersAfterShuffle>,
) => {
  const { items } = action.payload;

  state.players.forEach((player) => {
    const item = items.find((el) => el.id === player.id);
    if (item) {
      player.isPlaying = false;
    }
  });
};

/**
 * Reducer that sets players volume after shuffle
 * @param state
 * @param action
 */
export const setVolumeAfterShuffleReducer = (
  state: PlayersState, action: PayloadAction<StopPlayersAfterShuffle>,
) => {
  const { items } = action.payload;

  state.players.forEach((player) => {
    const item = items.find((el) => el.id === player.id);
    if (item) {
      player.volume = item.volume;
    }
  });
};

export const setActiveComboReducer = (
  state: PlayersState, action: PayloadAction<SetActiveComboPayload>,
) => {
  state.activeCombo = action.payload.combo;
};

export const setShuffleTimeReducer = (
  state: PlayersState, action: PayloadAction<ShuffleTimePayload>,
) => {
  state.shuffleConfig.time = action.payload.time;
};

export const setUseHomepagePlayersReducer = (
  state: PlayersState, action: PayloadAction<SetUseHomepagePlayers>,
) => {
  state.useHomepage = action.payload.useHomepage;
};
