import 'slick-carousel/slick/slick.css';
import { css } from 'styled-components';
import rwd, { Breakpoint } from '../rwd';
// import 'slick-carousel/slick/slick-theme.css';

const styles = css`
  /* Arrows */
  .slick-prev,
  .slick-next {
    font-size: 0;
    line-height: 0;
    top: 0;
    bottom: 0;
    position: absolute;
    width: 60px;
    z-index: 1;
    border: 0;
    padding: 0;

    ${rwd[Breakpoint.MEDIUM]`
      display: inline-flex;
    `}
    
    &:hover,
    &:focus {
      outline: none;
      
      &::before {
        outline: none;
        background: ${(props) => props.theme.colors.white30};
      }
    }
    
    &::before {
      content: '';
      cursor: pointer;
      position: absolute;
      width: 26px;
      height: 26px;
      top: 64px;
  
      transform: translate(0, -50%);
      border-radius: 50%;
  
      cursor: pointer;
  
      color: ${(props) => props.theme.colors.white};
      border: none;
      outline: none;
      background: ${(props) => props.theme.colors.white15};
    }
    
    svg {
      cursor: pointer;
      position: absolute;
      top: 64px;
      transform: translateY(-50%);
    }

    .slick-disabled {
      &::before {
        opacity: .25;
      }
    }
  }

  .slick-prev {
    left: 0;
    
    &::before {
      left: 4px;
    }
    
    svg {
      left: 10px;
    }
  }

  .slick-next {
    right: 0;
    //background: linear-gradient(270deg, #5CE5B4 0%, #5CE5B4 40%, rgba(92,229,180,0.00) 100%);
    
    &::before {
      right: 4px;
    }
    
    svg {
      right: 10px;
    }
  }
`;

export default styles;
