import {
  configureStore, getDefaultMiddleware, EnhancedStore,
} from 'redux-starter-kit';
import { combineEpics, createEpicMiddleware } from 'redux-observable';
import {
  combineReducers, Reducer, Middleware, AnyAction, ReducersMapObject,
} from 'redux';
import baseReducer from './modules/base';
import playersReducer from './modules/players';
import favouritesReducer from './modules/favourites';
import playlistsReducer from './modules/playlists';
import usersReducer, { resetUser } from './modules/users';
import subscriptionsReducer from './modules/subscriptions';
import textsReducer from './modules/texts';
import timerReducer from './modules/timer';
import { PlayersState } from './modules/players/players.interfaces';
import { FavouritesState } from './modules/favourites/favourites.interfaces';
import { PlaylistsState } from './modules/playlists/playlists.interfaces';
import { UsersState } from './modules/users/users.interfaces';
import { createShuffleMiddleware } from './middleware/ShuffleMiddleware/ShuffleMiddleware';
import { createOscillationMiddleware } from './middleware/OscillationMiddleware/OscillationMiddleware';
import { createTimerMiddleware } from './middleware/TimerMiddleware/TimerMiddleware';
import FavouritesMiddleware from './middleware/FavouritesMiddleware/FavouritesMiddleware';
import PlaylistsMiddleware from './middleware/PlaylistsMiddleware/PlaylistsMiddleware';
import { BaseState } from './modules/base/base.interfaces';
import { createSocketMiddleware } from './middleware/SocketMiddleware/SocketMiddleware';
import createUserLimitMiddleware from './middleware/UserLimitMiddleware/UserLimitMiddleware';
import userEpics from './modules/users/users.epics';
import favouritesEpics from './modules/favourites/favourites.epics';
import textsEpics from './modules/texts/texts.epics';
import timerEpics from './modules/timer/timer.epics';
import { SubscriptionsState } from './modules/subscriptions/subscriptions.interfaces';
import { TextsState } from './modules/texts/texts.interfaces';
import { TimerState } from './modules/timer/timer.interfaces';
import { createAuthMiddleware } from './middleware/AuthMiddleware/AuthMiddleware';

export interface RootState {
  base: BaseState;
  players: PlayersState;
  favourites: FavouritesState;
  playlists: PlaylistsState;
  users: UsersState;
  subscriptions: SubscriptionsState;
  texts: TextsState;
  timer: TimerState;
}

export const rootEpic = combineEpics(
  ...userEpics,
  ...favouritesEpics,
  ...textsEpics,
  ...timerEpics,
);

/**
 * Factory that returns reducers and any additional reducers (used with chrome extension)
 * @param additionalReducers
 * @returns {Reducer<CombinedState<unknown>>}
 */
export const appReducerFactory = (
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  additionalReducers: ReducersMapObject<any, any> = {},
) => combineReducers({
  base: baseReducer,
  players: playersReducer,
  favourites: favouritesReducer,
  playlists: playlistsReducer,
  users: usersReducer,
  subscriptions: subscriptionsReducer,
  texts: textsReducer,
  timer: timerReducer,
  ...additionalReducers,
});

// eslint-disable-next-line @typescript-eslint/no-explicit-any
type RootReducerFactory = (appReducer: Reducer<any>) => Reducer<RootState>

const rootReducerFactory: RootReducerFactory = (appReducer) => (state, action) => {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  if (action.type === (resetUser as any).toString()) {
    // eslint-disable-next-line no-param-reassign
    state = undefined;
  }

  return appReducer(state, action);
};

/**
 * Function that initializes full redux store
 * @param initalState
 * @param middlewares
 * @param appReducer
 */
// eslint-disable-next-line import/prefer-default-export
export const initializeStore = (
  initalState: RootState | undefined,
  middlewares: Middleware[] = [
    createUserLimitMiddleware(),
  ],
  appReducer = appReducerFactory(),
): EnhancedStore<RootState, AnyAction> => {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const epicMiddleware = createEpicMiddleware<any, any, RootState>();

  const store = configureStore({
    reducer: rootReducerFactory(appReducer),
    devTools: !!process.env.USE_DEVTOOLS,
    middleware: [
      ...middlewares,
      ...getDefaultMiddleware(),
      createAuthMiddleware(),
      createTimerMiddleware(),
      FavouritesMiddleware,
      PlaylistsMiddleware,
      // createSocketMiddleware(),
      createOscillationMiddleware(),
      createShuffleMiddleware(),
      epicMiddleware,
    ],
    preloadedState: initalState,
  });

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  epicMiddleware.run(rootEpic as any);

  return store;
};

/**
 * Function that initialize redux store without middlewares
 * Used at server for lite server side rendering
 * @param initalState
 */
// eslint-disable-next-line import/prefer-default-export
export const initializeLiteStore = (
  initalState: RootState | undefined,
) => {
  const store = configureStore({
    reducer: rootReducerFactory(appReducerFactory()),
    preloadedState: initalState,
  });
  return store;
};

export type Store = ReturnType<typeof initializeStore>;
