import {
  from, scheduled, interval,
} from 'rxjs';
import {
  catchError, flatMap, mergeMap, filter, debounce,
} from 'rxjs/operators';
import { async } from 'rxjs/internal/scheduler/async';
import UsersApi from 'app/Api/Users.api';
import { Epic, ofType } from 'redux-observable';
import { RootState } from 'app/redux';
import { UserDetails } from '@noisli/common';
import catchAuthError from 'app/redux/utils/catchAuthError';
// eslint-disable-next-line import/no-extraneous-dependencies
import { Action } from 'redux';
import { PayloadAction } from 'redux-starter-kit';
import { completeOnboarding, setUser, setUserDetails } from '.';
import {
  setOscillatorState, setShufflingState, setMasterPlayerIsMuted, setMasterPlayerVolume,
} from '../players';
import {
  SetMasterPlayerMutedPayload,
  SetMasterPlayerVolumePayload,
  SetOscillationPayload,
} from '../players/players.interfaces';
import { SetShufflePayload } from '../common/shuffle.interfaces';

const setUserDetailsResolver = (userDetails: UserDetails | undefined) => {
  if (!userDetails) {
    return;
  }

  // eslint-disable-next-line consistent-return
  return setUserDetails(userDetails);
};

/**
 * Epic that saves onboarding completion
 * @param action$
 */
// eslint-disable-next-line @typescript-eslint/no-explicit-any
const completeOnboardingEpic: Epic<Action<string>, any, RootState> = (action$) => action$.pipe(
  ofType(completeOnboarding.toString()),
  mergeMap(() => from(UsersApi.preferencesUpdate({
    didOnboarding: true,
  })).pipe(
    flatMap((data) => scheduled([
      setUser(data.user),
      setUserDetailsResolver(data.userDetails),
    ], async)),
    catchError(catchAuthError),
  )),
);

/**
 * Epic that save shuffle preference for user
 * @param action$
 * @param rootState
 */
const setShuffleEpic: Epic<
// eslint-disable-next-line @typescript-eslint/no-explicit-any
  PayloadAction<SetShufflePayload, string>, any, RootState
> = (action$, rootState) => action$.pipe(
  ofType(setShufflingState.toString()),
  filter((action) => action.payload.isShuffling !== rootState.value.users.userDetails.shuffle),
  mergeMap((action) => from(UsersApi.preferencesUpdate({
    shuffle: action.payload.isShuffling,
  })).pipe(
    flatMap((data) => scheduled([
      setUser(data.user),
      setUserDetailsResolver(data.userDetails),
    ], async)),
    catchError(catchAuthError),
  )),
);

/**
 * Epic that save oscillation preference for user
 * @param action$
 * @param rootState
 */
const setOscillationEpic: Epic<
// eslint-disable-next-line @typescript-eslint/no-explicit-any
  PayloadAction<SetOscillationPayload, string>, any, RootState
> = (action$, rootState) => action$.pipe(
  ofType(setOscillatorState.toString()),
  filter(
    (action) => action.payload.isOscillating !== rootState.value.users.userDetails.oscillation,
  ),
  mergeMap((action) => from(UsersApi.preferencesUpdate({
    oscillation: action.payload.isOscillating,
  })).pipe(
    flatMap((data) => scheduled([
      setUser(data.user),
      setUserDetailsResolver(data.userDetails),
    ], async)),
    catchError(catchAuthError),
  )),
);

/**
 * Epic that save muted preference for user
 * @param action$
 * @param rootState
 */
const setMasterPlayerMutedEpic: Epic<
// eslint-disable-next-line @typescript-eslint/no-explicit-any
  PayloadAction<SetMasterPlayerMutedPayload, string>, any, RootState
  > = (action$, rootState) => action$.pipe(
    ofType(setMasterPlayerIsMuted.toString()),
    filter(
      (action) => action.payload.isMuted !== rootState.value.users.userDetails.masterVolumeMuted
        && !action.payload.mutedByTimer,
    ),
    mergeMap((action) => from(UsersApi.preferencesUpdate({
      masterVolumeMuted: action.payload.isMuted,
    })).pipe(
      flatMap((data) => scheduled([
        setUser(data.user),
        setUserDetailsResolver(data.userDetails),
      ], async)),
      catchError(catchAuthError),
    )),
  );

/**
 * Epic that save master volume preference for user
 * @param action$
 * @param rootState
 */
const setMasterPlayerVolumeEpic: Epic<
// eslint-disable-next-line @typescript-eslint/no-explicit-any
  PayloadAction<SetMasterPlayerVolumePayload, string>, any, RootState
  > = (action$, rootState) => action$.pipe(
    debounce(() => interval(1000)),
    ofType(setMasterPlayerVolume.toString()),
    filter((action) => action.payload.volume !== rootState.value.users.userDetails.masterVolume),
    mergeMap((action) => from(UsersApi.preferencesUpdate({
      masterVolume: action.payload.volume,
    })).pipe(
      flatMap((data) => scheduled([
        setUser(data.user),
        setUserDetailsResolver(data.userDetails),
      ], async)),
      catchError(catchAuthError),
    )),
  );

export default [
  completeOnboardingEpic,
  setShuffleEpic,
  setOscillationEpic,
  setMasterPlayerMutedEpic,
  setMasterPlayerVolumeEpic,
];
