import React from 'react';
import { useSelector } from 'react-redux';
import IndexPage from './pages/IndexPage';
import FavoritesPage from './pages/FavoritesPage';
import PlaylistsPage from './pages/PlaylistsPage';
import { getChromeExtension } from './redux/modules/chromeExtension';
import { ChromeExtensionPage } from './redux/modules/chromeExtension/chromeExtension.interfaces';

const App: React.FC = () => {
  const { page } = useSelector(getChromeExtension);

  return (
    <>
      { page === ChromeExtensionPage.PLAYLISTS && (
        <PlaylistsPage />
      )}

      { page === ChromeExtensionPage.FAVORITES && (
        <FavoritesPage />
      )}

      { page === ChromeExtensionPage.ONBOARDING && (
        <IndexPage />
      )}
    </>
  );
};

export default App;
