import { Player } from 'app/redux/modules/players/players.interfaces';
import Oscillator, { OscillatorCreateArgs } from './Oscillator';

/**
 * Class that is responsible of managing the oscillators of playing players
 * It creates oscillators for new players or when the oscillation is on
 * It destroys oscillators if player is stopped
 * It stops the oscillators when shuffle is starting
 * It revert the oscillator effect is oscillation is off
 */
export default class OscillationManager {
  oscillators: Oscillator[] = [];

  createOrUpdate({
    dispatch,
    oscillationConfig,
    playerInstance,
  }: OscillatorCreateArgs) {
    const oscillator = this.oscillators.find(
      (item) => item.playerInstance.id === playerInstance.id,
    );

    if (!oscillator) {
      const newOscillator = new Oscillator({
        dispatch,
        oscillationConfig,
        playerInstance,
      });

      this.oscillators.push(newOscillator);
      return;
    }

    oscillator.update({
      playerInstance,
    });
  }

  destroy(playerId: string) {
    const oscillator = this.oscillators.find((item) => item.playerInstance.id === playerId);

    if (!oscillator) {
      return;
    }

    oscillator.destroy();
    this.oscillators = this.oscillators.filter((item) => item.playerInstance.id !== playerId);
  }

  returnToDefaultValueAndDestroy(playerInstance: Player, targetVolume: number) {
    const oscillator = this.oscillators.find(
      (item) => item.playerInstance.id === playerInstance.id,
    );

    if (!oscillator) {
      return;
    }

    oscillator.returnToTargetVolumeAndDestroy(playerInstance, targetVolume).then(() => {
      this.oscillators = this.oscillators.filter(
        (item) => item.playerInstance.id !== playerInstance.id,
      );
    }).catch(() => {});
  }
}
