import React from 'react';
import { ThemeProvider } from 'styled-components';
import { Provider } from 'react-redux';
import App from 'next/app';
import Router from 'next/router';
import { AppWithReduxInterface } from 'app/redux/with-redux-store';
import { setIsMobile } from 'app/redux/modules/base';
import theme from 'app/styles/theme';
import GlobalStyle from 'app/styles/global';
import { setAuthHeader } from 'app/components/withAuth/withAuth';
import {
  initializeLiteStore, initializeStore, RootState, Store,
} from '../redux';
import PlayerMasterController
  from '../components/_Core/PlayerMasterController/PlayerMasterController/PlayerMasterController';
import BackgroundShuffleProvider from '../components/BackgroundShuffleContext/BackgroundShuffleProvider';
import UpgradeModal from '../components/UpgradeModal/UpgradeModal';
import ErrorBanner from '../components/ErrorBanner/ErrorBanner';
import mobileDetect from '../utils/mobileDetect';
import * as gtag from '../utils/GoogleAnalytics/gtag';

// Send pageview after every route change
Router.events.on('routeChangeComplete', url => gtag.pageview(url?.split('?')[0]));

// Define if we are on server
const isServer = typeof window === 'undefined';
const NEXT_REDUX_STORE = 'CSSUnitValues';

/**
 * Function that initialize store or retirive already existing store
 * Store is assgned to window for compabiblity purposes of next.js
 * @param initialState
 * @returns {EnhancedStore<RootState, AnyAction> | any}
 */
export function getOrCreateStore(initialState: RootState | undefined = undefined): Store {
  // Create store if unavailable on the client and set it on the window object
  // @ts-ignore
  if (isServer) {
    return initializeLiteStore(initialState);
  }
  // @ts-ignore
  if (!window[NEXT_REDUX_STORE]) {
    // @ts-ignore
    window[NEXT_REDUX_STORE] = initializeStore(initialState);
  }
  // @ts-ignore
  return window[NEXT_REDUX_STORE];
}

/**
 * Base App component
 * It is used on every page as top level component
 * Refer to next.js docs to find out more
 *
 * IMPORTANT!!!!
 * NoisliApp component is using pageProp called `withPlayer` to determine if players should be loaded on page
 * You can pass that prop in getInitialProps static method of page component
 * One of examples of usage you might look into: ./timer/index.tsx
 * Without passing this prop, the sound players will be removed
 * !!!!
 */
class NoisliApp extends App<AppWithReduxInterface> {
  reduxStore: Store = getOrCreateStore();

  state = {
    isMobile: false,
    showOfflineAlert: false,
  };

  showOfflineAlert = () => {
    this.setState({
      showOfflineAlert: true,
    });
  };

  hideOfflineAlert = () => {
    this.setState({
      showOfflineAlert: false,
    });
  };

  componentDidMount() {
    this.setState({
      isMobile: mobileDetect(),
    });

    if (this.reduxStore) {
      this.reduxStore.dispatch(setIsMobile({
        isMobile: mobileDetect(),
      }));
    }

    setAuthHeader();

    window.addEventListener('offline', this.showOfflineAlert);
    window.addEventListener('online', this.hideOfflineAlert);
  }

  componentWillUnmount(): void {
    window.removeEventListener('offline', this.showOfflineAlert);
    window.removeEventListener('online', this.hideOfflineAlert);
  }

  render() {
    const { showOfflineAlert } = this.state;
    const { Component, pageProps } = this.props;

    return (
      <ThemeProvider theme={theme}>
        <Provider store={this.reduxStore}>
          <BackgroundShuffleProvider>
            <GlobalStyle />
            <Component {...pageProps} />
            {pageProps.withPlayer && (
              <>
                <PlayerMasterController useHomepage={pageProps.useHomepage} />
                <audio src="https://cdn2.noisli.com/alerts/timerend.mp3" id="noisli-timer-end" />
                <audio src="https://cdn2.noisli.com/alerts/breakend.mp3" id="noisli-break-end" />
              </>
            )}
            <UpgradeModal />
            <ErrorBanner
              show={showOfflineAlert}
              message="Oops, it looks like you're offline. Please check your Internet connection."
            />
          </BackgroundShuffleProvider>
        </Provider>
      </ThemeProvider>
    );
  }
}

export type NoisliAppType = typeof NoisliApp;

NoisliApp.getInitialProps = async (appContext) => {
  const appProps = await App.getInitialProps(appContext);
  const res = appContext?.ctx?.res;
  res?.setHeader('X-Frame-Options', 'deny');
  return { ...appProps };
};

export default NoisliApp;
