import { createSlice } from 'redux-starter-kit';
import state from './favourites.state';
import {
  unmarkActiveFavouriteReducer,
  markActiveFavouriteReducer,
  setFavouriteIsShufflingReducer,
  unmarkFavouriteReducer,
  markFavouriteReducer,
  setFavouritesReducer,
  updateFavouriteReducer,
  createFavouriteReducer,
  deleteFavouriteReducer,
  setSharedFavouriteReducer,
  setSharedFavouritePlayingReducer,
  resetUserFavouritesReducer,
} from './favourites.reducers';

const favouritesSlice = createSlice({
  slice: 'favourites',

  initialState: state,

  reducers: {
    unmarkActiveFavourite: unmarkActiveFavouriteReducer,
    markActiveFavourite: markActiveFavouriteReducer,
    markFavourite: markFavouriteReducer,
    unmarkFavourite: unmarkFavouriteReducer,
    setFavouriteIsShuffling: setFavouriteIsShufflingReducer,

    setFavourites: setFavouritesReducer,
    updateFavourite: updateFavouriteReducer,
    deleteFavourite: deleteFavouriteReducer,

    createFavourite: createFavouriteReducer,
    fetchFavourites: () => {},

    setSharedFavourite: setSharedFavouriteReducer,
    setSharedFavouritePlaying: setSharedFavouritePlayingReducer,

    resetUserFavourites: resetUserFavouritesReducer,
  },
});
// Extract the action creators object and the reducer
const { actions, reducer, selectors } = favouritesSlice;
// Extract and export each action creator by name
export const {
  unmarkActiveFavourite,
  markActiveFavourite,
  markFavourite,
  unmarkFavourite,
  setFavouriteIsShuffling,

  setFavourites,
  fetchFavourites,
  updateFavourite,
  createFavourite,
  deleteFavourite,
  setSharedFavourite,
  setSharedFavouritePlaying,

  resetUserFavourites,
} = actions;

export const { getFavourites } = selectors;
// Export the reducer, either as a default or named export
export default reducer;
