import axios from './AxiosInstance';
import {
  SaveTextDto,
} from '@noisli/common';

/**
 * Static class that have all connection to API regarding Text module
 */
export default class TextApi {
  static apiUrl = `${process.env.REACT_APP_API_URL}/text`;

  static async saveText(body: SaveTextDto) {
    const response = await axios.post<string>(`${TextApi.apiUrl}`, body, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async getDownloadToken() {
    const response = await axios.post<string>(`${TextApi.apiUrl}/get-download-token`, {}, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }
}
