import {
  interval,
} from 'rxjs';
import {
  debounce, catchError, tap, ignoreElements, filter, map,
} from 'rxjs/operators';
import { Epic, ofType } from 'redux-observable';
import { RootState } from 'app/redux';
// eslint-disable-next-line import/no-extraneous-dependencies
import { PayloadAction } from 'redux-starter-kit';
import { fromPromise } from 'rxjs/internal-compatibility';
import TimerApi from 'app/Api/Timer.api';
import {
  updateTimerConfig, setActualTime, setSessionsCompleted, setActiveTimerCombo, setTimerTitle,
} from '.';
import catchAuthError from '../../utils/catchAuthError';
import {
  SetActiveTimerComboPayload,
  SetActualTimePayload,
  SetSessionsCompletedPayload,
  UpdateTimerConfigPayload,
} from './timer.interfaces';
import { setTimerPageTitle } from './timer.helpers';

/**
 * Epic that saves timer config after changes in timer config
 * Timer config change may occur in timer settings page
 * @param action$
 * @param state
 */
const saveTimerConfigEpic: Epic<
// eslint-disable-next-line @typescript-eslint/no-explicit-any
  PayloadAction<UpdateTimerConfigPayload, string>, any, RootState
> = (action$, state) => action$.pipe(
  ofType(updateTimerConfig.toString()),
  debounce(() => interval(500)),
  tap(() => {
    const { timerConfig } = state.value.timer;
    const apiCall$ = fromPromise(TimerApi.updateConfig(timerConfig)).pipe(
      catchError(catchAuthError),
    );

    return apiCall$;
  }),
  ignoreElements(),
);

/**
 * After session completed, epic is run to save session in server
 * @param action$
 * @param state
 * @returns {Observable<never>}
 */
const saveSessionsCompletedEpic: Epic<
// eslint-disable-next-line @typescript-eslint/no-explicit-any
  PayloadAction<SetSessionsCompletedPayload, string>, any, RootState
  > = (action$, state) => action$.pipe(
    ofType(setSessionsCompleted.toString()),
    debounce(() => interval(300)),
    tap(() => {
      const { timerConfig } = state.value.timer;
      const apiCall$ = fromPromise(TimerApi.updateConfig({
        ...timerConfig,
      })).pipe(
        catchError(catchAuthError),
      );

      return apiCall$;
    }),
    ignoreElements(),
  );

/**
 * After every change of active combo, run epic
 * Epic is debounced by 4 seconds to prevent contstant saving to server in case of changing volume
 * Or user tinkering with player
 *
 * @param action$
 * @param state
 * @returns {Observable<never>}
 */
const saveActiveTimerComboEpic: Epic<
// eslint-disable-next-line @typescript-eslint/no-explicit-any
  PayloadAction<SetActiveTimerComboPayload, string>, any, RootState
  > = (action$, state) => action$.pipe(
    ofType(setActiveTimerCombo.toString()),
    debounce(() => interval(4000)),
    tap((action) => {
      const { timerConfig } = state.value.timer;
      const apiCall$ = fromPromise(TimerApi.updateConfig({
        ...timerConfig,
        savedTimerCombo: action.payload.timerCombo,
      })).pipe(
        catchError(catchAuthError),
      );

      return apiCall$;
    }),
    ignoreElements(),
  );

/**
 * If timer is running, it runs this epic on every actual time set
 * And updates browser tab title
 * @param action$
 * @param state
 * @returns {Observable<C>}
 */
const changeBrowserTabTitleOnTimeUpdateEpic: Epic<
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  PayloadAction<SetActualTimePayload, string>, any, RootState
> = (action$, state) => action$.pipe(
  ofType(setActualTime.toString()),
  filter(
    () => state.value.timer.timerConfig.showTimerOnBrowserTab
      && state.value.timer.isRunning,
  ),
  map((action) => {
    const { actualTime } = action.payload;
    const { currentTimerType } = state.value.timer;
    return setTimerTitle({
      title: setTimerPageTitle(currentTimerType, actualTime),
    });
  }),
);

export default [
  saveTimerConfigEpic,
  saveActiveTimerComboEpic,
  saveSessionsCompletedEpic,
  changeBrowserTabTitleOnTimeUpdateEpic,
];
