import playersList from 'app/assets/players_list.json';
import playersHomepageList from 'app/assets/players_list_homepage.json';
import config from 'app/config/config';
import {
  Player, PlayerHomepage, PlayersHomepageListItem, PlayersListItem,
} from './players.interfaces';

export const generatePlayerUrl = (id: string) => config.cdnUrl.replace(/<sound-name>/g, id);

export const generateInitalPlayersState = (): Player[] => playersList.map(
  (player: PlayersListItem) => ({
    id: player.id,
    url: generatePlayerUrl(player.id),
    volume: player.volume,
    pro: player.pro,
    isLoaded: false,
    isPlaying: false,
    isOscillating: false,
    isShuffling: false,
  }),
);

export const generateInitalPlayersHomepageState = (): PlayerHomepage[] => playersHomepageList.map(
  (player: PlayersHomepageListItem) => ({
    id: player.id,
    url: generatePlayerUrl(player.as || player.id),
    as: player.as,
    volume: player.volume,
    show: player.show,
    disabled: player.disabled,
    pro: false,
    isLoaded: false,
    isPlaying: false,
    isOscillating: false,
    isShuffling: false,
  }),
);
