import React from 'react';
// eslint-disable-next-line
import { Transition } from 'react-transition-group';
import { CloseButton } from '@noisli/app/dist/components/Modal/Modal.styled';
import { RemoveIcon } from '@noisli/icons';
import { Container } from './Modal.styled';

const duration = 300;

const transitionStyles = {
  entered: {
    transition: `opacity ${duration}ms ease-in-out`,
    opacity: 1,
    overflow: 'auto',
  },
  exiting: {
    transition: `opacity ${duration}ms ease-in-out`,
    opacity: 0,
    overflow: 'visible',
  },
  entering: {},
  exited: {},
  unmounted: {},
};

interface Props {
  isOpen?: boolean;
  onCloseClick?: () => void;
}

const Modal: React.FC<Props> = ({
  children,
  onCloseClick,
  isOpen = false,
}) => (
  <Transition
    appear
    unmountOnExit
    in={isOpen}
    timeout={duration}
  >
    {state => (
      <Container
        style={{
          ...transitionStyles[state],
        }}
      >
        { onCloseClick && (
          <CloseButton onClick={onCloseClick} style={{ marginTop: -1 }}><RemoveIcon /></CloseButton>
        )}
        {children}
      </Container>
    )}
  </Transition>
);

export default Modal;
