import { UpdateFavouritesResponseEventData } from '@noisli/common';
import { SocketHandlerWithData } from '../../middleware/SocketMiddleware/Socket.interface';
import { setFavourites } from './index';

// eslint-disable-next-line import/prefer-default-export
export const onUpdateFavourites: SocketHandlerWithData<UpdateFavouritesResponseEventData> = (
  { dispatch },
  data,
) => {
  dispatch(setFavourites(data.favourites));
};
