import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import theme from '@noisli/app/dist/styles/theme';
import GlobalStyle from '@noisli/app/dist/styles/global';
import { ThemeProvider } from 'styled-components';
import axiosInstance from '@noisli/app/dist/Api/AxiosInstance';
import App from './App';
import store from './redux/popupStore';
import ExtensionStyle from './styles/global';

axiosInstance.interceptors.response.use(response => response, (error) => {
  if (error.response.status === 401) {
    if (window) {
      // we need to check if we have user in store (if user is logged in)
      if (store.getState()?.users?.user?.uuid) {
        window.location.reload();
      }
    }
  }
  throw error;
});

store.ready().then(() => {
  ReactDOM.render((
    <ThemeProvider theme={theme}>
      <GlobalStyle />
      <ExtensionStyle />
      <Provider store={store}>
        <App />
      </Provider>
    </ThemeProvider>
  ),
  document.getElementById('root'));
});
