// eslint-disable-next-line import/prefer-default-export
import Push from 'push.js';
import { TimerType } from '@noisli/common';

/**
 * Function that returns formatted string of time
 * @param hours
 * @param minutes
 * @param seconds
 * @returns {string}
 */
export const formatActualTime = (
  hours: number | string,
  minutes: number | string,
  seconds: number | string,
) => `${hours.toString().padStart(2, '0')}:${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;

/**
 * reoarse actual time to trim hours if hours is set to 00
 * @param actualTime
 * @returns {string}
 */
export const formatSessionTime = (actualTime: string) => {
  const [hours, minutes, seconds] = actualTime.split(':');

  let actualTimeText = '';

  if (hours === '00') {
    actualTimeText = `${minutes}:${seconds}`;
  } else {
    actualTimeText = `${hours}:${minutes}:${seconds}`;
  }

  return actualTimeText;
};

/**
 * Function that sends push notification with set text
 * @param timerType
 */
export const sendPushNotification = (timerType: TimerType) => {
  let message = '';

  switch (timerType) {
    case TimerType.Session:
      message = 'Time is up!';
      break;
    case TimerType.ShortBreak:
      message = 'Short Break is up!';
      break;
    case TimerType.LongBreak:
      message = 'Long Break is up!';
      break;

    default:
      break;
  }

  if (!message) {
    return;
  }

  Push.create(message, {
    icon: 'https://about.noisli.com/assets/icons/favicon-192x192.png',
    timeout: 4000,
  });
};

/**
 * Function that determine what title should be generated
 * to display it to user
 * @param timerType
 * @param actualTime
 * @param showBreakNotification
 * @returns {string}
 */
export const setTimerPageTitle = (
  timerType: TimerType, actualTime: string, showBreakNotification = false,
) => {
  let title = '';
  const [hours, minutes, seconds] = actualTime.split(':');
  let actualTimeText = '';

  if (hours === '00' && minutes === '00') {
    actualTimeText = `${seconds}s`;
  } else if (hours === '00') {
    actualTimeText = `${minutes}:${seconds}`;
  } else {
    actualTimeText = `${hours}:${minutes}:${seconds}`;
  }

  switch (timerType) {
    case TimerType.Session:
      title = actualTimeText;
      break;
    case TimerType.ShortBreak:
      if (showBreakNotification) {
        title = 'Short Break?';
        break;
      }
      title = `<${actualTimeText}>`;
      break;
    case TimerType.LongBreak:
      if (showBreakNotification) {
        title = 'Long Break?';
        break;
      }
      title = `<${actualTimeText}>`;
      break;

    default:
      break;
  }

  if (!title) {
    return '';
  }

  return title;
};
