import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getTimer } from '@noisli/app/dist/redux/modules/timer';
import { setHasAskForPermissionInSession, getChromeExtension } from '../../../redux/modules/chromeExtension';
import Header from './Header';

const HeaderContainer = () => {
  const dispatch = useDispatch();
  const { hasAskForPermission } = useSelector(getChromeExtension);
  const { timerConfig } = useSelector(getTimer);

  const checkNotificationPermissions = () => {
    if (hasAskForPermission || !timerConfig.webNotification) {
      return;
    }

    window.chrome.permissions.contains({
      permissions: ['notifications'],
    }, (hasPermission) => {
      if (!hasPermission) {
        window.chrome.permissions.request({
          permissions: ['notifications'],
        }, () => {
          dispatch(setHasAskForPermissionInSession());
        });
      }
    });
  };

  return (
    <Header
      onSettingsClick={() => window.open(`${process.env.REACT_APP_PAGE_URL}/timer`, '_blank')}
      onTimerStart={checkNotificationPermissions}
    />
  );
};

export default HeaderContainer;
