import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getUsers, setShowLimitReachedModal } from '@noisli/app/dist/redux/modules/users';
import LimitReachedModal from './LimitReachedModal';

const LimitReachedModalContainer = () => {
  const dispatch = useDispatch();
  const { showLimitReachedModal } = useSelector(getUsers);

  const handleOnCloseClick = () => {
    dispatch(setShowLimitReachedModal({
      showModal: false,
    }));
  };

  const handleUpgradeProClick = () => {
    window.open(`${process.env.REACT_APP_PAGE_URL}/billing/change-plan?plan=Pro&cycle=year`, '_blank');
  };

  return (
    <LimitReachedModal
      isOpen={showLimitReachedModal}
      onCloseClick={handleOnCloseClick}
      onUpgradeClick={handleUpgradeProClick}
    />
  );
};

export default LimitReachedModalContainer;
