import axios from './AxiosInstance';
import {
  GetPlansDto,
  GetPaymentsHistoryDto,
  UpdateBillingCycleDto,
  UpdateBillingPlanDto,
  TeamMember, CheckCouponDto, Subscription, HighRiskTransaction,
} from '@noisli/common';
import { IncomingMessage } from 'http';

/**
 * Static class that have all connection to API regarding Subscriptions module
 */
export default class SubscriptionsApi {
  static apiUrl = `${process.env.REACT_APP_API_URL}/subscriptions`;

  static async getPlans() {
    const response = await axios.get<GetPlansDto>(`${SubscriptionsApi.apiUrl}/plans`, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async getUserSubscription() {
    const response = await axios.get<Subscription>(`${SubscriptionsApi.apiUrl}/user-subscription`, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async getPaymentHistory() {
    const response = await axios.get<GetPaymentsHistoryDto>(`${SubscriptionsApi.apiUrl}/history`, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async updateBillingCycle(body: UpdateBillingCycleDto) {
    const response = await axios.put(`${SubscriptionsApi.apiUrl}/update-cycle`, body, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async updateBillingPlan(body: UpdateBillingPlanDto) {
    const response = await axios.put(`${SubscriptionsApi.apiUrl}/update-plan`, body, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async cancelSubscription() {
    const response = await axios.post(`${SubscriptionsApi.apiUrl}/cancel-subscription`, null, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async getTeamList() {
    const response = await axios.get<{ data: TeamMember[] }>(`${SubscriptionsApi.apiUrl}/list-team`, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async checkCoupon(coupon: string) {
    const response = await axios.post<CheckCouponDto>(`${SubscriptionsApi.apiUrl}/check-coupon`, {
      coupon,
    });
    const { data } = response;

    return data;
  }

  static async getHighRiskTransaction() {
    const response = await axios.get<{ data: HighRiskTransaction}>(`${SubscriptionsApi.apiUrl}/high-risk-transaction`, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async getPublicPlans(req?: IncomingMessage) {
    let realIp;
    const xForwardedFor = req?.headers['x-forwarded-for'];
    if (xForwardedFor) {
      if (typeof xForwardedFor === 'string') {
        const ips = xForwardedFor.split(',');
        const [userIp] = ips;
        const ipChunks = userIp.split('.');
        ipChunks[ipChunks.length - 1] = '0';
        realIp = ipChunks.join('.');
      }
    }
    const response = await axios.get<GetPlansDto>(`${SubscriptionsApi.apiUrl}/plans/public`, {
      params: {
        ip: realIp,
      },
    });
    const { data } = response;

    return data;
  }
}
