import { createSlice } from 'redux-starter-kit';
import state from './players.state';
import {
  setPlayerPlayingReducer,
  setPlayerVolumeReducer,
  setPlayerIsLoadedReducer,
  setMasterPlayerPlayingReducer,
  setMasterPlayerVolumeReducer,
  setMasterPlayerIsMutedReducer,
  stopAllPlayersReducer,
  setOscillatorStateReducer,
  startOscillatorReducer,
  clearOscillatorReducer,
  setPlayerOscillatingReducer,
  updateOscillatorReducer,
  setShufflingStateReducer,
  setShuffledCombosReducer,
  updateShuffleReducer,
  setPlayerShufflingReducer,
  setShufflingTranstionStateReducer,
  setActiveComboReducer,
  renewShuffleReducer,
  setPlayersShufflingReducer,
  startupPlayersForShuffleReducer,
  setPlayersVolumeReducer,
  stopPlayersAfterShuffleReducer,
  setVolumeAfterShuffleReducer,
  destroyShuffleReducer,
  revertPlayersVolumeReducer,
  setShuffleTimeReducer, setUseHomepagePlayersReducer,
} from './players.reducers';

const playersSlice = createSlice({
  slice: 'players',

  initialState: state,

  reducers: {
    setPlayerPlaying: setPlayerPlayingReducer,
    setPlayerVolume: setPlayerVolumeReducer,
    setPlayersVolume: setPlayersVolumeReducer,
    revertPlayersVolume: revertPlayersVolumeReducer,
    setPlayerIsLoaded: setPlayerIsLoadedReducer,
    stopAllPlayers: stopAllPlayersReducer,
    setMasterPlayerPlaying: setMasterPlayerPlayingReducer,
    setMasterPlayerVolume: setMasterPlayerVolumeReducer,
    setMasterPlayerIsMuted: setMasterPlayerIsMutedReducer,

    setOscillatorState: setOscillatorStateReducer,
    setPlayerOscillating: setPlayerOscillatingReducer,
    startOscillator: startOscillatorReducer,
    updateOscillator: updateOscillatorReducer,
    clearOscillator: clearOscillatorReducer,

    setShufflingState: setShufflingStateReducer,
    setShufflingTranstionState: setShufflingTranstionStateReducer,
    setShuffledCombos: setShuffledCombosReducer,
    setPlayerShuffling: setPlayerShufflingReducer,
    updateShuffle: updateShuffleReducer,
    renewShuffle: renewShuffleReducer,
    destroyShuffle: destroyShuffleReducer,

    setPlayersShuffling: setPlayersShufflingReducer,
    startupPlayersForShuffle: startupPlayersForShuffleReducer,
    stopPlayersAfterShuffle: stopPlayersAfterShuffleReducer,
    setVolumeAfterShuffle: setVolumeAfterShuffleReducer,

    setActiveCombo: setActiveComboReducer,
    setShuffleTime: setShuffleTimeReducer,

    setUseHomepagePlayers: setUseHomepagePlayersReducer,
  },
});
// Extract the action creators object and the reducer
const { actions, reducer, selectors } = playersSlice;
// Extract and export each action creator by name
export const {
  setPlayerPlaying,
  setPlayerVolume,
  setPlayersVolume,
  setPlayerIsLoaded,
  revertPlayersVolume,
  stopAllPlayers,
  setMasterPlayerPlaying,
  setMasterPlayerVolume,
  setMasterPlayerIsMuted,

  setOscillatorState,
  setPlayerOscillating,
  startOscillator,
  updateOscillator,
  clearOscillator,

  setShufflingState,
  setShufflingTranstionState,
  setShuffledCombos,
  setPlayerShuffling,
  updateShuffle,
  renewShuffle,
  destroyShuffle,

  setPlayersShuffling,
  startupPlayersForShuffle,
  stopPlayersAfterShuffle,
  setVolumeAfterShuffle,

  setActiveCombo,
  setShuffleTime,

  setUseHomepagePlayers,
} = actions;

export const { getPlayers } = selectors;
// Export the reducer, either as a default or named export
export default reducer;
