import styled, { css } from 'styled-components';

export const Wrapper = styled.div`
  width: 44px;
  height: 44px;
`;

interface ContainerProps {
  disabled?: boolean;
  active?: boolean;
}

export const Container = styled.div<ContainerProps>`
  position: relative;
  width: 44px;
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 13px;
  line-height: 15px;
  font-weight: 600;
  color: ${(props) => props.theme.colors.white};
  border-radius: 50%;
  background-color: ${(props) => props.theme.colors.white15};
  cursor: pointer;
  
  ${(props) => props.disabled && css`
    color: ${props.theme.colors.white50};
    cursor: auto;
  `}
  
  ${(props) => props.active && css`
    background-color: ${props.theme.colors.white30};
  `}
`;

interface IconContainerProps {
  visible?: boolean;
  reverted?: boolean;
}

export const ContentContainer = styled.div<IconContainerProps>`
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  width: 44px;
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: ${(props) => props.theme.transition} opacity;
  
  ${(props) => props.visible && css`
    opacity: 1;
  `}
  
  ${(props) => props.reverted && css`
    opacity: 1;
    
    ${props.visible && css`
      opacity: 0;
    `}
  `}
`;
