import React from 'react';

import beautifulAmbients from './beautiful-ambients.svg';
import creativeThinking from './creative-thinking.svg';
import motivation from './motivation.svg';
import noiseBlocker from './noise-blocker.svg';
import productivity from './productivity.svg';
import random from './random.svg';
import relax from './relax.svg';
import sleep from './sleep.svg';
import studying from './studying.svg';
import writing from './writing.svg';

import { Icons as PlayerIcons } from '../PlayerIcons/index';

const Icons = {
  beautifulAmbients,
  creativeThinking,
  motivation,
  noiseBlocker,
  productivity,
  random,
  relax,
  sleep,
  studying,
  writing,
};

interface Props {
  name: string;
}

const PlaylistIcon: React.FC<Props> = ({ name }) => {
  if (Object.keys(Icons).some((key) => key === name)) {
    const Icon = Icons[(name as keyof typeof Icons)];
    return <Icon />;
  }

  if (Object.keys(PlayerIcons).some((key) => key === name)) {
    const Icon = PlayerIcons[(name as keyof typeof PlayerIcons)];
    return <Icon />;
  }

  return <></>;
};

export default PlaylistIcon;
