import React from 'react';
import { FormikErrors } from 'formik';
import useOnClickOutside from 'use-onclickoutside';
import {
  StyledInput,
  InputWrapper,
  StyledInputProps,
  InputError,
  InputSuccess,
  InputContainer,
  InputBorder,
} from './Input.styled';
import { HintText } from '../InputWithLabel/InputWithLabel.styled';

export interface InputProps extends StyledInputProps {
  value: string | number;
  type?: string;
  placeholder?: string;
  name?: string;
  error?: FormikErrors<string>;
  isTouched?: boolean;
  min?: number;
  max?: number;
  disabled?: boolean;
  readOnly?: boolean;
  success?: string;
  successAbsolute?: boolean;
  errorAbsolute?: boolean;
  hintText?: string;
  innerRef?: React.RefObject<HTMLInputElement>;
  onClick?: (event: React.MouseEvent<HTMLDivElement>) => void;
  onChange?: (event: React.ChangeEvent<HTMLInputElement>) => void;
  onBlur?: (event: React.FocusEvent<HTMLInputElement>) => void;
  onFocus?: (event: React.FocusEvent<HTMLInputElement>) => void;
}

/**
 * Universal, configurable input component that has some build-in functionalities (refer to interface)
 * @param big
 * @param error
 * @param isTouched
 * @param success
 * @param innerRef
 * @param onClick
 * @param successAbsolute
 * @param errorAbsolute
 * @param hintText
 * @param props
 * @returns {any}
 * @constructor
 */
const Input = ({
  big, error, isTouched, success, innerRef,
  onClick, successAbsolute, errorAbsolute, hintText, ...props
}: InputProps) => {
  const hasError = isTouched && !!error;
  const hasSuccess = !!success;

  const input = React.useRef<HTMLInputElement>(null);

  useOnClickOutside(input, () => {
    if (input.current) {
      input.current.blur();
    }
  });

  return (
    <InputWrapper onClick={onClick}>
      <InputContainer>
        <StyledInput
          big={big}
          autoComplete="off"
          ref={(el) => {
            if (innerRef) {
              // @ts-ignore
              // eslint-disable-next-line no-param-reassign
              innerRef.current = el;
            }
            // @ts-ignore
            input.current = el;
          }}
          hasError={hasError}
          hasSuccess={hasSuccess}
          {...props}
        />
        <InputBorder
          big={big}
          hasError={hasError}
          hasSuccess={hasSuccess}
        />
      </InputContainer>
      { hasError && (
        <InputError errorAbsolute={errorAbsolute}>
          {error}
        </InputError>
      )}
      { hasSuccess && (
        <InputSuccess successAbsolute={successAbsolute}>
          {success}
        </InputSuccess>
      )}
      {!hasError && hintText && <HintText>{hintText}</HintText>}
    </InputWrapper>
  );
};

export default Input;
