import React from 'react';
import { CancelIcon } from '@noisli/icons';
import useOnClickOutside from 'use-onclickoutside';
import { Container, ContentContainer, Wrapper } from './TimerSessionCounter.styled';
import ArrowTooltip from '../../Tooltip/Tooltip';
import { GridWrapper } from '../../Grid/Grid.styled';
import { ActionsWrapper, CancelAction, DeleteAction } from '../../Tooltip/Tooltip.styled';
import useEnterLeaveEventListeners from '../../../utils/hooks/useEnterLeaveEventListeners';

const baseFreeUserMessage = (
  <>
    Work in sessions
    <br />
    {'is a '}
    <strong>Pro</strong>
    {' feature.'}
    <br />
    Upgrade to enjoy it.
  </>
);

const baseWorkInSessionMessage = (
  <>
    Work in Sessions is disabled.
    <br />
    Activate it in your Timer Settings.
  </>
);

interface Props {
  isUserFree: boolean;
  isActive: boolean;
  isDisabled: boolean;
  sessionCount: number;
  sessionsCompleted: number;
  onReset: () => void;
  freeUserMessage?: React.ReactNode;
  workInSessionMessage?: React.ReactNode;
}

/**
 * Timer left circle "button" with session counter
 * Its state is based on user account type, if sessions are enabled
 * and sessions count
 * It also have tooltip to reset session count
 * @param isUserFree
 * @param sessionCount
 * @param sessionsCompleted
 * @param isActive
 * @param isDisabled
 * @param onReset
 * @param freeUserMessage
 * @param workInSessionMessage
 * @returns {any}
 * @constructor
 */
const TimerSessionCounter: React.FC<Props> = ({
  isUserFree, sessionCount, sessionsCompleted, isActive, isDisabled, onReset,
  freeUserMessage = baseFreeUserMessage,
  workInSessionMessage = baseWorkInSessionMessage,
}) => {
  const [isHover, setIsHover] = React.useState(false);
  const [isTooltipOpen, setIsTooltipOpen] = React.useState(false);
  const sessionCounterContainer = React.useRef<HTMLDivElement>(null);

  useEnterLeaveEventListeners(sessionCounterContainer,
    () => setIsTooltipOpen(true),
    () => setIsTooltipOpen(false),
    () => setIsTooltipOpen(false));

  if (isDisabled) {
    return (
      <Wrapper
        ref={sessionCounterContainer}
        onTouchEnd={() => setIsTooltipOpen(true)}
      >
        <ArrowTooltip
          white
          open={isTooltipOpen}
          placement="bottom"
          disablePortal
          title={isUserFree ? freeUserMessage : workInSessionMessage}
        >
          <Container disabled>{`0/${sessionCount}`}</Container>
        </ArrowTooltip>
      </Wrapper>
    );
  }

  return (
    <Wrapper ref={sessionCounterContainer}>
      <ArrowTooltip
        white
        open={isTooltipOpen}
        placement="bottom"
        disablePortal
        title={(
          <>
            <GridWrapper halfSpace>
              Do you want to reset
              <br />
              this session count?
            </GridWrapper>
            <ActionsWrapper>
              <CancelAction onClick={() => setIsTooltipOpen(false)}>No</CancelAction>
              <DeleteAction onClick={() => {
                setIsTooltipOpen(false);
                onReset();
              }}
              >
                Yes, reset
              </DeleteAction>
            </ActionsWrapper>
          </>
        )}
      >
        <Container
          active={isActive}
          onMouseEnter={() => setIsHover(true)}
          onMouseLeave={() => setIsHover(false)}
        >
          <ContentContainer reverted visible={isHover}>
            {`${sessionsCompleted}/${sessionCount}`}
          </ContentContainer>
          <ContentContainer visible={isHover} onClick={() => isHover && setIsTooltipOpen(true)}>
            <CancelIcon />
          </ContentContainer>
        </Container>
      </ArrowTooltip>
    </Wrapper>
  );
};

export default TimerSessionCounter;
