/**
 * iOS web audio unlocker
 * source: https://github.com/pavle-goloskokovic/web-audio-touch-unlock/
 * @param context
 */
export default function webAudioTouchUnlock(context: AudioContext) {
  return new Promise <boolean>((resolve, reject) => {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any,max-len
    if (!context || !(context instanceof ((window as any).AudioContext || (window as any).webkitAudioContext))) {
      reject(new Error('WebAudioTouchUnlock: You need to pass an instance of AudioContext to this method call'));
      return;
    }

    if (context.state === 'suspended' && 'ontouchstart' in window) {
      const unlock = () => {
        context.resume().then(() => {
          document.body.removeEventListener('touchstart', unlock);
          document.body.removeEventListener('touchend', unlock);
          document.body.removeEventListener('click', unlock);

          resolve(true);
        },
        (reason) => {
          reject(reason);
        });
      };

      document.body.addEventListener('touchstart', unlock, false);
      document.body.addEventListener('touchend', unlock, false);
      document.body.addEventListener('click', unlock, false);
    } else {
      resolve(false);
    }
  });
}
