import * as React from 'react';
import colors from 'app/styles/_colors';
import { colorCycler } from '../../styles/backgroundColors';

export interface BackgroundShuffleContextProps {
  color: string;
  forceColorUpdate?: () => void;
  changeColor?: (color: string, force?: boolean) => void;
  colorCycler?: ReturnType<typeof colorCycler>;
}

/**
 * React contenxt that holds current background shuffle color and references to provider functions
 * @type {React.Context<BackgroundShuffleContextProps>}
 */
const BackgroundShuffleContext = React.createContext<BackgroundShuffleContextProps>({
  color: colors.primary,
});

export default BackgroundShuffleContext;
