import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { SettingsIcon } from '@noisli/icons';
import PlayerMasterVolumeController
  from '@noisli/app/dist/components/_Core/PlayerMasterController/PlayerMasterVolumeController/PlayerMasterVolumeController';
import TopMenuShuffleButton from '@noisli/app/dist/components/TopMenu/TopMenuShuffleButton';
import TopMenuOscillationButton from '@noisli/app/dist/components/TopMenu/TopMenuOscillationButton';
import { AccountType } from '@noisli/common';
import { setOscillationThunk } from '@noisli/app/dist/redux/modules/players/players.thunks';
import { getPlayers, setShufflingState } from '@noisli/app/dist/redux/modules/players';
import { getUsers } from '@noisli/app/dist/redux/modules/users';
import { ChromeExtensionPage } from '../../../redux/modules/chromeExtension/chromeExtension.interfaces';
import { getChromeExtension, setChromeExtensionPage } from '../../../redux/modules/chromeExtension';
import {
  LeftSection, NavigationButton, RightSection, StyledFooter, Wrapper, SettingsButton, ButtonWrapper,
} from './Footer.styled';
import { MessageText } from '../Header/Header.styled';

const oscillationFreeUserMessage = (
  <MessageText>
    <a
      href={`${process.env.REACT_APP_PAGE_URL}/billing/change-plan?plan=Pro&cycle=year`}
      target="_blank"
      rel="noreferrer noopener"
    >
      Upgrade
    </a>
    {' to '}
    <strong>Pro</strong>
    <br />
    to enjoy Oscillation.
  </MessageText>
);

const shuffleFreeUserMessage = (
  <MessageText>
    <a
      href={`${process.env.REACT_APP_PAGE_URL}/billing/change-plan?plan=Pro&cycle=year`}
      target="_blank"
      rel="noreferrer noopener"
    >
      Upgrade
    </a>
    {' to '}
    <strong>Pro</strong>
    <br />
    to enjoy Shuffle.
  </MessageText>
);

const Footer = () => {
  const dispatch = useDispatch();
  const { page } = useSelector(getChromeExtension);
  const { user } = useSelector(getUsers);
  const { masterController } = useSelector(getPlayers);
  const [isVolumeControllerFocused, setIsVolumeControllerFocused] = React.useState<boolean>(false);
  const [isSettingsFocused, setIsSettingsFocused] = React.useState<boolean>(false);

  const navigateToPage = (pageToNavigate: ChromeExtensionPage) => {
    dispatch(setChromeExtensionPage({
      page: pageToNavigate,
    }));
  };

  const handleOscillationClick = () => {
    if (user.accountType === AccountType.FREE) {
      return;
    }
    dispatch(setOscillationThunk({
      isOscillating: !masterController.isOscillating,
    }));
  };

  const handleShuffleClick = () => {
    if (user.accountType === AccountType.FREE) {
      return;
    }
    dispatch(setShufflingState({
      isShuffling: !masterController.isShuffling,
    }));
  };

  return (
    <StyledFooter onMouseLeave={() => setIsSettingsFocused(false)}>
      <Wrapper>
        <LeftSection>
          <SettingsButton
            onMouseEnter={() => setIsSettingsFocused(true)}
            onClick={() => window.open(`${process.env.REACT_APP_PAGE_URL}/account`, '_blank')}
          >
            <SettingsIcon />
          </SettingsButton>
        </LeftSection>
        {!isVolumeControllerFocused && (
          <>
            { isSettingsFocused ? (
              <ButtonWrapper>
                <TopMenuOscillationButton
                  toTop
                  onClick={handleOscillationClick}
                  freeUserMessage={oscillationFreeUserMessage}
                />
                <TopMenuShuffleButton
                  toTop
                  onClick={handleShuffleClick}
                  freeUserMessage={shuffleFreeUserMessage}
                />
              </ButtonWrapper>
            ) : (
              <ButtonWrapper>
                <NavigationButton
                  active={page === ChromeExtensionPage.PLAYLISTS}
                  onClick={() => navigateToPage(ChromeExtensionPage.PLAYLISTS)}
                >
                  Playlists
                </NavigationButton>
                <NavigationButton
                  active={page === ChromeExtensionPage.FAVORITES}
                  onClick={() => navigateToPage(ChromeExtensionPage.FAVORITES)}
                >
                  Favorites
                </NavigationButton>
              </ButtonWrapper>
            )}
          </>
        )}
        <RightSection>
          <PlayerMasterVolumeController
            isChromeExtension
            setIsVolumeControllerFocused={setIsVolumeControllerFocused}
          />
        </RightSection>
      </Wrapper>
    </StyledFooter>
  );
};

export default Footer;
