import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useRouter } from 'next/router';
import { getTimer } from 'app/redux/modules/timer';
import { getPlayers, stopAllPlayers } from 'app/redux/modules/players';
import { Wrapper } from './Timer.styled';
import TimerSessionCounter from './TimerSessionCounter/TimerSessionCounter';
import Timer from './Timer';
import TimerControl from './TimerControl/TimerControl';
import {
  pauseTimerThunk,
  resumeTimerThunk,
  stopTimerThunk,
  setTimerComboPlayingThunk,
  startTimerThunk, dismissSessionThunk, getSessionCountForDayThunk, updateTimerConfigThunk, saveReplayCount,
} from '../../redux/modules/timer/timer.thunks';
import { getIsUserFree } from '../../redux/modules/users/users.selectors';
import { setMasterPlayerIsMutedThunk } from '../../redux/modules/players/players.thunks';
import { TimerType } from '@noisli/common';

export interface TimerContainerProps {
  onSettingsClick?: () => void;
  onTimerStart?: () => void;
  freeUserMessage?: React.ReactNode;
  workInSessionMessage?: React.ReactNode;
}

const FREE_USER_REPLAY_LIMIT = 10;

/**
 * Main timer component that links all timer parts into one and provide them with props
 * It also links redux with timer components
 * Exposed props are needed for usage inside chrome extension as extension provides different behaviour
 * There is large amount of computed properties that are used to perfectly describe state of timer
 * As state of timer depends on mix of lots of options in config
 * @param onSettingsClick
 * @param freeUserMessage
 * @param workInSessionMessage
 * @param onTimerStart
 * @returns {any}
 * @constructor
 */
const TimerContainer: React.FC<TimerContainerProps> = ({
  onSettingsClick, freeUserMessage, workInSessionMessage, onTimerStart,
}) => {
  const dispatch = useDispatch();
  const router = useRouter();
  const {
    actualTime, isPaused, currentTimerType, isRunning, sessionsCompleted,
    timerConfig, activeCombo, isTerminatingSession,
  } = useSelector(getTimer);
  const { masterController } = useSelector(getPlayers);
  const isUserFree = useSelector(getIsUserFree);

  React.useEffect(() => {
    dispatch(getSessionCountForDayThunk());
  }, []);

  const handleSettingsClick = () => {
    if (onSettingsClick) {
      onSettingsClick();
      return;
    }
    router.push('/timer');
  };

  const handleCancelClick = () => {
    dispatch(stopTimerThunk(true));
    dispatch(stopAllPlayers());
    if (isPaused) {
      setTimeout(() => {
        dispatch(setMasterPlayerIsMutedThunk({
          isMuted: false,
          mutedByTimer: true,
        }));
      }, 300);
    }
  };

  const handleOnPlayClick = () => {
    dispatch(startTimerThunk());
    onTimerStart?.();
  };

  const handleOnPauseClick = () => {
    if (isPaused) {
      dispatch(resumeTimerThunk());
      return;
    }

    if (isTerminatingSession) {
      return;
    }

    dispatch(pauseTimerThunk());
  };

  const handleOnReplayClick = () => {
    dispatch(startTimerThunk());
    dispatch(setTimerComboPlayingThunk());
    dispatch(saveReplayCount());
    onTimerStart?.();
  };

  const handleSkipClick = () => {
    dispatch(stopTimerThunk(false, true));
  };

  const handleSessionReset = () => {
    dispatch(dismissSessionThunk());
  };

  const timerSessionDisabled = isUserFree || !timerConfig.workInSession;
  const isShortBreak = currentTimerType === TimerType.ShortBreak;
  const isLongBreak = currentTimerType === TimerType.LongBreak;
  const isBreak = isShortBreak || isLongBreak;
  const isReplayActive = !isUserFree || (isUserFree && timerConfig.replayCount < FREE_USER_REPLAY_LIMIT);
  const isReplay = timerConfig.wasTimerRun && !!activeCombo
    && !masterController.isPlaying && !isBreak && isReplayActive;
  const isTimerSessionCounterActive = (isPaused || isRunning) && timerConfig.workInSession
    && !isBreak;

  return (
    <Wrapper>
      <TimerSessionCounter
        isUserFree={isUserFree}
        isDisabled={timerSessionDisabled}
        isActive={isTimerSessionCounterActive}
        sessionsCompleted={sessionsCompleted}
        sessionCount={timerConfig.sessionsCount}
        freeUserMessage={freeUserMessage}
        workInSessionMessage={workInSessionMessage}
        onReset={handleSessionReset}
      />
      <Timer
        workInSession={timerConfig.workInSession}
        actualTime={actualTime}
        isRunning={isRunning}
        currentTimerType={currentTimerType}
        sessionTime={timerConfig.time}
        shortBreakTime={timerConfig.shortBreak}
        longBreakTime={timerConfig.longBreak}
        onSettingsClick={handleSettingsClick}
        onCancelClick={handleCancelClick}
        onSkipClick={handleSkipClick}
      />
      <TimerControl
        isActive={isRunning}
        isPaused={isPaused}
        isPlaying={isRunning && !isPaused}
        isReplay={isReplay}
        onPlayClick={handleOnPlayClick}
        onPauseClick={handleOnPauseClick}
        onReplayClick={handleOnReplayClick}
      />
    </Wrapper>
  );
};

export default TimerContainer;
