/* eslint-disable @typescript-eslint/camelcase */
import React from 'react';
import { AccountType } from '@noisli/common';
import { useSelector } from 'react-redux';
import { getUsers } from '../../redux/modules/users';

export const GA_TRACKING_ID = process.env.REACT_APP_GA_TRACKING_CODE;

// https://developers.google.com/analytics/devguides/collection/gtagjs/pages
export const pageview = (url: string) => {
  (window as any).gtag?.('config', GA_TRACKING_ID, {
    // eslint-disable-next-line @typescript-eslint/camelcase
    page_path: url,
  }, { anonymize_ip: true });
};

interface EventProps {
  action: string;
  event_category: string;
  event_label?: string;
}

// https://developers.google.com/analytics/devguides/collection/gtagjs/events
export const event = ({
  action, ...props
}: EventProps) => {
  (window as any).gtag?.('event', action, props);
};

type UseGoogleAnalyticsProps = {
  action: string;
  event_category: string;
  accountType?: AccountType;
}

/**
 * Custom hook that handles google analytics report
 * It uses active user account type to define actions of event
 */
export const useGoogleAnalytics = () => {
  const { user } = useSelector(getUsers);

  React.useEffect(() => {
    const event_category = 'Pageview';
    let actionName;
    let event_label;

    switch (user.accountType) {
      case AccountType.FREE:
        event_label = 'Free';
        actionName = 'In';
        break;
      case AccountType.BUSINESS_MEMBER:
      case AccountType.BUSINESS_ADMIN:
        event_label = 'Business';
        actionName = 'In';
        break;
      case AccountType.PRO:
        event_label = 'Pro';
        actionName = 'In';
        break;
      default:
        actionName = 'Out';
        break;
    }

    event({
      action: actionName,
      event_category,
      event_label,
    });
  }, []);
};
