/* eslint-disable no-param-reassign */
import {PayloadAction} from 'redux-starter-kit';
import {TimerType} from '@noisli/common';
import {
  IsTerminatingSessionPayload,
  SetActiveTimerComboPayload,
  SetActualTimePayload,
  SetAllSessionsCompletedPayload,
  SetCurrentTimerTypePayload,
  SetSessionsCompletedPayload,
  SetSessionsForDayPayload,
  SetTimerConfigPayload,
  SetTimerIsActivePayload,
  SetTimerTitlePayload,
  StopTimerPayload,
  TimerState,
  UpdateTimerConfigPayload,
} from './timer.interfaces';
import {formatActualTime} from './timer.helpers';

export const startTimerReducer = (
  state: TimerState,
) => {
  state.isRunning = true;
};

export const pauseTimerReducer = (
  state: TimerState,
) => {
  state.isPaused = true;
};

export const resumeTimerReducer = (
  state: TimerState,
) => {
  state.isPaused = false;
};

export const stopTimerReducer = (
  state: TimerState,
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  action: PayloadAction<StopTimerPayload>,
) => {
  state.isRunning = false;
  state.isShortBreak = false;
  state.isLongBreak = false;
  state.isPaused = false;
};

export const setActualTimeReducer = (
  state: TimerState, action: PayloadAction<SetActualTimePayload>,
) => {
  state.actualTime = action.payload.actualTime;
};

export const setCurrentTimerTypeReducer = (
  state: TimerState, action: PayloadAction<SetCurrentTimerTypePayload>,
) => {
  state.currentTimerType = action.payload.currentTimerType;
};

/**
 * Reducer that set timer config after first time the config is loaded
 * @param state
 * @param action
 */
export const setTimerConfigReducer = (
  state: TimerState, action: PayloadAction<SetTimerConfigPayload>,
) => {
  const { timerConfig } = action.payload;

  if (state.configLoaded) {
    state.timerConfig = timerConfig;
    return;
  }

  state.timerConfig = timerConfig;

  // if timer config have saved session
  if (timerConfig.savedSessionState) {
    // check if saved session count is greater then user set sessions count
    if (timerConfig.savedSessionState.sessionCount > timerConfig.sessionsCount) {
      state.sessionsCompleted = 0;
    //  check if saved session count is equal to user set sessions count
    } else if (timerConfig.savedSessionState.sessionCount === timerConfig.sessionsCount) {
      // depending on saved type, set long break or reset timer
      if (timerConfig.savedSessionState.sessionType === TimerType.LongBreak) {
        state.sessionsCompleted = timerConfig.savedSessionState.sessionCount;
        state.currentTimerType = timerConfig.savedSessionState.sessionType;
      } else {
        state.sessionsCompleted = 0;
      }
    } else {
      // else just set sessions completed to saved sessions count
      state.sessionsCompleted = timerConfig.savedSessionState.sessionCount;
    }
  }

  // if there was saved combo in timer, use it as active combo to replay
  if (timerConfig.savedTimerCombo) {
    state.activeCombo = timerConfig.savedTimerCombo;
  }

  // set actual time depending savedSessionStateon current timer type
  switch (state.currentTimerType) {
    case (TimerType.Session):
      if (timerConfig.time) {
        const { hours, minutes, seconds } = timerConfig.time;
        state.actualTime = formatActualTime(hours, minutes, seconds);
      }
      break;
    case (TimerType.ShortBreak):
      if (timerConfig.shortBreak) {
        const { hours, minutes, seconds } = timerConfig.shortBreak;
        state.actualTime = formatActualTime(hours, minutes, seconds);
      }
      break;
    case (TimerType.LongBreak):
      if (timerConfig.longBreak) {
        const { hours, minutes, seconds } = timerConfig.longBreak;
        state.actualTime = formatActualTime(hours, minutes, seconds);
      }
      break;
    default:
      break;
  }

  state.configLoaded = true;
};

/**
 * Reducer that is similar to {setTimerConfigReducer} but takes into account if timer is running
 * It is used later when appliaction is already loaded
 * @param state
 * @param action
 */
export const updateTimerConfigReducer = (
  state: TimerState, action: PayloadAction<UpdateTimerConfigPayload>,
) => {
  const { isRunning } = state;
  const { timerConfig } = action.payload;

  // This condition checks saved session state from db with session count that is already loaded in state
  if (timerConfig.savedSessionState && timerConfig.savedSessionState.sessionCount !== state.timerConfig.sessionsCount) {
    if (timerConfig.savedSessionState.sessionCount > state.timerConfig.sessionsCount) {
      state.sessionsCompleted = 0;
    } else if (timerConfig.savedSessionState.sessionCount === timerConfig.sessionsCount) {
      if (timerConfig.savedSessionState.sessionType === TimerType.LongBreak) {
        state.sessionsCompleted = timerConfig.savedSessionState.sessionCount;
        state.currentTimerType = timerConfig.savedSessionState.sessionType;
      } else {
        state.sessionsCompleted = 0;
      }
    } else {
      state.sessionsCompleted = timerConfig.savedSessionState.sessionCount;
    }
  }

  // This condition checks sessionCount from new config with saved session count in state
  // we need to adjust the session counter, eg. if session counter is 5 and new session count is 3, we need to reset to 0 etc.
  if (timerConfig.sessionsCount && state.timerConfig.savedSessionState) {
    if (state.timerConfig.savedSessionState.sessionCount > timerConfig.sessionsCount) {
      state.sessionsCompleted = 0;
    } else if (state.timerConfig.savedSessionState.sessionCount === timerConfig.sessionsCount) {
      if (state.timerConfig.savedSessionState.sessionType !== TimerType.LongBreak) {
        state.sessionsCompleted = 0;
        state.currentTimerType = TimerType.Session;
        state.timerConfig.savedSessionState.sessionCount = 0;
        state.timerConfig.savedSessionState.sessionType = TimerType.Session;
      }
    } else {
      state.sessionsCompleted = state.timerConfig.savedSessionState?.sessionCount || 0;
    }
  }

  if (!isRunning) {
    switch (state.currentTimerType) {
      case (TimerType.Session):
        if (timerConfig.time) {
          const { hours, minutes, seconds } = timerConfig.time;
          state.actualTime = formatActualTime(hours, minutes, seconds);
        }
        break;
      case (TimerType.ShortBreak):
        if (timerConfig.shortBreak) {
          const { hours, minutes, seconds } = timerConfig.shortBreak;
          state.actualTime = formatActualTime(hours, minutes, seconds);
        }
        break;
      case (TimerType.LongBreak):
        if (timerConfig.longBreak) {
          const { hours, minutes, seconds } = timerConfig.longBreak;
          state.actualTime = formatActualTime(hours, minutes, seconds);
        }
        break;
      default:
        break;
    }
  }

  state.timerConfig = {
    ...state.timerConfig,
    ...timerConfig,
  };
};

export const reloadTimerConfigReducer = (
  state: TimerState, action: PayloadAction<UpdateTimerConfigPayload>,
) => updateTimerConfigReducer(state, action);

export const setIsTerminatingSessionReducer = (
  state: TimerState, action: PayloadAction<IsTerminatingSessionPayload>,
) => {
  state.isTerminatingSession = action.payload.isTerminating;
};

export const setActiveTimerComboReducer = (
  state: TimerState, action: PayloadAction<SetActiveTimerComboPayload>,
) => {
  state.activeCombo = action.payload.timerCombo;
  state.timerConfig.savedTimerCombo = action.payload.timerCombo;
};

export const setSessionsCompletedReducer = (
  state: TimerState, action: PayloadAction<SetSessionsCompletedPayload>,
) => {
  state.sessionsCompleted = action.payload.sessionsCompleted;
};

export const setSessionsForDayReducer = (
  state: TimerState, action: PayloadAction<SetSessionsForDayPayload>,
) => {
  state.sessionsForDay = action.payload.sessionsForDay;
};

export const setTimerIsActiveReducer = (
  state: TimerState, action: PayloadAction<SetTimerIsActivePayload>,
) => {
  state.isActive = action.payload.isActive;
};

export const setTimerTitleReducer = (
  state: TimerState, action: PayloadAction<SetTimerTitlePayload>,
) => {
  state.timerTitle = action.payload.title;
};

export const setAllSessionsCompletedReducer = (
  state: TimerState, action: PayloadAction<SetAllSessionsCompletedPayload>,
) => {
  state.allSessionsCompleted = action.payload.allSessionsCompleted;
};

export const incrementAllSessionsCompletedReducer = (
  state: TimerState,
) => {
  state.allSessionsCompleted += 1;
};

export const resetSavedSessionStateReducer = (
  state: TimerState,
) => {
  state.timerConfig.savedSessionState = {
    sessionCount: 0,
    sessionType: TimerType.Session,
  };
}
