import {Action, Dispatch, Middleware, MiddlewareAPI,} from 'redux';
import dayjs from 'dayjs';
import {
  AccountType,
  CheckLimitEventData,
  StopPlayingEventData,
  UserSocketEvent as UserLimitEvents,
  UserSocketEvent,
} from '@noisli/common';
import {PayloadAction} from 'redux-starter-kit';
import {EventEmitter} from 'events';
import {closeAllModals} from '../../modules/base';
import {checkLimitReached, resetUser, setShowLimitReachedModal, userAccountTypeMismatch} from '../../modules/users';
import {setMasterPlayerPlaying, stopAllPlayers} from '../../modules/players';
import {unmarkActivePlaylistCombo} from '../../modules/playlists';
import {unmarkActiveFavourite} from '../../modules/favourites';
// eslint-disable-next-line
import {TickWorkerInterface} from '../../../WebWorkers/tick.worker';
import socketHandlers from '../SocketMiddleware/Socket.handlers';
import {SetMasterPlayerPlayingPayload} from '../../modules/players/players.interfaces';
// eslint-disable-next-line
import TickWorker from 'worker-loader?name=static/[hash].worker.js!../../../WebWorkers/tick.worker';
import {RootState} from '../../index';
import UsersLimitConnectApi from '../../../Api/UserLimitConnect.api';

/**
 * Middleware that is tracking the user sound listen limit
 * We are using the timezone offset technique so the user cant easily spoof the time
 * @param isChromeExtension - pass true for proper handling on chrome extension
 * @returns {(middlewareAPI: MiddlewareAPI<Dispatch<any>, RootState>) => (next: Dispatch) => (action: Action) => void}
 */
const createUserLimitMiddleware: (isChromeExtension?: boolean) => Middleware = (isChromeExtension = false) => {
  const userLimitConnection = new UsersLimitConnectApi(isChromeExtension);

  return (
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    middlewareAPI: MiddlewareAPI<Dispatch<any>, RootState>,
  ) => {
    // middleware setup
    const { dispatch, getState } = middlewareAPI;
    let hasCheckedForLimit = false;
    let timestamp = dayjs();
    let tickWorker: TickWorkerInterface | null = null;
    let limitReachedEventListener: EventEmitter | null = null;
    let accountTypeEventListener: EventEmitter | null = null;

    // Function to run if user reached limit
    const terminatePlaying = (type: UserSocketEvent) => {
      // if event is check and we dont check for limit yet
      if (type === UserSocketEvent.CHECK_LIMIT && !hasCheckedForLimit) {
        // Show limit reached modal
        dispatch(closeAllModals());
        dispatch(setShowLimitReachedModal({
          showModal: true,
        }));
      } else if (type !== UserSocketEvent.CHECK_LIMIT) {
        dispatch(closeAllModals());
        dispatch(setShowLimitReachedModal({
          showModal: true,
        }));
      }
      hasCheckedForLimit = true;
      dispatch(stopAllPlayers());

      // stop limit worker
      if (tickWorker) {
        tickWorker.postMessage({
          command: 'stop',
        });
        tickWorker.terminate();
      }

      setTimeout(() => {
        dispatch(unmarkActivePlaylistCombo());
        dispatch(unmarkActiveFavourite());
      }, 1);
    };

    // Set random time to check the limit (60 seconds +- 10 seconds)
    const getRandomTimeoutTime = () => 60 * 1000 + (Math.random() * 20000 - 10000);

    // middleware function block
    return (next: Dispatch) => (action: Action) => {
      const state = getState();
      const { users } = state;
      const { limitReached, user } = users;

      // if user is not logged in, do not track
      if (!Object.keys(user || {}).length) {
        next(action);
        return;
      }

      // if event listener is not binded, bind it now
      if (!limitReachedEventListener) {
        // bind listener for limit reached event
        limitReachedEventListener = userLimitConnection.addEventListener(
          UserSocketEvent.LIMIT_REACHED,
          (type: UserLimitEvents) => {
            socketHandlers[UserSocketEvent.LIMIT_REACHED](middlewareAPI);
            terminatePlaying(type);
          },
        );
      }

      // if event listener is not binded, bind it now
      if (!accountTypeEventListener) {
        // bind listener for limit reached event
        accountTypeEventListener = userLimitConnection.addEventListener(
          'ACCOUNT_TYPE_UPDATE',
          (accountType: AccountType) => {
            const actualState = getState();
            if (actualState.users.user.accountType !== accountType) {
              // window.location.replace('/logout/passthrough');
              window.location.reload();
              dispatch(userAccountTypeMismatch());
            }
          },
        );
      }

      switch (action.type) {
        case (setMasterPlayerPlaying.toString()): {
          const act = (action as PayloadAction<SetMasterPlayerPlayingPayload>);

          // if we are starting to listening sounds
          if (act.payload.isPlaying && !state.players.masterController.isPlaying) {
            //create timestamp
            timestamp = dayjs();
            //send message that we have started playing
            userLimitConnection.userLimitRequest(UserSocketEvent.START_PLAYING, {
              uuid: user.uuid,
              timezoneOffset: -(new Date().getTimezoneOffset() / 60),
            });

            // Run new tick worker that will confirm time listened
            tickWorker = new TickWorker();

            tickWorker.onmessage = () => {
              // get current state of players
              const { players } = getState();
              // if we are not playing any sounds, just terminate the worker
              if (!players.masterController.isPlaying) {
                if (tickWorker) {
                  tickWorker.postMessage({
                    command: 'stop',
                  });
                  tickWorker.terminate();
                }
                return;
              }
              // Send confirmation of time to backend
              userLimitConnection.userLimitRequest(UserSocketEvent.CONFIRM_TIME, {
                uuid: user.uuid,
                timeDiff: dayjs().diff(timestamp, 'minute', true),
                timezoneOffset: -(new Date().getTimezoneOffset() / 60),
              });

              //update timestamp
              timestamp = dayjs();

              //start new count
              tickWorker?.postMessage({
                command: 'start_timeout',
                payload: {
                  timeout: getRandomTimeoutTime(),
                },
              });
            };

            // start first count
            tickWorker.postMessage({
              command: 'start_timeout',
              payload: {
                timeout: getRandomTimeoutTime(),
              },
            });
          }

          // if we are about to stop listening to sounds
          if (!act.payload.isPlaying && state.players.masterController.isPlaying) {
            // send stop playing message to backend (log time between last check and stop)
            userLimitConnection.userLimitRequest<StopPlayingEventData>(UserSocketEvent.STOP_PLAYING, {
              uuid: user.uuid,
              timeDiff: dayjs().diff(timestamp, 'minute', true),
              timezoneOffset: -(new Date().getTimezoneOffset() / 60),
            });

            // stop tick worker
            if (tickWorker) {
              tickWorker.postMessage({
                command: 'stop',
              });
              tickWorker.terminate();
            }
          }

          next(action);
          if (limitReached) {
            terminatePlaying(UserSocketEvent.LIMIT_REACHED);
          }
          break;
        }

        case (checkLimitReached.toString()): {
          // if check limit is forced in app, just ask server for limit
          userLimitConnection.userLimitRequest<CheckLimitEventData>(UserSocketEvent.CHECK_LIMIT, {
            uuid: user.uuid,
            timezoneOffset: -(new Date().getTimezoneOffset() / 60),
          });

          next(action);
          break;
        }

        case (stopAllPlayers.toString()): {
          if (!state.players.masterController.isPlaying) {
            next(action);
            break;
          }

          // if stop all players was requested by user, log time to server
          userLimitConnection.userLimitRequest<StopPlayingEventData>(UserSocketEvent.STOP_PLAYING, {
            uuid: user.uuid,
            timeDiff: dayjs().diff(timestamp, 'minute', true),
            timezoneOffset: -(new Date().getTimezoneOffset() / 60),
          });

          if (tickWorker) {
            tickWorker.postMessage({
              command: 'stop',
            });
            tickWorker.terminate();
          }

          next(action);
          break;
        }

        case (resetUser.toString()): {
          // if user is logging out, remove listener
          limitReachedEventListener?.removeAllListeners(UserSocketEvent.LIMIT_REACHED);
          limitReachedEventListener = null;

          next(action);
          break;
        }

        default:
          next(action);
          break;
      }
    };
  };
};

export default createUserLimitMiddleware;
