import styled, { css } from 'styled-components';
import { SolidButton, GhostButton, SolidButtonProps } from 'app/components/Button/Button.styled';
import rwd, { Breakpoint } from '../../../styles/rwd';

interface ContainerProps {
  account?: boolean;
  home?: boolean;
  textEditor?: boolean;
  isDistractionFreeMode?: boolean;
}

export const Container = styled.div<ContainerProps>`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: ${(props) => `${props.theme.header.height}px`};
  padding: 0 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: ${(props) => props.theme.colors.primary};
  box-shadow: ${(props) => props.theme.header.shadow};
  z-index: ${(props) => props.theme.zIndex.header};
  opacity: 1;
  transition: ${(props) => props.theme.transition} opacity;
  
  ${(props) => props.account && css`
    background-color: ${props.theme.colors.white};
  `}
  
  ${(props) => props.home && css`
    height: ${props.theme.header.home.height}px;
  `}
  
  ${(props) => props.isDistractionFreeMode && css`
    opacity: 0;
  `}
`;

export const LeftSection = styled.div``;

export const CenterSection = styled.div`
  position: absolute;
  top: 16px;
  left: 50%;
  transform: translateX(-50%);
`;

interface RightSectionProps {
  player?: boolean;
}

export const RightSection = styled.div<RightSectionProps>`
  display: flex;
  margin: 0 -6px;
  align-items: center;
  
  ${(props) => props.player && css`
    margin-top: 2px;
  `}
`;

interface HeaderIconContainerProps {
  color?: string;
  hoverColor?: string;
}

export const HeaderIconContainer = styled.div<HeaderIconContainerProps>`
  font-size: 18px;
  margin: 0 6px;
  color: ${(props) => props.color || props.theme.colors.black};
  cursor: pointer;
  width: 22px;
  height: 22px;
  
  ${rwd[Breakpoint.MEDIUM]`
    width: 18px;
    height: 18px;
  `}

  svg {
    color: ${(props) => props.color || props.theme.colors.black};
    transition: ${(props) => props.theme.transition} color;
    
    ${rwd[Breakpoint.MEDIUM]`
      width: 18px;
      height: 18px;
    `}
  }

  &:hover {
    svg {
      color: ${(props) => props.hoverColor || props.theme.colors.black};
    }
  }
`;

export const HeaderUpgradeButton = styled(SolidButton)`
  padding: 10px 20px;
  line-height: 16px;
  margin-right: 24px;
  
  &:hover {
    box-shadow: 0 2px 4px 0 ${(props) => props.theme.colors.black05};
  }
`;

interface HeaderCloseButtonProps {
  textEditor?: boolean;
}

export const HeaderCloseButton = styled(GhostButton)<HeaderCloseButtonProps>`
  color: ${(props) => props.theme.colors.lightGrey1};
  text-decoration: none;
  padding: 5px 7px 6px 8px;
  margin-right: 6px;
  width: 50px;
  height: 26px;
  flex-shrink: 0;
  background-color: ${(props) => props.theme.colors.black02};
  &:hover {
    background-color: ${(props) => props.theme.colors.black08};
  }
  
  ${(props) => props.textEditor && css`
    margin-left: 4px;
    color: ${props.theme.colors.white};
    background-color: ${props.theme.colors.black04};
    &:hover {
      background-color: ${props.theme.colors.black16};
    }
  `}
`;

export const HeaderLinksContainer = styled.div`
  margin: 0 -12px;
  display: flex;
  align-items: center;
`;

interface HeaderLinkProps {
  homepage?: boolean;
  usePrimary?: boolean;
}

export const HeaderLink = styled.a<HeaderLinkProps>`
  font-size: 14px;
  font-weight: 600;
  color: ${(props) => props.theme.colors.white};
  padding: 12px;
  text-decoration: none;
  white-space: nowrap;
  
  &:hover {
    text-decoration: underline;
  }
  
  ${(props) => props.homepage && css`
    opacity: 1;
    transition: ${props.theme.transition} opacity;
    
    &:hover {
      text-decoration: none;
      opacity: 0.5;
    }
  `}
  
  ${(props) => props.usePrimary && css`
    color: ${props.theme.colors.primary};
    transition: ${props.theme.transition} color;

    &:hover {
      text-decoration: none;
      color: ${props.theme.colors.primaryDark};
    }
  `}
`;

export const HeaderAuthContainer = styled.div`
  margin-right: -5px;
  margin-left: 38px;
  display: flex;
  align-items: center;
  
`;

