import { createSlice } from 'redux-starter-kit';
import { userAccountTypeMismatch } from '@noisli/app/dist/redux/modules/users';
import state from './chromeExtension.state';
import {
  setChromeExtensionPageReducer,
  setOfflineStateReducer,
  setHasAskForPermissionInSessionReducer,
} from './chromeExtension.reducers';

const baseSlice = createSlice({
  slice: 'chromeExtension',

  initialState: state,

  reducers: {
    setChromeExtensionPage: setChromeExtensionPageReducer,
    setOfflineState: setOfflineStateReducer,
    setHasAskForPermissionInSession: setHasAskForPermissionInSessionReducer,
  },

  extraReducers: {
    [userAccountTypeMismatch.toString()]: () => {
      // eslint-disable-next-line no-undef
      chrome.runtime.reload();
    },
  },
});
// Extract the action creators object and the reducer
const { actions, reducer, selectors } = baseSlice;
// Extract and export each action creator by name
export const {
  setChromeExtensionPage,
  setOfflineState,
  setHasAskForPermissionInSession,
} = actions;

export const { getChromeExtension } = selectors;
// Export the reducer, either as a default or named export
export default reducer;
