import styled, { css } from 'styled-components';
import rwd, { Breakpoint } from 'app/styles/rwd';

export const VolumeSliderContainer = styled.div`
  display: none;
  width: 123px;
  opacity: 0;
  margin-right: 19px;
  transition: ${(props) => props.theme.transition} opacity;

  &:hover {
    opacity: 1;
  }
`;

interface VolumeIconContainerProps {
  fullOpacity?: boolean;
  isChromeExtension?: boolean;
}

export const VolumeIconContainer = styled.div<VolumeIconContainerProps>`
  opacity: 0.5;
  cursor: pointer;
  width: 22px;
  height: 22px;
  
  ${rwd[Breakpoint.MEDIUM]`
    width: 18px;
    height: 18px;
  `}
  
  ${(props) => props.fullOpacity && css`
    opacity: 1;
  `}
  
  ${(props) => props.isChromeExtension && css`
    width: 18px;
    height: 18px;
  `}
  
  svg {
    font-size: 18px;
    color: ${(props) => props.theme.colors.white};
  }
`;

interface ContainerProps {
  hasFocus?: boolean;
  isChromeExtension?: boolean;
}

export const Container = styled.div<ContainerProps>`
  display: flex;
  align-items: center;
  margin-right: 6px;
  
  ${(props) => props.isChromeExtension && css`
    margin-right: 0;
  `}

  ${(props) => props.hasFocus && css`
    ${VolumeIconContainer} {
      opacity: 1;
    }
    
    ${rwd[Breakpoint.MEDIUM]`
      ${VolumeSliderContainer} {
        display: flex;
        opacity: 0.5;
  
        &:hover {
          opacity: 1;
        }
      }
    `}
    
    ${props.isChromeExtension && css`
      ${VolumeSliderContainer} {
        display: flex;
        opacity: 0.5;
  
        &:hover {
          opacity: 1;
        }
      }
    `}
  `}
`;
