import axios from 'axios';
import { getOrCreateStore } from '../pages/_app';

const protectedPaths = [
  '/delete-account',
  '/playlists',
  '/favorites',
  '/text-editor',
  '/account',
  '/billing',
  '/billing/change-plan',
  '/billing/edit-billing-cycle',
  '/billing/edit-payment-details',
  '/billing/edit-team-size',
  '/discount',
  '/preferences',
  '/team',
  '/timer',
];

/**
 * Setup custom axios instance with interceptor to handle unauthorized state
 */
const axiosInstance = axios.create();
axiosInstance.interceptors.response.use((response) => response, (error) => {
  if (error.response.status === 401) {
    if (window) {
      const store = getOrCreateStore();
      // we need to check if we have user in store (if user is logged in)
      if (store.getState()?.users?.user?.uuid || protectedPaths.some(path => window.location.pathname.includes(path))) {
        window.location.replace('/login');
      }
    }
  }
  throw error;
});

export default axiosInstance;
