import React, { MouseEvent } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getFavourites } from '@noisli/app/dist/redux/modules/favourites';
import { getTimer } from '@noisli/app/dist/redux/modules/timer';
import { getUsers, setShowLimitReachedModal } from '@noisli/app/dist/redux/modules/users';
import { Favourite } from '@noisli/app/dist/redux/modules/favourites/favourites.interfaces';
import { setFavouritePlayingThunk } from '@noisli/app/dist/redux/modules/favourites/favourites.thunks';
import { createSharedComboThunk } from '@noisli/app/dist/redux/modules/base/base.thunks';
import { Container } from './Favorites.styled';
import EmptyState from './EmptyState/EmptyState';
import FavoritesItem from './FavoritesItem/FavoritesItem';
import FavoritesSaveButton from './FavoritesSaveButton/FavoritesSaveButton';

const FavoritesContainer = () => {
  const dispatch = useDispatch();
  const { favourites } = useSelector(getFavourites);
  const { isTerminatingSession } = useSelector(getTimer);
  const { limitReached } = useSelector(getUsers);

  const handleFavouriteClick = (favourite: Favourite) => {
    if (limitReached) {
      dispatch(setShowLimitReachedModal({
        showModal: true,
      }));
      return;
    }

    if (favourite.isShuffling || isTerminatingSession) {
      return;
    }

    dispatch(setFavouritePlayingThunk({
      id: favourite.id,
    }));
  };

  const handleShareButtonClick = (e: MouseEvent<HTMLDivElement>, favourite: Favourite) => {
    e.stopPropagation();
    dispatch(createSharedComboThunk(favourite));
  };

  return (
    <Container>
      { favourites.length ? (
        <>
          { favourites.map(item => (
            <FavoritesItem
              key={item.uuid}
              favourite={item}
              onClick={() => handleFavouriteClick(item)}
              onShareButtonClick={e => handleShareButtonClick(e, item)}
            />
          ))}
          <FavoritesSaveButton />
        </>
      ) : (
        <EmptyState />
      )}
    </Container>
  );
};

export default FavoritesContainer;
