// eslint-disable-next-line import/no-extraneous-dependencies
import {
  MiddlewareAPI, Dispatch, Action,
} from 'redux';
import { getActiveFavourite } from '@noisli/app/dist/redux/modules/favourites/favourites.selectors';
import { setFavourites } from '@noisli/app/dist/redux/modules/favourites';
import { PayloadAction } from 'redux-starter-kit';
import { Favourite as FavouriteFromApi } from '@noisli/common';
import { stopAllPlayers } from '@noisli/app/dist/redux/modules/players';
import { RootState } from '../../background';

const FavouritesObserverMiddleware = (
  { dispatch, getState }: MiddlewareAPI<Dispatch, RootState>,
) => (next: Dispatch) => (action: Action) => {
  const state = getState();
  const activeFavourite = getActiveFavourite(state);

  switch (action.type) {
    case (setFavourites.toString()): {
      const { payload } = (action as PayloadAction<FavouriteFromApi[]>);
      if (activeFavourite) {
        const foundActiveFavourite = payload.find(
          favourite => favourite.uuid === activeFavourite.id,
        );
        if (!foundActiveFavourite) {
          dispatch(stopAllPlayers());
        }
      }
      next(action);
      break;
    }

    default:
      next(action);
      break;
  }
};

export default FavouritesObserverMiddleware;
