import React from 'react';
import { checkLimitReached } from '@noisli/app/dist/redux/modules/users';
import { useDispatch } from 'react-redux';
import { useGoogleAnalytics, pageview } from '../utils/GoogleAnalytics';
import Layout from '../components/Layout/Layout';
import PlaylistsContainer from '../components/Playlists/Playlists.container';
import ShareModalContainer from '../components/ShareModal/ShareModal.container';
import LimitReachedModalContainer from '../components/LimitReachedModal/LimitReachedModal.container';

const PlaylistsPage: React.FC = () => {
  const dispatch = useDispatch();
  useGoogleAnalytics();

  React.useEffect(() => {
    pageview('/playlists');
    dispatch(checkLimitReached());
  }, []);

  return (
    <Layout>
      <PlaylistsContainer />
      <ShareModalContainer />
      <LimitReachedModalContainer />
    </Layout>
  );
};

export default PlaylistsPage;
