import React from 'react';
import { Transition } from 'react-transition-group';
import { RemoveIcon } from '@noisli/icons';
import Portal from '../Portal/Portal';
import {
  Container, Mask, ModalRoot, CloseButton,
} from './Modal.styled';

const duration = 300;

const transitionStyles = {
  entered: {
    transition: `opacity ${duration}ms ease-in-out`,
    opacity: 1,
    overflow: 'auto',
  },
  exiting: {
    transition: `opacity ${duration}ms ease-in-out`,
    opacity: 0,
    overflow: 'visible',
  },
  entering: {},
  exited: {},
  unmounted: {},
};

interface Props {
  isOpen?: boolean;
  onCloseClick?: () => void;
}

/**
 * Main modal component. Use it for building custom modals
 * @param children
 * @param onCloseClick
 * @param isOpen
 * @returns {any}
 * @constructor
 */
const Modal: React.FC<Props> = ({
  children,
  onCloseClick,
  isOpen = false,
}) => {
  React.useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'auto';
    }

    return () => {
      document.body.style.overflow = '';
    };
  }, [isOpen]);

  return (
    <Portal selector="#modal">
      <Transition
        appear
        // mountOnEnter
        unmountOnExit
        in={isOpen}
        timeout={duration}
      >
        {(state) => (
          <ModalRoot
            style={{
              ...transitionStyles[state],
            }}
          >
            <Mask>
              <Container>
                { onCloseClick && (
                  <CloseButton onClick={onCloseClick}><RemoveIcon /></CloseButton>
                )}
                {children}
              </Container>
            </Mask>
          </ModalRoot>
        )}
      </Transition>
    </Portal>
  );
};

export default Modal;
