import axios from 'axios';
import { UserIncludes, UserRO } from '@noisli/common';
import UsersApi from '@noisli/app/dist/Api/Users.api';
import { setUserDataWithIncludesThunk } from '@noisli/app/dist/redux/modules/users/users.thunks';
import { resetUser } from '@noisli/app/dist/redux/modules/users';
import { setPlaylists } from '@noisli/app/dist/redux/modules/playlists';
import { generatePlaylistsState } from '@noisli/app/dist/redux/modules/playlists/playlists.utils';
import { setChromeExtensionPage } from '../redux/modules/chromeExtension';
import { ChromeExtensionPage } from '../redux/modules/chromeExtension/chromeExtension.interfaces';
import store, { RootState } from '../redux/background';
import { event } from '../utils/GoogleAnalytics';

export const setAuthHeader = (jwt?: string) => {
  const token = jwt;

  if (token) {
    axios.defaults.headers.Authorization = `Bearer ${token}`;
  }
};

export const resetAuthHeader = () => {
  delete axios.defaults.headers.Authorization;
};

export const auth = async (
  includes: UserIncludes[],
): Promise<UserRO | undefined> => UsersApi.checkUser(undefined, includes);

export const logout = async () => {
  resetAuthHeader();
  store.dispatch(resetUser());
  store.dispatch(setPlaylists(generatePlaylistsState()));
  store.dispatch(setChromeExtensionPage({
    page: ChromeExtensionPage.ONBOARDING,
  }));
};

export const performAuthCheck = async () => {
  window.chrome.cookies.get({ url: process.env.REACT_APP_PAGE_URL as string, name: 'jwt' }, async (cookie) => {
    if (cookie) {
      setAuthHeader(cookie.value);
      let state = store.getState();
      try {
        const userRO = await auth([
          'favourites',
          'timer',
          'userDetails',
        ]);
        if (!state.users.user.uuid && userRO?.user.uuid) {
          event({
            eventAction: 'Extension',
            eventCategory: 'Log in',
          });
        }
        if (state.users.user.uuid && state.users.user.uuid !== userRO?.user.uuid) {
          store.dispatch(resetUser());
          store.dispatch(setPlaylists(generatePlaylistsState()));
        }
        store.dispatch(setUserDataWithIncludesThunk(userRO));
        state = store.getState();
        if ((state as RootState).chromeExtension.page === ChromeExtensionPage.ONBOARDING) {
          store.dispatch(setChromeExtensionPage({
            page: ChromeExtensionPage.PLAYLISTS,
          }));
        }
      } catch (e) {
        if ((state as RootState).chromeExtension.isOffline) {
          return;
        }
        logout();
      }
    } else {
      logout();
    }
  });
};


axios.interceptors.response.use(response => response, (error) => {
  if (error?.response?.status === 401) {
    logout();
  }
  return Promise.reject(error);
});
