import {
  AccountType, SharedComboFavourite, SharedComboPlaylist, ShareType,
} from '@noisli/common';
// eslint-disable-next-line import/no-extraneous-dependencies
import { AnyAction } from 'redux';
// eslint-disable-next-line import/no-extraneous-dependencies
import { ThunkAction } from 'redux-thunk';
import { RootState } from '../../index';
import { getActiveFavourite } from '../favourites/favourites.selectors';
import { getActivePlaylist, getActivePlaylistCombo } from '../playlists/playlists.selectors';
import ShareApi from '../../../Api/Share.api';
import {
  setSharedCombo,
  setSharedComboIsLoading,
  setShowSoundsLockedModal,
  setShowShareModal,
  setShowSharePlayModal,
} from '.';
import { ExtendedSharedCombo } from './base.interfaces';
import { Favourite } from '../favourites/favourites.interfaces';
import { setPlayerVolume, stopAllPlayers } from '../players';
import { setPlayerPlayingThunk } from '../players/players.thunks';
import { markActiveFavourite } from '../favourites';
import { markActivePlaylistCombo, setPlaylistsSliderPosition } from '../playlists';
import { Playlist } from '../playlists/playlists.interfaces';

/**
 * Thunk that creates shared combo taking into account what is currently playing
 * 3 types: playlist, favourite or custom combo
 * If param is passed, it takes favoruite or playlist that is passed and create shared combo based on it
 *
 * @param favourite
 * @param playlist
 * @returns {(dispatch, getState) => Promise<void>}
 */
export const createSharedComboThunk = (
  favourite?: Favourite, playlist?: Playlist,
): ThunkAction<Promise<void>, RootState, null, AnyAction> => async (dispatch, getState) => {
  const state = getState();
  const activeFavourite = getActiveFavourite(state);
  const activePlaylist = getActivePlaylist(state);

  let sharedCombo: ExtendedSharedCombo = {} as unknown as ExtendedSharedCombo;
  dispatch(setShowShareModal({
    showShareModal: true,
  }));
  dispatch(setSharedComboIsLoading({
    isLoading: true,
  }));

  try {
    if (favourite) {
      sharedCombo = await ShareApi.create({
        type: ShareType.FAVOURITE,
        content: {
          favourite: {
            id: favourite.id,
            name: favourite.name,
          },
          items: favourite.items,
        },
      });
    } else if (playlist) {
      const activePlaylistCombo = playlist.items[0];

      if (!activePlaylistCombo) return;

      sharedCombo = await ShareApi.create({
        type: ShareType.PLAYLIST,
        content: {
          items: activePlaylistCombo.items,
          playlist: {
            name: playlist.name,
            id: playlist.id,
            comboId: activePlaylistCombo.id,
          },
        },
      });
    } else if (activeFavourite) {
      sharedCombo = await ShareApi.create({
        type: ShareType.FAVOURITE,
        content: {
          favourite: {
            id: activeFavourite.id,
            name: activeFavourite.name,
          },
          items: activeFavourite.items,
        },
      });
    } else if (activePlaylist) {
      const activePlaylistCombo = getActivePlaylistCombo(state);

      if (activePlaylistCombo) {
        sharedCombo = await ShareApi.create({
          type: ShareType.PLAYLIST,
          content: {
            items: activePlaylistCombo.items,
            playlist: {
              name: activePlaylist.name,
              id: activePlaylist.id,
              comboId: activePlaylistCombo.id,
            },
          },
        });
      }
    } else {
      sharedCombo = await ShareApi.create({
        type: ShareType.CUSTOM_COMBO,
        content: {
          items: state.players.players.filter((player) => player.isPlaying).map((player) => ({
            id: player.id,
            volume: player.volume,
          })),
        },
      });
    }

    dispatch(setSharedCombo({
      sharedCombo: {
        ...sharedCombo,
        isPlaying: true,
      },
    }));
  } catch (e) {
    // eslint-disable-next-line
    return;
  } finally {
    dispatch(setSharedComboIsLoading({
      isLoading: false,
    }));
  }
};

/**
 * Thunk that sets loaded shared combo in play
 * and turn all things corelated to type of combo to play
 * @returns {(dispatch, getState) => void}
 */
export const setSharedComboPlayingThunk = (

): ThunkAction<void, RootState, null, AnyAction> => (dispatch, getState) => {
  const {
    base, playlists, users, players,
  } = getState();
  const sitePlayers = players.players;
  const { sharedCombo } = base;

  if (!sharedCombo || sharedCombo.isPlaying) {
    return;
  }

  if (sharedCombo.type === ShareType.PLAYLIST) {
    const sharedPlaylist = (sharedCombo.content.playlist as SharedComboPlaylist);
    const playlistToPlayIndex = playlists.playlists.findIndex(
      (playlist) => playlist.id === sharedPlaylist.id,
    );
    // If playlist does not exists
    if (playlistToPlayIndex === -1) {
      return;
    }
    const playlistToPlay = playlists.playlists[playlistToPlayIndex];
    const playlistComboToPlay = playlistToPlay.items.find(
      (combo) => combo.id === sharedPlaylist.comboId,
    );

    // If playlist or combo in playlist is pro and user is free
    if (playlistToPlay
      && (playlistToPlay.pro || playlistComboToPlay?.pro)
      && users.user.accountType === AccountType.FREE
    ) {
      dispatch(setShowSoundsLockedModal({
        showSoundsLockedModal: true,
      }));
      dispatch(setSharedCombo({
        sharedCombo: undefined,
      }));
      return;
    }

    dispatch(setPlaylistsSliderPosition({
      position: playlistToPlayIndex,
    }));
  }

  dispatch(stopAllPlayers());

  let comboItems = sharedCombo.content.items;

  // Check if free user wants to play pro users
  if (users.user.accountType === AccountType.FREE) {
    let hasProSound = false;

    comboItems = comboItems.filter((item) => {
      const matchPlayer = sitePlayers.find((player) => player.id === item.id);
      if (matchPlayer) {
        if (!hasProSound) {
          hasProSound = !!matchPlayer.pro;
        }
        return !matchPlayer.pro;
      }
      return true;
    });

    if (hasProSound) {
      dispatch(setShowSoundsLockedModal({
        showSoundsLockedModal: true,
      }));
    }
  }

  // run each sound in combo
  comboItems.forEach((item) => {
    dispatch(setPlayerVolume({
      id: item.id,
      volume: item.volume,
    }));
    dispatch(setPlayerPlayingThunk({
      id: item.id,
      isPlaying: true,
    }));
  });

  // Run store changes depending on combo type
  switch (sharedCombo.type) {
    case ShareType.PLAYLIST:
      dispatch(markActivePlaylistCombo({
        playlistId: (sharedCombo.content.playlist as SharedComboPlaylist).id,
        comboId: (sharedCombo.content.playlist as SharedComboPlaylist).comboId,
      }));

      break;

    case ShareType.FAVOURITE:
      dispatch(markActiveFavourite({
        id: (sharedCombo.content.favourite as SharedComboFavourite).id,
      }));
      break;

    default:
      break;
  }

  dispatch(setSharedCombo({
    sharedCombo: {
      ...sharedCombo,
      isPlaying: true,
    },
  }));
};

/**
 * Thunk that runs shared combo modal
 * @returns {(dispatch, getState) => void}
 */
export const setShowSharePlayModalThunk = (

): ThunkAction<void, RootState, null, AnyAction> => (dispatch, getState) => {
  const {
    base, playlists, users,
  } = getState();
  const { sharedCombo } = base;

  if (!sharedCombo || sharedCombo.isPlaying) {
    return;
  }

  if (sharedCombo.type === ShareType.PLAYLIST) {
    const playlistId = (sharedCombo.content.playlist as SharedComboPlaylist).id;
    const playlistToPlay = playlists.playlists.find((playlist) => playlist.id === playlistId);

    if (playlistToPlay && playlistToPlay.pro && users.user.accountType === AccountType.FREE) {
      dispatch(setShowSoundsLockedModal({
        showSoundsLockedModal: true,
      }));
      dispatch(setSharedCombo({
        sharedCombo: undefined,
      }));
      return;
    }
  }

  dispatch(setShowSharePlayModal({
    showSharePlayModal: true,
  }));
};
