/* eslint-disable no-param-reassign */
import { PayloadAction } from 'redux-starter-kit';
import { Favourite as FavouriteFromApi } from '@noisli/common';
import {
  Favourite,
  FavouritesState,
  MarkActiveFavouritePayload,
  FavouriteIdPayload,
  UpdateFavouritePayload, CreateFavouritePayload, DeleteFavouritePayload, SetSharedFavouritePayload,
} from './favourites.interfaces';
import { SetComboShufflePayload } from '../common/shuffle.interfaces';
import { ComboIdPayload } from '../common/combos.interfaces';

export const markFavouriteReducer = (
  state: FavouritesState, action: PayloadAction<ComboIdPayload>,
) => {
  const idx = state.favourites.findIndex((favourite) => favourite.id === action.payload.id);

  if (idx > -1) {
    state.favourites[idx].isPlaying = true;
  }
};

export const unmarkFavouriteReducer = (
  state: FavouritesState, action: PayloadAction<FavouriteIdPayload>,
) => {
  const idx = state.favourites.findIndex((favourite) => favourite.id === action.payload.id);

  if (idx > -1) {
    state.favourites[idx].isPlaying = false;
  }
};

export const unmarkActiveFavouriteReducer = (
  state: FavouritesState,
) => {
  const idx = state.favourites.findIndex((favourite) => favourite.isPlaying);

  if (idx > -1) {
    state.favourites[idx].isPlaying = false;
  }
};

export const markActiveFavouriteReducer = (
  state: FavouritesState, action: PayloadAction<MarkActiveFavouritePayload>,
) => {
  const idx = state.favourites.findIndex((favourite) => favourite.id === action.payload.id);
  if (idx > -1) {
    state.favourites[idx].isPlaying = true;
  }
};

export const setFavouriteIsShufflingReducer = (
  state: FavouritesState, action: PayloadAction<SetComboShufflePayload>,
) => {
  const { isShuffling, comboId } = action.payload;

  state.favourites.forEach((combo) => {
    if (combo.id === comboId) {
      combo.isShuffling = isShuffling;
    }
  });
};

export const setFavouritesReducer = (
  state: FavouritesState, action: PayloadAction<FavouriteFromApi[]>,
) => {
  state.favourites = action.payload.map((favourite) => {
    const newFavourite: Favourite = {
      ...favourite,
      id: favourite.uuid,
      isPlaying: false,
      isShuffling: false,
    };
    const oldFavourite = state.favourites.find((fav) => fav.uuid === favourite.uuid);

    if (oldFavourite) {
      newFavourite.isPlaying = oldFavourite.isPlaying;
      newFavourite.isShuffling = oldFavourite.isShuffling;
    }

    return newFavourite;
  });
};

export const updateFavouriteReducer = (
  state: FavouritesState, action: PayloadAction<UpdateFavouritePayload>,
) => {
  state.favourites.forEach((favourite) => {
    if (favourite.uuid === action.payload.uuid) {
      favourite.name = action.payload.payload.name;
    }
  });
};

export const createFavouriteReducer = (
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  state: FavouritesState, action: PayloadAction<CreateFavouritePayload>,
) => {};

export const deleteFavouriteReducer = (
  state: FavouritesState, action: PayloadAction<DeleteFavouritePayload>,
) => {
  state.favourites = state.favourites.filter((favourite) => favourite.id !== action.payload.uuid);
};

export const setSharedFavouriteReducer = (
  state: FavouritesState, action: PayloadAction<SetSharedFavouritePayload>,
) => {
  state.sharedFavourite = action.payload.favourite;
};

export const setSharedFavouritePlayingReducer = (
  state: FavouritesState,
) => {
  if (state.sharedFavourite) {
    state.sharedFavourite.isPlaying = true;
  }
};

export const resetUserFavouritesReducer = (
  state: FavouritesState,
) => {
  state.favourites = [];
};
