// eslint-disable-next-line import/no-extraneous-dependencies
import { Dispatch, AnyAction } from 'redux';
import { Combo } from 'app/redux/modules/common/combos.interfaces';
import { TimerConfig, TimerTime, TimerType } from '@noisli/common';
import Timer, { TimerUpdateArgs } from './Timer';
import { RootState } from '../../index';

interface TimerManagerCreateArgs {
  dispatch: Dispatch<AnyAction>;
  timerConfig: TimerConfig;
  timerType: TimerType;
  getState: () => RootState;
}

/**
 * Class that is responsible of managing the timer
 * It creates or updates timer when timer is played, paused or replayed
 * It destroy timer if timer is cancelled
 */
export default class TimerManager {
  timer: Timer | null = null;

  activeCombo?: Combo;

  createOrUpdate({
    dispatch,
    timerConfig,
    timerType,
    getState,
  }: TimerManagerCreateArgs) {
    let times: TimerTime = timerConfig.time;

    switch (timerType) {
      case TimerType.Session:
        times = timerConfig.time;
        break;
      case TimerType.LongBreak:
        times = timerConfig.longBreak;
        break;
      case TimerType.ShortBreak:
        times = timerConfig.shortBreak;
        break;
      default:
        break;
    }

    if (!this.timer) {
      this.timer = new Timer({
        dispatch,
        times,
        timerType,
        timerConfig,
        getState,
      });
    } else {
      this.timer.destroy();
      this.timer = new Timer({
        dispatch,
        times,
        timerType,
        timerConfig,
        getState,
      });
    }
  }

  update(args: TimerUpdateArgs) {
    if (this.timer) {
      this.timer.update(args);
    }
  }

  destroy(isCancelled = false) {
    if (this.timer) {
      this.timer.destroy(isCancelled);
      this.timer = null;
    }
  }
}
