import React from 'react';
import { pageview, useGoogleAnalytics } from '../utils/GoogleAnalytics';
import Layout from '../components/Layout/Layout';
import FavoritesContainer from '../components/Favorites/Favorites.container';
import ShareModalContainer from '../components/ShareModal/ShareModal.container';
import LimitReachedModalContainer from '../components/LimitReachedModal/LimitReachedModal.container';

const FavoritesPage: React.FC = () => {
  useGoogleAnalytics();
  React.useEffect(() => {
    pageview('/favorites');
  }, []);
  return (
    <Layout>
      <FavoritesContainer />
      <ShareModalContainer />
      <LimitReachedModalContainer />
    </Layout>
  );
};

export default FavoritesPage;
