import { createSlice } from 'redux-starter-kit';
import state from './users.state';
import {
  setIsLoadingReducer,
  setUserReducer,
  setUserDetailsReducer,
  resetUserReducer,
  completeOnboardingReducer,
  setLimitReachedReducer, setShowLimitReachedModalReducer, checkLimitReachedReducer,
  setPasswordLockReducer,
} from './users.reducers';

const favouritesSlice = createSlice({
  slice: 'users',

  initialState: state,

  reducers: {
    setIsLoading: setIsLoadingReducer,
    setUser: setUserReducer,
    setUserDetails: setUserDetailsReducer,
    resetUser: resetUserReducer,
    completeOnboarding: completeOnboardingReducer,
    setLimitReached: setLimitReachedReducer,
    setShowLimitReachedModal: setShowLimitReachedModalReducer,
    checkLimitReached: checkLimitReachedReducer,
    setPasswordLock: setPasswordLockReducer,

    userAccountTypeMismatch: () => {},
  },
});
// Extract the action creators object and the reducer
const { actions, reducer, selectors } = favouritesSlice;
// Extract and export each action creator by name
export const {
  setIsLoading,
  setUser,
  setUserDetails,
  resetUser,
  completeOnboarding,
  setLimitReached,
  setShowLimitReachedModal,
  checkLimitReached,
  setPasswordLock,
  userAccountTypeMismatch,
} = actions;

export const { getUsers } = selectors;

// Export the reducer, either as a default or named export
export default reducer;
