import { of } from 'rxjs';
import { AxiosError } from 'axios';
import { handleUserUnauthorizedThunk } from '../modules/users/users.thunks';

/**
 * Epic util that handles auth error and runs user logout
 * @param error
 * @returns {Observable<(dispatch: ThunkDispatch<RootState, null, AnyAction>, getState: () => RootState, extraArgument: null) => void>}
 */
const catchAuthError = (error: AxiosError) => {
  if (error.response && error.response.status === 401) {
    return of(handleUserUnauthorizedThunk());
  }
  throw error;
};

export default catchAuthError;
