// eslint-disable-next-line import/no-extraneous-dependencies
import createSelector from 'selectorator';
import { RootState } from 'app/redux';
import { Combo } from 'app/redux/modules/common/combos.interfaces';
import { getPlaylists } from '.';
import { PlaylistsState, Playlist } from './playlists.interfaces';

export const getActivePlaylist = createSelector<RootState, Playlist | undefined>(
  [getPlaylists],
  ({ playlists }: PlaylistsState) => playlists.find((playlist) => playlist.isPlaying),
);

export const getActivePlaylistCombo = createSelector<RootState, Combo | undefined>(
  [getPlaylists],
  ({ playlists }: PlaylistsState) => {
    const activePlaylist = playlists.find((playlist) => playlist.isPlaying);

    if (!activePlaylist) {
      return undefined;
    }

    return activePlaylist.items.find((combo) => combo.isPlaying);
  },
);
