// eslint-disable-next-line import/no-extraneous-dependencies
import {
  Action, AnyAction, Dispatch, Middleware, MiddlewareAPI,
} from 'redux';
import { RootState } from 'app/redux';
import { PayloadAction } from 'redux-starter-kit';
import { getActiveFavourite } from 'app/redux/modules/favourites/favourites.selectors';
import { getActivePlaylist, getActivePlaylistCombo } from 'app/redux/modules/playlists/playlists.selectors';
import { SetShufflePayload } from 'app/redux/modules/common/shuffle.interfaces';
import {
  destroyShuffle,
  renewShuffle,
  setPlayerVolume,
  setShufflingState,
  stopAllPlayers,
  updateShuffle,
} from 'app/redux/modules/players';
import { markActiveFavourite, unmarkActiveFavourite } from 'app/redux/modules/favourites';
import { markActivePlaylistCombo, unmarkActivePlaylistCombo } from 'app/redux/modules/playlists';
import { SetPlayerVolumePayload } from 'app/redux/modules/players/players.interfaces';
import { getPlayerInstance } from 'app/redux/modules/players/players.selectors';
import { AccountType } from '@noisli/common';
import ShuffleManager, { ShuffleType } from './ShuffleManager';
import { setIsTerminatingSession } from '../../modules/timer';
import { IsTerminatingSessionPayload } from '../../modules/timer/timer.interfaces';

/**
 * Middleware that is responsible for handling shuffle actions
 * It is switched off on mobile devices or if user is free
 * It starts or update shuffle for active favourite or playlist only
 * It updates shuffle state when volume of player is changed by oscillator
 * It renew shuffle after successful change to reflect actual state
 * Difference between update and renew is that after update, the time for shuffle to start is not reloaded
 * It terminates gracefully shuffling, is shuffling is deactivated
 * It terminates immediately shuffling if players are cleared or if timer is ending session
 * It destroys oscillators if timer is terminating session
 *
 * Everything is running in switch/case block with visible actions names
 *
 * @returns {({dispatch, getState}: MiddlewareAPI<Dispatch<AnyAction>, RootState>) => (next: Dispatch) => (action: Action) => void}
 */
// eslint-disable-next-line import/prefer-default-export
export const createShuffleMiddleware: () => Middleware = () => {
  const shuffleManager = new ShuffleManager();
  // For development purposes
  // (global as any).NoisliShuffleManager = shuffleManager;

  return (
    { dispatch, getState }: MiddlewareAPI<Dispatch<AnyAction>, RootState>,
  ) => (next: Dispatch) => (action: Action) => {
    const state = getState();

    if (state.base.isMobile || state.users.user.accountType === AccountType.FREE) {
      next(action);
      return;
    }

    const { shuffleConfig, activeCombo } = state.players;
    const { isShuffling, isShufflingTransition } = state.players.masterController;

    const activeFavourite = getActiveFavourite(state);
    const activePlaylist = getActivePlaylist(state);
    const activePlaylistCombo = getActivePlaylistCombo(state);

    switch (action.type) {
      case (setShufflingState.toString()): {
        const { payload } = (action as PayloadAction<SetShufflePayload, string>);

        if (payload.isShuffling) {
          if (activeFavourite) {
            const { favourites } = state.favourites;

            if (favourites.length > 1) {
              shuffleManager.createOrUpdate({
                dispatch,
                shuffleConfig,
                combosList: favourites,
                activeCombo: activeCombo || activeFavourite,
                type: ShuffleType.FAVOURITES,
              });
            }
          } else if (activePlaylist && activePlaylistCombo) {
            if (activePlaylist.items.length > 1) {
              shuffleManager.createOrUpdate({
                dispatch,
                shuffleConfig,
                combosList: activePlaylist.items,
                activeCombo: activeCombo || activePlaylistCombo,
                type: ShuffleType.PLAYLIST,
              });
            }
          }
        } else if (isShufflingTransition) {
          shuffleManager.endTransitionAndDestroy();
        } else {
          shuffleManager.destroy();
        }

        next(action);
        break;
      }

      case (updateShuffle.toString()): {
        dispatch(setShufflingState({
          isShuffling: true,
        }));

        break;
      }

      case (renewShuffle.toString()): {
        shuffleManager.destroy();
        dispatch(setShufflingState({
          isShuffling: true,
        }));

        break;
      }

      case (destroyShuffle.toString()): {
        shuffleManager.destroy();
        break;
      }

      case (markActiveFavourite.toString()):
      case (markActivePlaylistCombo.toString()): {
        next(action);

        if (isShuffling) {
          dispatch(setShufflingState({
            isShuffling: true,
          }));
        }

        break;
      }

      case (unmarkActiveFavourite.toString()):
      case (unmarkActivePlaylistCombo.toString()): {
        next(action);

        if (isShuffling) {
          dispatch(destroyShuffle());
        }

        break;
      }

      case (setPlayerVolume.toString()): {
        const { payload } = (action as PayloadAction<SetPlayerVolumePayload, string>);
        const playerInstance = getPlayerInstance(state, { id: payload.id });

        next(action);

        if (isShuffling && playerInstance && playerInstance.isOscillating) {
          dispatch(updateShuffle());
        }

        break;
      }

      case (stopAllPlayers.toString()): {
        shuffleManager.destroy(isShufflingTransition);
        next(action);
        break;
      }

      case (setIsTerminatingSession.toString()): {
        const { payload } = (action as PayloadAction<IsTerminatingSessionPayload, string>);

        next(action);

        if (payload.isTerminating) {
          shuffleManager.destroy();
        }

        break;
      }

      default:
        next(action);
        break;
    }
  };
};
