/* eslint-disable no-param-reassign */
import { PayloadAction } from 'redux-starter-kit';
import {
  BaseState,
  SetIsMobilePayload,
  SetShowHomepageModalPayload,
  SetPlayerPagePayload,
  SetSharedComboPayload,
  SetShowSoundsLockedModalPayload,
  SetShowUpgradeModalPayload,
  SetShowShareModalPayload,
  SetShowSharePlayModalPayload,
  SetBaseTitlePayload,
} from './base.interfaces';
import { IsLoadingPayload } from '../common/isLoading.interface';

// eslint-disable-next-line import/prefer-default-export
export const setIsMobileReducer = (
  state: BaseState, action: PayloadAction<SetIsMobilePayload>,
) => {
  state.isMobile = action.payload.isMobile;
};

export const setShowHomepageModalReducer = (
  state: BaseState, action: PayloadAction<SetShowHomepageModalPayload>,
) => {
  state.showHomepageModal = action.payload.showHomepageModal;
};

export const setPlayerPageReducer = (
  state: BaseState, action: PayloadAction<SetPlayerPagePayload>,
) => {
  state.playerPage = action.payload.playerPage;
};

export const setSharedComboReducer = (
  state: BaseState, action: PayloadAction<SetSharedComboPayload>,
) => {
  state.sharedCombo = action.payload.sharedCombo;
};

export const setSharedComboIsLoadingReducer = (
  state: BaseState, action: PayloadAction<IsLoadingPayload>,
) => {
  state.sharedComboIsLoading = action.payload.isLoading;
};

export const setShowSoundsLockedModalReducer = (
  state: BaseState, action: PayloadAction<SetShowSoundsLockedModalPayload>,
) => {
  state.showSoundsLockedModal = action.payload.showSoundsLockedModal;
};

export const setShowUpgradeModalReducer = (
  state: BaseState, action: PayloadAction<SetShowUpgradeModalPayload>,
) => {
  state.showUpgradeModal = action.payload.showUpgradeModal;
};

export const setShowShareModalReducer = (
  state: BaseState, action: PayloadAction<SetShowShareModalPayload>,
) => {
  state.showShareModal = action.payload.showShareModal;
};

export const setShowSharePlayModalReducer = (
  state: BaseState, action: PayloadAction<SetShowSharePlayModalPayload>,
) => {
  state.showSharePlayModal = action.payload.showSharePlayModal;
};

export const setBaseTitleReducer = (
  state: BaseState, action: PayloadAction<SetBaseTitlePayload>,
) => {
  state.baseTitle = action.payload.baseTitle;
};

export const closeAllModalsReducer = (
  state: BaseState,
) => {
  state.showUpgradeModal = false;
  state.showSoundsLockedModal = false;
  state.showHomepageModal = false;
  state.showShareModal = false;
  state.showSharePlayModal = false;
};
