import React from 'react';
import { PlayIcon, ReplayIcon, PauseIcon } from '@noisli/icons';
import { Container, ContentContainer } from './TimerControl.styled';

interface Props {
  isActive: boolean;
  isPlaying: boolean;
  isReplay: boolean;
  isPaused: boolean;
  onPlayClick: () => void;
  onPauseClick: () => void;
  onReplayClick: () => void;
}

/**
 * Timer right button with play, pause and replay buttons
 * @param isActive
 * @param isPlaying
 * @param isReplay
 * @param isPaused
 * @param onPlayClick
 * @param onPauseClick
 * @param onReplayClick
 * @returns {any}
 * @constructor
 */
const TimerControl: React.FC<Props> = ({
  isActive, isPlaying, isReplay, isPaused, onPlayClick, onPauseClick, onReplayClick,
}) => {
  const ActionButton = React.useMemo(() => {
    if (!isActive && isReplay) {
      return (
        <ContentContainer onClick={onReplayClick}>
          <ReplayIcon />
        </ContentContainer>
      );
    }

    if (isPlaying || isPaused) {
      return (
        <ContentContainer onClick={onPauseClick}>
          <PauseIcon />
        </ContentContainer>
      );
    }

    return (
      <ContentContainer onClick={onPlayClick}>
        <PlayIcon />
      </ContentContainer>
    );
  }, [isPlaying, isReplay, isPaused]);

  return (
    <Container isActive={isPaused}>
      {ActionButton}
    </Container>
  );
};

export default TimerControl;
