import React, { CSSProperties } from 'react';
import sharedLinksFactory from '@noisli/app/dist/components/ShareModal/sharedLinksFactory';
import { SharedCombo } from '@noisli/common';
import { Text, Title } from '@noisli/app/dist/components/Modal/Modal.styled';
import { GridWrapper } from '@noisli/app/dist/components/Grid/Grid.styled';
import Input from '@noisli/app/dist/components/Inputs/Input/Input';
import { SolidButton } from '@noisli/app/dist/components/Button/Button.styled';
import { FbIcon, TwIcon } from '@noisli/icons';
import EmailIcon from '@noisli/app/dist/components/ShareModal/EmailIcon';
import { ModalGridWrapper } from '@noisli/app/dist/components/ShareModal/ShareModal.styled';
import { ShareImage, ButtonWrapper } from './ShareModal.styled';
import Modal from '../Modal/Modal';

interface Props {
  isOpen: boolean;
  sharedCombo?: SharedCombo;
  isLoading: boolean;
  onCloseClick: () => void;
}

const stylesOverride: {[key: string]: CSSProperties} = {
  title: {
    marginLeft: -10,
  },
  labelGridWrapper: {
    paddingBottom: 12,
  },
  labelGridWrapperFirst: {
    paddingBottom: 12,
    marginTop: -1,
  },
  labelText: {
    lineHeight: '15px',
  },
};

const ShareModal: React.FC<Props> = ({
  isOpen, sharedCombo, isLoading, onCloseClick,
}) => {
  const [successMessage, setSuccessMessage] = React.useState('');
  const [timeoutId, setTimeoutId] = React.useState(0);

  const shareLinks = React.useMemo(
    () => sharedLinksFactory(sharedCombo), [sharedCombo],
  );

  const input = React.useRef<HTMLInputElement>(null);

  const copyToClipboard = () => {
    if (input.current) {
      input.current.select();
      document.execCommand('copy');
      setSuccessMessage('Copied to your clipboard!');

      clearTimeout(timeoutId);
      setTimeoutId(setTimeout(() => {
        setSuccessMessage('');
      }, 2000));
    }
  };

  return (
    <Modal
      isOpen={isOpen}
      onCloseClick={onCloseClick}
    >
      <GridWrapper doubleSpace>
        <ShareImage />
      </GridWrapper>
      <ModalGridWrapper style={stylesOverride.title}>
        <Title>Share with the World</Title>
      </ModalGridWrapper>
      <GridWrapper doubleSpace>
        <Text center>
          Let others enjoy this Combo,
          <br />
          and be their little hero!
        </Text>
      </GridWrapper>
      <GridWrapper style={stylesOverride.labelGridWrapperFirst}>
        <Text style={stylesOverride.labelText}>Copy the link</Text>
      </GridWrapper>
      <GridWrapper>
        <Input
          innerRef={input}
          big
          readOnly
          success={successMessage}
          successAbsolute
          value={isLoading ? 'Preparing your combo to be share with the world...' : shareLinks.base}
          onClick={copyToClipboard}
        />
      </GridWrapper>
      <GridWrapper style={stylesOverride.labelGridWrapper}>
        <Text style={stylesOverride.labelText}>Share via</Text>
      </GridWrapper>
      <ButtonWrapper>
        <SolidButton as="a" href={shareLinks.fb} icon target="_blank">
          <FbIcon />
        </SolidButton>
        <SolidButton as="a" href={shareLinks.twitter} icon target="_blank">
          <TwIcon />
        </SolidButton>
        <SolidButton as="a" href="#" icon onClick={() => window.open(shareLinks.email)}>
          <EmailIcon />
        </SolidButton>
      </ButtonWrapper>
    </Modal>
  );
};

export default ShareModal;
