// eslint-disable-next-line import/no-extraneous-dependencies
import {
  MiddlewareAPI, Dispatch, Action,
} from 'redux';
import { RootState } from '../../background';
import { setOfflineState } from '../../modules/chromeExtension';

const OfflineStateObserverMiddleware = (
  { dispatch }: MiddlewareAPI<Dispatch, RootState>,
) => {
  window.addEventListener('offline', () => {
    dispatch(setOfflineState({
      isOffline: true,
    }));
  });

  window.addEventListener('online', () => {
    dispatch(setOfflineState({
      isOffline: false,
    }));
  });

  return (next: Dispatch) => (action: Action) => next(action);
};

export default OfflineStateObserverMiddleware;
