import {
  SharedCombo, SharedComboFavourite, SharedComboPlaylist, ShareType,
} from '@noisli/common';
import { stringify } from 'query-string';

/**
 * Share links factory. It fills all available methods and types of share and return object with ready links
 * @param sharedCombo
 * @returns {{twitter: string; fb: string; email: string; base: string} | {twitter: string; fb: string; email: string; base: string}}
 */
const sharedLinksFactory = (sharedCombo?: SharedCombo) => {
  if (!sharedCombo) {
    return {
      base: '',
      fb: '',
      twitter: '',
      email: '',
    };
  }

  switch (sharedCombo.type) {
    case (ShareType.FAVOURITE):
      return {
        base: `${process.env.REACT_APP_PAGE_URL}/s/${sharedCombo.shareId}`,
        fb: `https://www.facebook.com/sharer/sharer.php?${stringify({
          u: `${process.env.REACT_APP_PAGE_URL}/s/${sharedCombo.shareId}`,
        })}`,
        twitter: `https://twitter.com/intent/tweet?${stringify({
          url: `${process.env.REACT_APP_PAGE_URL}/s/${sharedCombo.shareId}`,
          text: `I'm currently listening to ${(sharedCombo.content.favourite as SharedComboFavourite).name} on @Noisli`,
        })}`,
        email: `mailto:%20?${stringify({
          subject: '',
          body: `I'm currently listening to ${(sharedCombo.content.favourite as SharedComboFavourite).name} on Noisli.com.
Listen here: ${process.env.REACT_APP_PAGE_URL}/s/${sharedCombo.shareId}`,
        })}`,
      };

    case ShareType.PLAYLIST:
      return {
        base: `${process.env.REACT_APP_PAGE_URL}/s/${sharedCombo.shareId}`,
        fb: `https://www.facebook.com/sharer/sharer.php?${stringify({
          u: `${process.env.REACT_APP_PAGE_URL}/s/${sharedCombo.shareId}`,
        })}`,
        twitter: `https://twitter.com/intent/tweet?${stringify({
          url: `${process.env.REACT_APP_PAGE_URL}/s/${sharedCombo.shareId}`,
          text: `I'm currently listening to ${(sharedCombo.content.playlist as SharedComboPlaylist).name} on @Noisli`,
        })}`,
        email: `mailto:%20?${stringify({
          subject: '',
          body: `I'm currently listening to ${(sharedCombo.content.playlist as SharedComboPlaylist).name} on Noisli.com.
Listen here: ${process.env.REACT_APP_PAGE_URL}/s/${sharedCombo.shareId}`,
        })}`,
      };

    default:
      return {
        base: `${process.env.REACT_APP_PAGE_URL}/s/${sharedCombo.shareId}`,
        fb: `https://www.facebook.com/sharer/sharer.php?${stringify({
          u: `${process.env.REACT_APP_PAGE_URL}/s/${sharedCombo.shareId}`,
        })}`,
        twitter: `https://twitter.com/intent/tweet?${stringify({
          url: `${process.env.REACT_APP_PAGE_URL}/s/${sharedCombo.shareId}`,
          text: 'I\'ve just created this Combo on @Noisli',
        })}`,
        email: `mailto:%20?${stringify({
          subject: '',
          body: `I've just created this Combo on Noisli.com.
Listen here: ${process.env.REACT_APP_PAGE_URL}/s/${sharedCombo.shareId}`,
        })}`,
      };
  }
};

export default sharedLinksFactory;
