/* eslint-disable no-param-reassign */
import React, { PureComponent } from 'react';
// eslint-disable-next-line import/no-webpack-loader-syntax, import/no-duplicates
import TickWorker from 'worker-loader?name=static/[hash].worker.js!../../../../WebWorkers/tick.worker';
// eslint-disable-next-line import/no-duplicates

import { AccountType } from '@noisli/common';
import HlsInstance from './HlsInstance';
import { PlayerCommonProps, PlayerCommonState, VolumeTransitionParams } from './Player.interface';
import { easeLinear, easeOutQuart } from './Easings';

interface State extends PlayerCommonState {
  isSwappingInstances: boolean;
  isVolumeTransitioning: boolean;
  isMuted: boolean;
  isOffline: boolean;
  shouldReloadWhenOnline: boolean;
  offlineTimeoutId: number;
}

interface PlayerInterface {
  mediaEl: HTMLMediaElement | null;
  mediaElPrimary: HTMLMediaElement | null;
  mediaElSecondary: HTMLMediaElement | null;
  hlsInstance: HlsInstance | null;
  hlsInstancePrimary: HlsInstance | null;
  hlsInstanceSecondary: HlsInstance | null;
  volumeFadeDuration: {
    fadeIn: number;
    fadeOut: number;
    crossFade: number;
  };
}

type Props = PlayerCommonProps & {
  useAudio: boolean;
}

/**
 * Core Player component
 * It is designed with react class component to make change flow more controllable
 */
class PlayerComponent extends PureComponent<Props, State> implements PlayerInterface {
  // Active media element
  mediaEl: HTMLMediaElement | null = null;
  // First media element
  mediaElPrimary: HTMLMediaElement | null = null;
  // Second media element, used to perform seamless transition
  mediaElSecondary: HTMLMediaElement | null = null;
  // Active hls instance
  hlsInstance: HlsInstance | null = null;
  // HLS Instance of first player
  hlsInstancePrimary: HlsInstance | null = null;
  // HLS Instance of second player
  hlsInstanceSecondary: HlsInstance | null = null;

  // Fade durations config
  volumeFadeDuration = {
    fadeIn: 800,
    fadeOut: 300,
    crossFade: 2000,
  };

  // Time (in ms) after which the player should be stopped when being offline
  offlineMuteTimeout = 25000;

  constructor(props: Props) {
    super(props);
    this.state = {
      isSwappingInstances: false,
      volumeTransitionId: 0,
      isVolumeTransitioning: false,
      isMuted: false,
      isOffline: false,
      shouldReloadWhenOnline: false,
      offlineTimeoutId: 0,
    };
  }

  static getDerivedStateFromProps(nextProps: Props, prevState: State) {
    const newState: Partial<State> = {};

    // If user mutes/unmutes sounds, lock volume changes and change muted state
    if (nextProps.masterController.isMuted !== prevState.isMuted) {
      newState.isVolumeTransitioning = true;
      newState.isMuted = nextProps.masterController.isMuted;
      return newState;
    }

    return null;
  }

  componentDidMount() {
    this.init();
    this.setNativeEventListeners();
  }

  componentDidUpdate(prevProps: Props) {
    const {
      playerInstance, masterController, user, setShowSoundsLockedModal,
    } = this.props;

    // If user is free and the pro sound would be played
    if (playerInstance.isPlaying && playerInstance.pro
      && user && user.accountType === AccountType.FREE
    ) {
      setShowSoundsLockedModal({
        showSoundsLockedModal: true,
      });
      return null;
    }

    // If player was not playing, but is set to play
    if (playerInstance.isPlaying && !prevProps.playerInstance.isPlaying) {
      return this.setPlayerPlay(true);
    }

    // If player was playing, but is set to no play
    if (!playerInstance.isPlaying && prevProps.playerInstance.isPlaying) {
      return this.setPlayerPlay(false);
    }

    // if user unmutes sounds
    if (
      masterController.isMuted !== prevProps.masterController.isMuted
      && masterController.isMuted === false
    ) {
      return this.setVolume(playerInstance.isPlaying, false, true);
    }

    // if user mutes sounds
    if (
      masterController.isMuted !== prevProps.masterController.isMuted
      && masterController.isMuted === true
    ) {
      return this.setVolume(false, false, true, true);
    }

    // if volume is changed
    if (
      playerInstance.volume !== prevProps.playerInstance.volume
      || masterController.volume !== prevProps.masterController.volume
    ) {
      return this.setVolume(playerInstance.isPlaying, true);
    }

    return null;
  }

  /**
   * Make sure the volume is in range of [0, 1]
   * @param volume
   */
  // eslint-disable-next-line class-methods-use-this
  getNormalizedVolume(volume: number) {
    return Math.max(0, Math.min(volume, 1));
  }

  /**
   * Set event listeners for online and offline state changes
   */
  setNativeEventListeners() {
    window.addEventListener('online', () => {
      const { playerInstance } = this.props;
      const { shouldReloadWhenOnline, offlineTimeoutId } = this.state;

      // remove offline timeout to prevent sound stop
      if (offlineTimeoutId) {
        clearTimeout(offlineTimeoutId);
      }

      // change state of isOffline
      this.setState({
        isOffline: false,
      }, () => {
        // if player was stopped but was playing before, reset it and start playing
        if (shouldReloadWhenOnline && playerInstance.isPlaying) {
          if (this.hlsInstance && this.hlsInstance.mediaEl
            && this.hlsInstance.mediaEl.currentTime === 0
          ) {
            this.setState({
              isSwappingInstances: false,
            });
            if (this.hlsInstancePrimary) this.hlsInstancePrimary.destroy();
            if (this.hlsInstanceSecondary) this.hlsInstanceSecondary.destroy();
            this.init();
            this.setPlayerPlay(true);
            this.setState({
              shouldReloadWhenOnline: false,
            });
          }
        }
      });
    });

    window.addEventListener('offline', () => {
      const { playerInstance } = this.props;

      // set timeout to stop player when being offline
      const offlineTimeoutId = setTimeout(() => this.setPlayerPlay(false), this.offlineMuteTimeout);

      // set state to recover when going online
      this.setState({
        shouldReloadWhenOnline: playerInstance.isPlaying,
        isOffline: true,
        offlineTimeoutId,
      });
    });
  }

  /**
   * Method that runs player on/off
   * @param play
   */
  setPlayerPlay(play: boolean) {
    const { isSwappingInstances, isOffline } = this.state;

    // If we don't have hls initialized, return
    if (!this.hlsInstance || !this.hlsInstance.mediaEl) {
      return;
    }

    if (play) {
      // if we are offline, but request the play, schedule it to play when we goes online
      if (isOffline) {
        this.setState({
          shouldReloadWhenOnline: true,
        });
        return;
      }

      // Set the volume to 0 and start playing from main instance
      this.hlsInstance.mediaEl.volume = 0;
      const playPromise = this.hlsInstance.mediaEl.play();

      if (this.mediaElSecondary) {
        try {
          this.mediaElSecondary.play();
          // eslint-disable-next-line no-empty
        } catch (e) {}
      }

      // IE11 fix play no promise
      if (playPromise === undefined) {
        this.setVolume(play);
      } else {
        playPromise.then(() => {
          // after play is resolved, set volume to value from props with easing
          this.setVolume(play);
        });
      }
    } else {
      // if we stop player, check if we are at the middle of crossfade and terminate it
      if (isSwappingInstances) {
        this.terminateSwappingInstances();
        return;
      }

      // perform fade out (set volume to 0)
      this.setVolume(play).then(() => {
        // then reset the player
        if (this.hlsInstance && this.hlsInstance.mediaEl) {
          this.hlsInstance.mediaEl.pause();
          this.hlsInstance.mediaEl.currentTime = 0;
        }
      });
    }
  }

  /**
   * Method that is setting the volume to set value
   * @param isPlaying
   * @param instant
   * @param muteTransition
   * @param isMutedSet
   * @returns {Promise<void>}
   */
  setVolume(
    isPlaying: boolean, instant = false,
    muteTransition = false, isMutedSet = false,
  ): Promise<void> {
    const { playerInstance, masterController } = this.props;
    const {
      isSwappingInstances, isVolumeTransitioning, isMuted, shouldReloadWhenOnline,
    } = this.state;
    // we take the player volume (slider on page) and mulitply it with master volume (switch on top)
    const targetVolume = playerInstance.volume * masterController.volume;

    // if hls is not initialized, reject
    if (!this.hlsInstance || !this.hlsInstance.mediaEl) {
      return Promise.reject();
    }

    // if we are swapping instances or we are muted, simply resolve
    if (
      (isSwappingInstances && !shouldReloadWhenOnline)
      || (isMuted && !muteTransition)
    ) {
      return Promise.resolve();
    }

    // if change should be instant, eg. we are moving the volume slider
    if (instant) {
      // if we are doing volume transition or we are muted, simply resovle
      if (isVolumeTransitioning || isMuted || masterController.isMuted) {
        return Promise.resolve();
      }
      // set volume instantly
      this.hlsInstance.mediaEl.volume = this.getNormalizedVolume(targetVolume);
      return Promise.resolve();
    }

    // if we are at the middle of shuffle transition
    if (playerInstance.isShuffling && !muteTransition) {
      return Promise.resolve();
    }

    // if player is not playing
    if (!playerInstance.isPlaying && muteTransition) {
      return Promise.resolve();
    }

    // if we are stopping the player or muting it (setting volume to 0), do fade out
    if (!isPlaying || playerInstance.volume === 0 || masterController.volume === 0) {
      return this.volumeFadeOut(isMutedSet);
    }

    // do fade in
    return this.volumeFadeIn(isMutedSet);
  }

  /**
   * Method to run volume transition with fade out setting
   * @param muteTransition
   * @returns {Promise<never>}
   */
  volumeFadeOut(muteTransition = false): Promise<void> {
    return this.volumeTransition(
      this.volumeFadeDuration.fadeOut, 0, 1, {
        muteTransition,
      },
    );
  }

  /**
   * Method to run volume transition with fade in setting
   * @param muteTransition
   * @returns {Promise<never>}
   */
  volumeFadeIn(muteTransition = false): Promise<void> {
    return this.volumeTransition(
      this.volumeFadeDuration.fadeIn, 1, 0, {
        muteTransition,
      },
    );
  }

  /**
   * Volume transition method - "The meat of our player"
   * first 3 params are required
   * @param duration - duration of transition in ms
   * @param targetVolume - volume to which we are transitioning
   * @param startVolume - volume from which we are transitioning
   * @param mediaEl - chosen media element (by default we use active instance)
   * @param easing - easing function (default linear ease)
   * @param timeout - time in ms to wait until running the transition
   * @param worker - if set, we are using worker to handle time scheduling
   * @param muteTransition - flag to determine if we are doing the mute transition
   * @returns {Promise<never> | Promise<void>}
   */
  volumeTransition(
    duration: number,
    targetVolume: number,
    startVolume: number,
    {
      mediaEl = this.hlsInstance ? this.hlsInstance.mediaEl : null,
      easing = easeLinear,
      timeout = 0,
      worker = null,
      muteTransition = false,
    }: Partial<VolumeTransitionParams>,
  ) {
    // 60 fps interval
    const INTERVAL = 1000 / 60;
    const { masterController, playerInstance } = this.props;
    const { isSwappingInstances, volumeTransitionId } = this.state;

    let masterControllerVolume = masterController.volume;
    // if master controller is muted, we are referencing the last volume it has set
    if (masterController.isMuted) {
      masterControllerVolume = masterController.lastVolume;
    }

    // without element, we cant transition volume
    if (!mediaEl) {
      return Promise.reject();
    }

    // if there is ongoing transition, we need to clear it, one exception is when we are swapping instances
    if (volumeTransitionId && !isSwappingInstances) {
      clearInterval(volumeTransitionId);
    }

    // get delta of transition
    const delta = targetVolume - startVolume;

    // if delta is 0 or duration is 0, set volume instantly
    if (!delta || !duration) {
      mediaEl.volume = this.getNormalizedVolume(
        targetVolume * playerInstance.volume * masterControllerVolume,
      );
      return Promise.resolve();
    }

    // calculate ticks (number of iterations we perform in transition)
    const ticks = Math.floor(duration / INTERVAL);

    // return promise which runs fader
    return new Promise<void>((resolve, reject) => {
      let tick = 1;
      let volume = startVolume;

      // fading function, we can pass terminator function, if we need to stop transition in the middle of run
      const step = (terminate: () => void) => {
        // eslint-disable-next-line no-shadow
        const { masterController } = this.props;

        masterControllerVolume = masterController.volume;
        if (muteTransition) {
          masterControllerVolume = masterController.lastVolume;
        }
        const volumeModifier = playerInstance.volume * masterControllerVolume;

        if (!mediaEl) {
          reject();
          return;
        }

        // if we are transitioning volume down, we need to take start volume into account
        if (delta < 0) {
          volume = (startVolume + easing(tick / ticks) * delta) * volumeModifier;
        } else {
          // run this if we are transitionig up
          volume = easing(tick / ticks) * delta * volumeModifier;
        }

        // set volume to media element
        mediaEl.volume = this.getNormalizedVolume(volume);
        tick += 1;

        // if tick reached ticks count (we are at last iteration)
        if (tick === ticks) {
          // set target volume to element
          mediaEl.volume = this.getNormalizedVolume(targetVolume * volumeModifier);
          // notify that we finish transitioning
          this.setState({
            isVolumeTransitioning: false,
          });
          // end transition and resolve promise
          terminate();
          resolve();
        }
      };

      // if we are using worker, use worker setup
      if (worker) {
        // set worker function to run on each tick
        worker.onmessage = () => {
          // if we have first tick, set interval on worker
          if (tick === 1) {
            worker.postMessage({
              command: 'start_interval',
              payload: {
                interval: INTERVAL,
              },
            });
          }

          // run step function
          step(() => {
            worker.postMessage({
              command: 'stop',
            });
            worker.terminate();
          });
        };

        // run worker with timeout specified
        worker.postMessage({
          command: 'start_timeout',
          payload: {
            timeout,
          },
        });
      } else {
        // use timeout and interval to run the fader
        setTimeout(() => {
          const transitionId: number = setInterval(() => {
            step(() => clearInterval(transitionId));
          }, INTERVAL);
          // notify that we are transitioning
          this.setState({
            volumeTransitionId: transitionId,
            isVolumeTransitioning: true,
          });
        }, timeout);
      }
    });
  }

  init() {
    this.initializeHls();
    this.hlsInstance = this.hlsInstancePrimary;
  }

  initializeHls() {
    const { id, playerInstance, setIsLoaded } = this.props;
    const { url } = playerInstance;

    // notify store that player is ready
    const onManifestParse = () => {
      setIsLoaded({
        id,
        isLoaded: true,
      });
    };

    this.hlsInstancePrimary = new HlsInstance(this.mediaElPrimary, url, onManifestParse);
    this.addVideoEventListeners(this.hlsInstancePrimary);
  }

  /**
   * Method to set event listener to start crossfade transition
   * @param hlsInstance
   */
  addVideoEventListeners(hlsInstance: HlsInstance) {
    if (hlsInstance.mediaEl) {
      hlsInstance.mediaEl.addEventListener('timeupdate', () => this.timeUpdateHandler(hlsInstance));
    }
  }

  /**
   * cleanup crossfade tracker
   * @param hlsInstance
   */
  removeVideoEventListeners(hlsInstance: HlsInstance) {
    if (hlsInstance.mediaEl) {
      hlsInstance.mediaEl.removeEventListener('timeupdate', () => this.timeUpdateHandler(hlsInstance));
    }
  }

  /**
   * handler to spy on timeupdate of video and start crossfade (swap instance)
   * @param hlsInstance
   */
  timeUpdateHandler(hlsInstance: HlsInstance) {
    const { masterController } = this.props;
    const DURATION_TOLERANCE = masterController.crossFadeInStartTime;
    const { isSwappingInstances } = this.state;
    // if currentTIme reached duration - tolerance and we are not swapping the instance
    if (
      hlsInstance.mediaEl
      && hlsInstance.mediaEl.currentTime > hlsInstance.mediaEl.duration - DURATION_TOLERANCE
      && !isSwappingInstances
    ) {
      this.swapInstances();
    }
  }

  /**
   * Method that starts the swap instance
   */
  swapInstances() {
    const { playerInstance } = this.props;
    const { url } = playerInstance;

    // notify that we are swapping instances
    this.setState({
      isSwappingInstances: true,
    });

    // choose replacement instance by selecting the inactive one
    let instanceReplacement: HlsInstance | null = null;

    if (this.hlsInstance === this.hlsInstancePrimary) {
      this.hlsInstanceSecondary = new HlsInstance(this.mediaElSecondary, url, () => {});
      this.addVideoEventListeners(this.hlsInstanceSecondary);
      instanceReplacement = this.hlsInstanceSecondary;
    } else if (this.hlsInstance === this.hlsInstanceSecondary) {
      this.hlsInstancePrimary = new HlsInstance(this.mediaElPrimary, url, () => {});
      this.addVideoEventListeners(this.hlsInstancePrimary);
      instanceReplacement = this.hlsInstancePrimary;
    }

    // if instance repalcement is ready to use
    if (instanceReplacement && instanceReplacement.mediaEl) {
      // set fader workers (crossfade is heavier to perform than fade)
      const fadeOutWorker = new TickWorker();
      const fadeInWorker = new TickWorker();

      // function to perform crossfade and reset the state
      const swapVideos = async () => {
        const { masterController } = this.props;
        // if we dont have replacement or main instance, do nothing
        if (!instanceReplacement
          || !instanceReplacement.mediaEl
          || !this.hlsInstance
          || !this.hlsInstance.mediaEl
        ) {
          return;
        }
        // first start instance replacement fade in
        this.volumeTransition(
          masterController.crossFadeInDuration, 1, 0, {
            mediaEl: instanceReplacement.mediaEl,
            easing: easeOutQuart,
            timeout: masterController.crossFadeInDelay,
            worker: fadeOutWorker,
          },
        );
        // and simultanously run the fade out
        this.volumeTransition(
          masterController.crossFadeOutDuration, 0, 1, {
            mediaEl: this.hlsInstance.mediaEl,
            easing: easeOutQuart,
            timeout: masterController.crossFadeOutStartTime,
            worker: fadeInWorker,
          },
        ).then(() => {
          // clear state after crossfade
          if (this.hlsInstance && this.hlsInstance.mediaEl) {
            this.hlsInstance.mediaEl.pause();
            this.hlsInstance.mediaEl.currentTime = 0;
            if (
              instanceReplacement && instanceReplacement.mediaEl && instanceReplacement.mediaEl.src
              && this.hlsInstance !== instanceReplacement
            ) {
              this.hlsInstance.destroy();
              this.removeVideoEventListeners(this.hlsInstance);
              this.hlsInstance = instanceReplacement;
            }
            this.setState({
              isSwappingInstances: false,
            });
          }
        });
      };

      // prepare the instance replacement by putting the volume to 0 and start playing
      instanceReplacement.mediaEl.volume = 0;
      const playPromise = instanceReplacement.mediaEl.play();
      // then perform swap
      if (playPromise === undefined) {
        swapVideos();
      } else {
        playPromise.then(swapVideos);
      }
    } else {
      // if we do not have instance replacement, end swapping and rely on video loop
      this.setState({
        isSwappingInstances: false,
      });
    }
  }

  /**
   * If we are at the middle of crossfade, we need to fade out both instances
   * then we need to reset one instance and assign it to main instance
   */
  terminateSwappingInstances() {
    if (this.hlsInstancePrimary) {
      this.volumeTransition(
        this.volumeFadeDuration.fadeOut, 0, 1, {
          mediaEl: this.hlsInstancePrimary.mediaEl,
        },
      ).then(() => {
        if (this.hlsInstancePrimary && this.hlsInstancePrimary.mediaEl) {
          this.hlsInstancePrimary.mediaEl.pause();
          this.hlsInstancePrimary.mediaEl.currentTime = 0;
          this.hlsInstance = this.hlsInstancePrimary;
        }
      });
    }
    if (this.hlsInstanceSecondary) {
      this.volumeTransition(
        this.volumeFadeDuration.fadeOut, 0, 1, {
          mediaEl: this.hlsInstanceSecondary.mediaEl,
        },
      ).then(() => {
        if (this.hlsInstanceSecondary && this.hlsInstanceSecondary.mediaEl) {
          this.hlsInstanceSecondary.mediaEl.pause();
          this.hlsInstanceSecondary.mediaEl.currentTime = 0;
          this.hlsInstanceSecondary.destroy();
          this.removeVideoEventListeners(this.hlsInstanceSecondary);
        }
      });
    }
  }

  render() {
    const { id, useAudio } = this.props;

    const hiddenStyle: React.CSSProperties = {
      width: 0,
      height: 0,
      visibility: 'hidden',
    };

    return (
      <div
        id={id}
        style={hiddenStyle}
      >
        { useAudio ? (
          <>
            <audio
              ref={(el) => { this.mediaElPrimary = el; }}
              style={hiddenStyle}
            />
            <audio
              ref={(el) => { this.mediaElSecondary = el; }}
              style={hiddenStyle}
            />
          </>
        ) : (
          <>
            <video
              ref={(el) => { this.mediaElPrimary = el; }}
              playsInline
              style={hiddenStyle}
            />
            <video
              ref={(el) => { this.mediaElSecondary = el; }}
              playsInline
              style={hiddenStyle}
            />
          </>
        )}
      </div>
    );
  }
}

export default PlayerComponent;
