import styled, { css } from 'styled-components';

interface ButtonIconProps {
  left?: boolean;
  right?: boolean;
}

export const ButtonIcon = styled.span<ButtonIconProps>`
  position: absolute;
  top: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  transition: margin ${(props) => props.theme.transition};
  
  ${(props) => props.left && css`
    margin-left: 0;
    left: 12px;
  `}

  ${(props) => props.right && css`
    right: 12px;
  `}
  
  svg {
    width: 16px;
    height: 16px;
  }
`;

export interface GhostButtonProps {
  disabled?: boolean;
  active?: boolean;
  iconButton?: boolean;
  white?: boolean;
  big?: boolean;
  upgrade?: boolean;
}

// eslint-disable-next-line import/prefer-default-export
export const GhostButton = styled.button<GhostButtonProps>`
  display: inline-flex;
  padding: 5px 13px;
  color: ${(props) => props.theme.colors.white};
  font-size: 13px;
  font-weight: 500;
  border-radius: 13px;
  background-color: ${(props) => props.theme.colors.black04};
  border-width: 0;
  cursor: pointer;
  transition: ${(props) => props.theme.transition} background-color;
  line-height: 15px;
  user-select: none;

  &:hover {
    background-color: ${(props) => props.theme.colors.black16};
  }

  &:focus {
    outline: none;
  }

  ${(props) => props.disabled && css`
    color: ${props.theme.colors.white50};
    cursor: pointer;
    
    &:hover {
      background-color: ${props.theme.colors.black04};
      cursor: pointer;
    }
  `}

  ${(props) => props.active && css`
    background-color: ${props.theme.colors.black16};
    font-weight: 600;
  `}

  ${(props) => props.iconButton && css`
    padding: 5px 6px;
    font-size: 14px;
  `}
  
  ${(props) => props.iconButton && props.disabled && css`
    color: ${props.theme.colors.white};
    
    svg {
      opacity: 0.5;
    }
  `}

  ${(props) => props.white && css`
    background-color: ${props.theme.colors.white15};

    &:hover {
      background-color: ${props.theme.colors.white30};
    }
  `}
  
  ${(props) => props.big && css`
    padding: 14px 30px;
    font-size: 14px;
    line-height: 16px;
    border-radius: 22px;
  `}
  
  ${(props) => props.upgrade && css`
    font-weight: 700;
    font-size: 14px;
    padding: 10px 20px;  
    border-radius: 18px;
    margin-right: 24px;
    width: 100px;
    height: 36px;
    
    &:hover {
      box-shadow: 0 2px 4px ${props.theme.colors.black05};
    }
  `}
`;

interface OutlineButtonProps {
  href?: string;
}

export const OutlineButton = styled.button<OutlineButtonProps>`
  width: 100%;
  display: inline-block;
  border: 1px solid ${(props) => props.theme.colors.black16};
  border-radius: 18px;
  background-color: transparent;
  font-size: 13px;
  font-weight: 600;
  line-height: 34px;
  text-align: center;
  position: relative;
  cursor: pointer;
  text-decoration: none;
  color: ${(props) => props.theme.colors.lightGrey1};
  
  &:hover {
    &::before {
      opacity: 1;
    }
  }

  &:focus {
    outline: none;
  }
  
  &::before {
    content: '';
    position: absolute;
    top: -1px;
    left: -1px;
    right: -1px;
    bottom: -1px;
    border-radius: 18px;
    border: 2px solid ${(props) => props.theme.colors.primary};
    opacity: 0;
    transition: opacity ${(props) => props.theme.transition}, border-color ${(props) => props.theme.transition};
  }
`;

export interface SolidButtonProps {
  red?: boolean;
  inline?: boolean;
  flat?: boolean;
  white?: boolean;
  icon?: boolean;
  withShadow?: boolean;
  disabled?: boolean;
  withSimpleShadow?: boolean;

  href?: string;
  target?: string;
}

export const SolidButton = styled.button<SolidButtonProps>`
  width: 100%;
  padding: 0;
  color: ${(props) => props.theme.colors.white};
  background-color: ${(props) => props.theme.colors.primary};
  border: none;
  border-radius: 18px;
  font-size: 14px;
  font-weight: 700;
  line-height: 36px;
  text-align: center;
  position: relative;
  cursor: pointer;
  transition: background-color ${(props) => props.theme.transition};
  text-decoration: none;
  display: inline-block;
  
  &:hover {
    background-color: ${(props) => props.theme.colors.primaryDark};
  }

  &:focus {
    outline: none;
  }

  ${(props) => props.red && css`
    background-color: ${props.theme.colors.red1};

    &:hover {
      background-color: ${props.theme.colors.red2};
    }
  `}

  ${(props) => props.inline && css`
    width: auto;
    padding: 0 26px;
  `}

  ${(props) => props.flat && css`
    line-height: 26px;
    font-weight: 600;
    font-size: 13px;
  `}
  
  ${(props) => props.white && css`
    color: ${props.theme.colors.primary};
    background-color: ${props.theme.colors.white};

    &:hover {
      background-color: ${props.theme.colors.white};
    }
  `}
  
  ${(props) => props.icon && css`
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 36px;
  `}
  
  ${(props) => props.withShadow && css`
    box-shadow: 0 2px 4px ${props.theme.colors.black05};
    transition: background-color ${props.theme.transition}, box-shadow ${props.theme.transition};
    
    &:hover {
      box-shadow: 0 2px 4px ${props.theme.colors.black25};
    }
  `}
  
  ${(props) => props.disabled && css`
    color: ${props.theme.colors.white50};
    cursor: not-allowed;
    
    &:hover {
      color: ${props.theme.colors.white50};
      background-color: ${props.theme.colors.primary};
    }
  `}
  
  ${(props) => props.withSimpleShadow && css`
    box-shadow: 0 2px 4px ${props.theme.colors.black05};

    &:hover {
      box-shadow: 0 2px 4px ${props.theme.colors.black05};
    }
  `}
`;

export const ButtonWrapper = styled.div`
  display: flex;
  margin: 0 -4px;

  ${GhostButton},
  ${SolidButton},
  ${OutlineButton} {
    margin: 0 4px;
  }
`;
