import styled, { css } from 'styled-components';

export const StyledDropdown = styled.div`
  position: relative;
`;

export const ActivatorContainer = styled.div``;

interface DropdownMenuProps {
  right?: boolean;
  isOpen?: boolean;
}

export const DropdownMenu = styled.div<DropdownMenuProps>`
  padding: 22px 0;
  position: absolute;
  width: 200px;
  background-color: ${(props) => props.theme.colors.white};
  top: calc(100% + 5px);
  left: 0;
  border-radius: 11px;
  border-top-left-radius: 0;
  box-shadow: 0 3px 6px ${(props) => props.theme.colors.black20};
  opacity: 0;
  visibility: hidden;
  transition: ${(props) => props.theme.transition} opacity;

  ${(props) => props.right && css`
    left: auto;
    right: 0;

    border-top-left-radius: 11px;
    border-top-right-radius: 0;
  `}

  ${(props) => props.isOpen && css`
    visibility: visible;
    opacity: 1;
  `}
`;

interface DropdownMenuItemProps {
  primaryColor?: boolean;
}

export const DropdownMenuItem = styled.a<DropdownMenuItemProps>`
  width: 100%;
  display: block;
  padding: 0 22px;
  height: 36px;
  line-height: 36px;
  font-size: 13px;
  font-weight: 500;
  color: ${(props) => props.theme.colors.lightGrey1};
  background-color: transparent;
  transition: ${(props) => props.theme.transition} background-color;
  text-decoration: none;

  ${(props) => props.primaryColor && css`
    color: ${props.theme.colors.primary};
    font-weight: 600;
  `}

  &:hover {
    background-color: ${(props) => props.theme.colors.black02};
  }
`;

export const DropdownMenuSeparator = styled.span`
  display: block;
  margin: 17px 22px 18px;
  border-top: 1px solid ${(props) => props.theme.colors.black04};
`;
