import React from 'react';
import StyledSlider from './RangeSlider.styled';

interface Props {
  min: number;
  max: number;
  step: number;
  value: number;
  disabled?: boolean;
  onChange: (e: React.ChangeEvent<{}>, value: number | number[]) => void;
}

const RangeSlider: React.FC<Props> = ({
  min, max, step, value, disabled, onChange,
}) => (
  <StyledSlider
    min={min}
    max={max}
    step={step}
    value={value}
    disabled={disabled}
    onChange={onChange}
    onClick={(e) => { e.stopPropagation(); }}
  />
);

export default RangeSlider;
