// eslint-disable-next-line import/no-extraneous-dependencies
import {
  Dispatch, Action, Middleware, MiddlewareAPI, AnyAction,
} from 'redux';
import { RootState } from 'app/redux';
import {
  startOscillator,
  clearOscillator,
  updateOscillator,
  setPlayerPlaying,
  setPlayerVolume,
  stopAllPlayers,
  setShufflingTranstionState,
} from 'app/redux/modules/players';
import { PayloadAction } from 'redux-starter-kit';
import {
  OscillatorPlayerPayload,
  SetPlayerPlayingPayload,
  SetPlayerVolumePayload,
} from 'app/redux/modules/players/players.interfaces';
import { getPlayerInstance } from 'app/redux/modules/players/players.selectors';
import { getActiveFavourite } from 'app/redux/modules/favourites/favourites.selectors';
import { getActivePlaylistCombo } from 'app/redux/modules/playlists/playlists.selectors';
import { SetShufflePayload } from 'app/redux/modules/common/shuffle.interfaces';
import { setIsTerminatingSession } from 'app/redux/modules/timer';
import { AccountType } from '@noisli/common';
import OscillationManager from './OscillationManager';
import { IsTerminatingSessionPayload } from '../../modules/timer/timer.interfaces';


/**
 * Middleware that is responsible for handling oscillation actions
 * It is switched off on mobile devices or if user is free
 * It turns off oscillatiors if shuffling transition is starting
 * It starts or update oscillators for active players
 * It updates oscillators if player volume is changed
 * It destroys oscillators if player is deactivated
 * It destroys oscillators if timer is terminating session
 * Everything is running in switch/case block with visible actions names
 *
 * @returns {({dispatch, getState}: MiddlewareAPI<Dispatch<AnyAction>, RootState>) => (next: Dispatch) => (action: Action) => void}
 */
// eslint-disable-next-line import/prefer-default-export
export const createOscillationMiddleware: () => Middleware = () => {
  const oscillationManager = new OscillationManager();
  // For development purposes
  // (global as any).NoisliOscillationManager = oscillationManager;

  return (
    { dispatch, getState }: MiddlewareAPI<Dispatch<AnyAction>, RootState>,
  ) => (next: Dispatch) => (action: Action) => {
    const state = getState();

    if (state.base.isMobile || state.users.user.accountType === AccountType.FREE) {
      next(action);
      return;
    }

    const { isShufflingTransition } = state.players.masterController;
    const isOscillationOn = state.players.masterController.isOscillating;
    const activeFavourite = getActiveFavourite(state);
    const activePlaylistCombo = getActivePlaylistCombo(state);

    switch (action.type) {
      case (startOscillator.toString()): {
        const { payload } = (action as PayloadAction<OscillatorPlayerPayload, string>);
        const playerInstance = getPlayerInstance(state, { id: payload.playerId });

        if (!playerInstance || isShufflingTransition || !playerInstance.isPlaying) {
          return;
        }

        oscillationManager.createOrUpdate({
          dispatch,
          playerInstance,
          oscillationConfig: state.players.oscillationConfig,
        });

        next(action);
        break;
      }

      case (updateOscillator.toString()): {
        const { payload } = (action as PayloadAction<OscillatorPlayerPayload, string>);
        const playerInstance = getPlayerInstance(state, { id: payload.playerId });

        if (!playerInstance || isShufflingTransition || !playerInstance.isPlaying) {
          return;
        }

        oscillationManager.createOrUpdate({
          dispatch,
          playerInstance,
          oscillationConfig: state.players.oscillationConfig,
        });

        break;
      }

      case (clearOscillator.toString()): {
        const { payload } = (action as PayloadAction<OscillatorPlayerPayload, string>);

        if (!isShufflingTransition) {
          if (payload.immediate) {
            oscillationManager.destroy(payload.playerId);
            break;
          }

          if (activeFavourite) {
            activeFavourite.items.forEach((item) => {
              if (item.id !== payload.playerId) {
                return;
              }
              const playerInstance = getPlayerInstance(state, { id: item.id });
              if (!playerInstance) {
                return;
              }
              oscillationManager.returnToDefaultValueAndDestroy(playerInstance, item.volume);
            });
            next(action);
            break;
          }

          if (activePlaylistCombo) {
            activePlaylistCombo.items.forEach((item) => {
              if (item.id !== payload.playerId) {
                return;
              }
              const playerInstance = getPlayerInstance(state, { id: item.id });
              if (!playerInstance) {
                return;
              }
              oscillationManager.returnToDefaultValueAndDestroy(playerInstance, item.volume);
            });
            next(action);
            break;
          }
        }

        oscillationManager.destroy(payload.playerId);

        next(action);
        break;
      }

      case (setPlayerPlaying.toString()): {
        if (!isOscillationOn) {
          next(action);
          break;
        }
        const { payload } = (action as PayloadAction<SetPlayerPlayingPayload, string>);
        next(action);

        if (payload.isPlaying) {
          dispatch(startOscillator({
            playerId: payload.id,
          }));
        } else {
          dispatch(clearOscillator({
            playerId: payload.id,
          }));
        }

        break;
      }

      case (setPlayerVolume.toString()): {
        if (!isOscillationOn) {
          next(action);
          break;
        }
        const { payload } = (action as PayloadAction<SetPlayerVolumePayload, string>);
        const playerInstance = getPlayerInstance(state, { id: payload.id });

        next(action);

        if (!playerInstance) {
          break;
        }

        if (playerInstance.isOscillating) {
          break;
        } else {
          dispatch(updateOscillator({
            playerId: payload.id,
          }));
        }

        break;
      }

      case (stopAllPlayers.toString()): {
        next(action);
        const { scheduledOscillation } = getState().players.masterController;

        scheduledOscillation.forEach((oscillator) => {
          dispatch(clearOscillator({
            playerId: oscillator.playerId,
            immediate: true,
          }));
        });
        break;
      }

      case (setShufflingTranstionState.toString()): {
        const { payload } = (action as PayloadAction<SetShufflePayload, string>);

        next(action);
        if (isOscillationOn) {
          if (payload.isShuffling) {
            state.players.masterController.scheduledOscillation.forEach((oscillator) => {
              dispatch(clearOscillator({
                playerId: oscillator.playerId,
              }));
            });
          } else {
            state.players.players.forEach((player) => {
              if (!player.isPlaying) {
                return;
              }
              dispatch(startOscillator({
                playerId: player.id,
              }));
            });
          }
        }

        break;
      }

      case (setIsTerminatingSession.toString()): {
        const { payload } = (action as PayloadAction<IsTerminatingSessionPayload, string>);

        next(action);

        if (payload.isTerminating) {
          state.players.masterController.scheduledOscillation.forEach((oscillator) => {
            dispatch(clearOscillator({
              playerId: oscillator.playerId,
              immediate: true,
            }));
          });
        }

        break;
      }

      default:
        next(action);
        break;
    }
  };
};
