import axios from './AxiosInstance';
import {
  Favourite,
  CreateFavouriteDto,
  UpdateFavouriteDto,
} from '@noisli/common';

/**
 * Static class that have all connection to API regarding Favourites module
 */
export default class FavouritesApi {
  static apiUrl = `${process.env.REACT_APP_API_URL}/favourite`;

  static async getAll() {
    const response = await axios.get<Favourite[]>(`${FavouritesApi.apiUrl}`, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async getSingle(uuid: string) {
    const response = await axios.get<Favourite>(`${FavouritesApi.apiUrl}/${uuid}`);
    const { data } = response;

    return data;
  }


  static async create(payload: CreateFavouriteDto) {
    const response = await axios.post<Favourite>(`${FavouritesApi.apiUrl}/create`, payload, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async update(uuid: string, payload: UpdateFavouriteDto) {
    const response = await axios.put<Favourite>(`${FavouritesApi.apiUrl}/${uuid}`, payload, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async delete(uuid: string) {
    const response = await axios.delete<string>(`${FavouritesApi.apiUrl}/${uuid}`, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }

  static async play() {
    await axios.get(`${FavouritesApi.apiUrl}/play`, {
      withCredentials: true,
    });
  }
}
