import React from 'react';
import { CancelIcon, SettingsIcon, SkipIcon } from '@noisli/icons';
import { TimerTime, TimerType } from '@noisli/common';
import {
  Container, ContentContainer, Digits, IconContainer, TextContainer,
} from './Timer.styled';

interface Props {
  actualTime: string;
  workInSession: boolean;
  isRunning: boolean;
  currentTimerType: TimerType;
  sessionTime: TimerTime;
  shortBreakTime: TimerTime;
  longBreakTime: TimerTime;
  onSettingsClick: () => void;
  onCancelClick: () => void;
  onSkipClick?: () => void;
}

const Timer: React.FC<Props> = ({
  workInSession,
  actualTime,
  isRunning,
  currentTimerType,
  sessionTime,
  shortBreakTime,
  longBreakTime,
  onSettingsClick,
  onSkipClick,
  onCancelClick,
}) => {
  const [isHover, setIsHover] = React.useState(false);

  // Show timer text according to current timer type
  const TimerText = React.useMemo(() => {
    if (currentTimerType === TimerType.LongBreak) {
      return 'Long break';
    }

    if (currentTimerType === TimerType.ShortBreak) {
      return 'Short break';
    }

    if (workInSession) {
      return 'Session';
    }

    return '';
  }, [currentTimerType, workInSession]);

  const timeDigits = React.useMemo(() => actualTime.split(':'), [actualTime]);

  // On hover, change content to show settings and skip button
  const OnHoverContent = React.useMemo(() => (
    <ContentContainer isActive={isHover} isRunning={isRunning}>
      <IconContainer onClick={onSettingsClick}>
        <SettingsIcon />
      </IconContainer>
      <TextContainer>
        {TimerText}
      </TextContainer>
      { (currentTimerType !== TimerType.Session) ? (
        <IconContainer rightIcon onClick={onSkipClick}>
          <SkipIcon />
        </IconContainer>
      ) : (
        <IconContainer rightIcon disabled={!isRunning} onClick={onCancelClick}>
          <CancelIcon />
        </IconContainer>
      )}
    </ContentContainer>
  ), [isHover, TimerText, currentTimerType]);

  // check if we have hours active, as we need to show the hours digits then
  const hasHours = React.useMemo(() => {
    switch (currentTimerType) {
      case TimerType.Session:
        return sessionTime.hours !== 0;
      case TimerType.ShortBreak:
        return shortBreakTime.hours !== 0;
      case TimerType.LongBreak:
        return longBreakTime.hours !== 0;
      default:
        return false;
    }
  }, [currentTimerType, sessionTime, shortBreakTime, longBreakTime]);

  return (
    <Container
      onMouseEnter={() => setIsHover(true)}
      onMouseLeave={() => setIsHover(false)}
    >
      <ContentContainer isActive={!isHover} isRunning={isRunning}>
        { hasHours && (
          <>
            <Digits>{timeDigits[0]}</Digits>
            <Digits colon>:</Digits>
          </>
        )}
        <Digits>{timeDigits[1]}</Digits>
        <Digits colon>:</Digits>
        <Digits>{timeDigits[2]}</Digits>
      </ContentContainer>
      {OnHoverContent}
    </Container>
  );
};

export default Timer;
