import styled, { css } from 'styled-components';
import rwd, { Breakpoint } from 'app/styles/rwd';

export interface GridContainerProps {
  fluid?: boolean;
  center?: boolean;
  white?: boolean;
  lightGrey?: boolean;
  relative?: boolean;
}

export const GridContainer = styled.div<GridContainerProps>`
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: auto;
  max-width: 290px;

  ${rwd[Breakpoint.SMALL]`
    max-width: 540px;
  `}

  ${rwd[Breakpoint.MEDIUM]`
    max-width: 720px;
  `}

  ${rwd[Breakpoint.LARGE]`
    max-width: 960px;
  `}
  
  ${(props) => props.fluid && css`
    max-width: none !important;
  `}
  
  ${(props) => props.center && css`
    align-items: center;
  `}
  
  ${(props) => props.white && css`
    background-color: ${props.theme.colors.white};
  `}
  
  ${(props) => props.lightGrey && css`
    background-color: ${props.theme.colors.lightGrey1};
  `}
  
  ${(props) => props.relative && css`
    position: relative;
  `}
`;

export interface RowProps {
  center?: boolean;
}

export const Row = styled.div<RowProps>`
  width: 100%;
  display: flex;
  
  ${(props) => props.center && css`
    justify-content: center;
  `}
`;

export interface WrapperProps {
  doubleSpace?: boolean;
  tripleSpace?: boolean;
  halfSpace?: boolean;
  fullWidth?: boolean;
}

export const GridWrapper = styled.div<WrapperProps>`
  padding-bottom: 22px;

  ${(props) => props.doubleSpace && `
    padding-bottom: 44px;
  `}
  
  ${(props) => props.tripleSpace && `
    padding-bottom: 66px;
  `}

  ${(props) => props.halfSpace && `
    padding-bottom: 12px;
  `}
  
  ${(props) => props.fullWidth && `
    width: 100%;
  `}
`;
