import React from 'react';
import {connect, useDispatch, useSelector} from 'react-redux';
import useEventListener from '@use-it/event-listener';
import throttle from 'lodash/throttle';

import { RootState } from 'app/redux';
import { setMasterPlayerIsMutedThunk } from 'app/redux/modules/players/players.thunks';
import {
  MasterController,
  Player,
  SetMasterPlayerMutedPayload,
} from 'app/redux/modules/players/players.interfaces';
import PlayerContainer from 'app/components/_Core/Player/Player/PlayerContainer';
import { setUseHomepagePlayers } from 'app/redux/modules/players';
import {isIOS, isIpadOS} from "../../../../utils/mobileDetect";
import { getPlayersAreLoaded } from 'app/redux/modules/players/players.selectors';

interface PlayerMasterControllerReduxProps {
  players: Player[];
  masterController: MasterController;
  setMasterPlayerIsMuted: (payload: SetMasterPlayerMutedPayload) => void;
}

interface Props {
  useHomepage?: boolean;
}

const PlayerMasterController: React.FC<Props & PlayerMasterControllerReduxProps> = ({
  players, masterController, setMasterPlayerIsMuted, useHomepage,
}) => {
  const dispatch = useDispatch();
  const playersLoaded = useSelector(getPlayersAreLoaded);

  // Check wheater we are at the /index or on internal pages
  React.useEffect(() => {
    dispatch(setUseHomepagePlayers({
      useHomepage: !!useHomepage,
    }));
  }, [useHomepage]);

  // Mute sounds directly in audio elements for ios
  React.useEffect(() => {
    if (isIOS() || isIpadOS()) {
      players.forEach(player => {
        const playerContainer = document.getElementById(player.id);
        if (!playerContainer) return;
        const audioEls = playerContainer.querySelectorAll<HTMLAudioElement>('audio');
        Array.from(audioEls).forEach(audioEl => {
          audioEl.muted = masterController.isMuted;
        });
      });
    }
  }, [masterController.isMuted, playersLoaded]);

  // throttle the user space click interaction
  const handleSpaceClickThrottled = React.useMemo(() => throttle((isMuted: boolean) => {
    setMasterPlayerIsMuted({
      isMuted,
    });
  }, 100, { leading: true }), []);

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const handleSpaceClick = (e: any) => {
    if (e.target && (e.target.tagName.toUpperCase() === 'INPUT' || e.target.tagName.toUpperCase() === 'TEXTAREA')) {
      return;
    }
    if (e.code === 'Space' || e.keyCode === 32) {
      e.preventDefault();
      handleSpaceClickThrottled(!masterController.isMuted);
    }
  };

  useEventListener('keydown', handleSpaceClick);

  return (
    <div>
      { players.map((player) => (
        <PlayerContainer key={player.id} id={player.id} />
      ))}
    </div>
  );
};

const mapStateToProps = (state: RootState, ownProps: Props) => ({
  players: ownProps.useHomepage ? state.players.playersHomepage : state.players.players,
  masterController: state.players.masterController,
});

const mapDispatchToProps = {
  setMasterPlayerIsMuted: setMasterPlayerIsMutedThunk,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(PlayerMasterController);
