// eslint-disable-next-line import/no-extraneous-dependencies
import { AnyAction } from 'redux';
// eslint-disable-next-line import/no-extraneous-dependencies
import { ThunkAction } from 'redux-thunk';
import { RootState } from 'app/redux';
import {
  PlayerVolumePayload,
  SetMasterPlayerMutedPayload,
  SetMasterPlayerPlayingPayload,
  SetMasterPlayerVolumePayload,
  SetOscillationPayload,
  SetPlayerPlayingPayload,
} from './players.interfaces';
import {
  clearOscillator,
  setMasterPlayerIsMuted,
  setMasterPlayerPlaying,
  setMasterPlayerVolume,
  setOscillatorState,
  setPlayerPlaying,
  setPlayersVolume,
  setVolumeAfterShuffle,
  startOscillator,
  stopPlayersAfterShuffle,
} from '.';

/**
 * Start player and mark controller as playing or not
 * @param payload
 * @returns {(dispatch, getState) => void}
 */
export const setPlayerPlayingThunk = (
  payload: SetPlayerPlayingPayload,
): ThunkAction<void, RootState, null, AnyAction> => (dispatch, getState) => {
  dispatch(setPlayerPlaying(payload));
  if (payload.isPlaying) {
    dispatch(setMasterPlayerPlaying({ isPlaying: true }));
    return;
  }

  const state = getState();
  if (state.players.useHomepage) {
    if (state.players.playersHomepage.every((player) => !player.isPlaying)) {
      dispatch(setMasterPlayerPlaying({ isPlaying: false }));
    }
    return;
  }

  if (state.players.players.every((player) => !player.isPlaying)) {
    dispatch(setMasterPlayerPlaying({ isPlaying: false }));
  }
};

/**
 * Thunk that handles master controller volume slider
 * @param payload
 * @returns {(dispatch, getState) => Promise<void>}
 */
export const setMasterPlayerVolumeThunk = (
  payload: SetMasterPlayerVolumePayload,
): ThunkAction<void, RootState, null, AnyAction> => async (dispatch, getState) => {
  dispatch(setMasterPlayerVolume(payload));
  const state = getState();
  if (payload.volume === 0) {
    dispatch(setMasterPlayerIsMuted({
      isMuted: true,
    }));
    return;
  }

  if (state.players.masterController.isMuted) {
    dispatch(setMasterPlayerIsMuted({
      isMuted: false,
    }));
  }
};

export const setMasterPlayerIsMutedThunk = (
  payload: SetMasterPlayerMutedPayload,
): ThunkAction<void, RootState, null, AnyAction> => async (dispatch, getState) => {
  const state = getState();

  if (payload.isMuted) {
    dispatch(setMasterPlayerIsMuted(payload));
    dispatch(setMasterPlayerVolume({
      volume: 0,
    }));
  } else {
    dispatch(setMasterPlayerVolume({
      volume: state.players.masterController.lastVolume === 0
        ? 0.01
        : state.players.masterController.lastVolume,
    }));
    dispatch(setMasterPlayerIsMuted(payload));
  }
};

/**
 * Thunk that set oscillation on/off and start oscillators for players
 * @param payload
 * @returns {(dispatch, getState) => Promise<void>}
 */
export const setOscillationThunk = (
  payload: SetOscillationPayload,
): ThunkAction<void, RootState, null, AnyAction> => async (dispatch, getState) => {
  const state = getState();

  if (payload.isOscillating) {
    state.players.players.forEach((player) => {
      if (!player.isPlaying) {
        return;
      }

      dispatch(startOscillator({
        playerId: player.id,
      }));
    });

    dispatch(setOscillatorState({
      isOscillating: true,
    }));

    return;
  }

  state.players.masterController.scheduledOscillation.forEach((oscillator) => {
    dispatch(clearOscillator({
      playerId: oscillator.playerId,
    }));
  });

  dispatch(setOscillatorState({
    isOscillating: false,
  }));
};

export const stopPlayersAfterShuffleThunk = (
  payload: PlayerVolumePayload[],
): ThunkAction<void, RootState, null, AnyAction> => async (dispatch) => {
  dispatch(stopPlayersAfterShuffle({
    items: payload,
  }));

  setTimeout(() => {
    dispatch(setVolumeAfterShuffle({
      items: payload,
    }));
  }, 1000);
};
