import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getBase, setShowShareModal } from '@noisli/app/dist/redux/modules/base';
import ShareModal from './ShareModal';

const ShareModalContainer: React.FC = () => {
  const dispatch = useDispatch();
  const { sharedCombo, sharedComboIsLoading, showShareModal } = useSelector(getBase);

  const onCloseClick = () => {
    dispatch(setShowShareModal({
      showShareModal: false,
    }));
  };

  return (
    <ShareModal
      isOpen={showShareModal}
      onCloseClick={onCloseClick}
      sharedCombo={sharedCombo}
      isLoading={sharedComboIsLoading}
    />
  );
};

export default ShareModalContainer;
