import React from 'react';
import Hls from 'hls.js';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from 'app/redux';
import { getPlayerInstance } from 'app/redux/modules/players/players.selectors';
import { setPlayerIsLoaded, setPlayerPlaying } from 'app/redux/modules/players';
import { setShowSoundsLockedModal as setShowSoundsLockedModalAction } from 'app/redux/modules/base';
import { SetPlayerIsLoadedPayload, SetPlayerPlayingPayload } from 'app/redux/modules/players/players.interfaces';
import { getUsers } from 'app/redux/modules/users';
import Player from './Player';
import { SetShowSoundsLockedModalPayload } from '../../../../redux/modules/base/base.interfaces';

interface Props {
  id: string;
}

enum SupportLevel {
  HLS,
  HLS_NATIVE,
  NO_SUPPORT,
}

const PlayerContainer: React.FC<Props> = ({ id }) => {
  const [supportLevel, setSupportLevel] = React.useState<SupportLevel>(SupportLevel.NO_SUPPORT);

  // Redux connect setup
  const dispatch = useDispatch();
  const playerInstance = useSelector((state: RootState) => getPlayerInstance(state, { id }));
  const masterController = useSelector((state: RootState) => state.players.masterController);
  const { user } = useSelector(getUsers);
  const setIsLoaded = (payload: SetPlayerIsLoadedPayload) => dispatch(setPlayerIsLoaded(payload));
  const setPlaying = (payload: SetPlayerPlayingPayload) => dispatch(setPlayerPlaying(payload));
  const setShowSoundsLockedModal = (payload: SetShowSoundsLockedModalPayload) => dispatch(
    setShowSoundsLockedModalAction(payload),
  );

  // Check hls support
  React.useEffect(() => {
    const video = document.createElement('video');
    if (Hls.isSupported()) {
      setSupportLevel(SupportLevel.HLS);
    } else if (video.canPlayType('application/vnd.apple.mpegurl')) {
      setSupportLevel(SupportLevel.HLS_NATIVE);
    } else {
      setSupportLevel(SupportLevel.NO_SUPPORT);
    }
  }, []);

  if (!playerInstance || supportLevel === SupportLevel.NO_SUPPORT) {
    return <></>;
  }

  // with native hls, we need to use audio instead of video, else we need to use video
  if (supportLevel === SupportLevel.HLS || supportLevel === SupportLevel.HLS_NATIVE) {
    return (
      <Player
        id={id}
        playerInstance={playerInstance}
        masterController={masterController}
        setIsLoaded={setIsLoaded}
        setPlaying={setPlaying}
        setShowSoundsLockedModal={setShowSoundsLockedModal}
        user={user}
        useAudio={supportLevel === SupportLevel.HLS_NATIVE}
      />
    );
  }

  return <></>;
};

export default PlayerContainer;
