/* eslint-disable no-param-reassign */
import { PayloadAction } from 'redux-starter-kit';
import {
  SubscriptionsState,
  SetSubscriptionPlansPayload,
  SetSubscriptionPayload,
  SetPaymentsHistoryPayload,
  SetTeamMembersPayload,
  AddTeamMemberPayload,
  RemoveTeamMemberPayload,
  SetHighRiskTransactionPayload,
  SetHighRiskTransactionModalIsOpenPayload,
  SetUserWasSubscriberPayload,
} from './subscriptions.interfaces';
import initialState from './subscriptions.state';
import { IsLoadingPayload } from '../common/isLoading.interface';

// eslint-disable-next-line import/prefer-default-export
export const setSubscriptionPlansReducer = (
  state: SubscriptionsState, action: PayloadAction<SetSubscriptionPlansPayload>,
) => {
  state.plans = action.payload.plans;
};

export const setIsLoadingPlansReducer = (
  state: SubscriptionsState, action: PayloadAction<IsLoadingPayload>,
) => {
  state.isLoadingPlans = action.payload.isLoading;
};

export const setSubscriptionReducer = (
  state: SubscriptionsState, action: PayloadAction<SetSubscriptionPayload>,
) => {
  state.currentSubscription = action.payload.subscription;
};

export const setPaymentHistoryReducer = (
  state: SubscriptionsState, action: PayloadAction<SetPaymentsHistoryPayload>,
) => {
  state.paymentHistory = action.payload.paymentHistory;
};

export const setTeamMembersReducer = (
  state: SubscriptionsState, action: PayloadAction<SetTeamMembersPayload>,
) => {
  state.teamMembers = action.payload.teamMembers;
};

export const addTeamMemberReducer = (
  state: SubscriptionsState, action: PayloadAction<AddTeamMemberPayload>,
) => {
  state.teamMembers.push(action.payload.teamMember);
};

export const removeTeamMemberReducer = (
  state: SubscriptionsState, action: PayloadAction<RemoveTeamMemberPayload>,
) => {
  state.teamMembers = state.teamMembers.filter(
    (teamMember) => teamMember.uuid !== action.payload.uuid,
  );
};

export const resetSubscriptionsReducer = (
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  state: SubscriptionsState,
) => {
  state = initialState;
};

export const setHighRiskTransactionReducer = (
  state: SubscriptionsState, action: PayloadAction<SetHighRiskTransactionPayload>,
) => {
  state.highRiskTransaction = action.payload.highRiskTransaction;
};

export const setHighRiskTransactionModalIsOpenReducer = (
  state: SubscriptionsState, action: PayloadAction<SetHighRiskTransactionModalIsOpenPayload>,
) => {
  state.highRiskTransactionModalIsOpen = action.payload.highRiskTransactionModalIsOpen;
};

export const setUserWasSubscriberReducer = (
  state: SubscriptionsState, action: PayloadAction<SetUserWasSubscriberPayload>,
) => {
  state.wasSubscriber = action.payload.wasSubscriber;
};
