import React from 'react';
import useOnClickOutside from 'use-onclickoutside';

type Handler = (event: Event) => void;

/**
 * Custom react hook to bind callbacks to native event listeners
 * instead using the preffered react event binding
 *
 * It is implemented and used because of bug that ruins the react event handlers
 * and the handler functions was not always firing leaving eg. tooltips open
 *
 * @param ref
 * @param onMouseEnter
 * @param onMouseLeave
 * @param onClickAway
 */
const useEnterLeaveEventListeners = <T extends HTMLElement>(
  ref: React.MutableRefObject<T | null>,
  onMouseEnter?: Handler,
  onMouseLeave?: Handler,
  onClickAway?: Handler,
) => {
  React.useEffect(() => {
    if (ref.current) {
      if (onMouseEnter) {
        ref.current.addEventListener('mouseenter', onMouseEnter);
      }

      if (onMouseLeave) {
        ref.current.addEventListener('mouseleave', onMouseLeave);
      }
    }

    return () => {
      if (ref.current) {
        if (onMouseEnter) {
          ref.current.removeEventListener('mouseenter', onMouseEnter);
        }

        if (onMouseLeave) {
          ref.current.removeEventListener('mouseleave', onMouseLeave);
        }
      }
    };
  }, [ref.current]);

  if (onClickAway) {
    useOnClickOutside(ref, onClickAway);
  }
};

export default useEnterLeaveEventListeners;
