import styled from 'styled-components';

export const InputWrapper = styled.div`
  display: flex;
  flex-direction: column;
  position: relative;
`;

export const InputContainer = styled.div`
  position: relative;
`;

export interface StyledInputProps {
  big?: boolean;
  hasError?: boolean;
  hasSuccess?: boolean;
}

// eslint-disable-next-line import/prefer-default-export
export const StyledInput = styled.input<StyledInputProps>`
  width: 100%;
  font-size: 13px;
  font-weight: 500;
  color: ${(props) => props.theme.colors.lightGrey1};
  line-height: 16px;
  padding: 5px 12px;
  height: 26px;
  border: none;
  border-radius: 13px;
  z-index: 1;
  
  ${(props) => props.big && `
    border-radius: 18px;
    padding: 10px 12px;
    height: 36px;
    line-height: 16px;
  `};

  &::placeholder {
    opacity: 0.5;
    font-weight: 500;
  }

  &:focus {
    outline: none;
  }

  &:disabled {
    opacity: 0.5;
  }
  
  &[type="number"] {
    appearance: textfield;
    
    &::-webkit-inner-spin-button, 
    &::-webkit-outer-spin-button { 
      -webkit-appearance: none;
    }
  }
`;

interface InputBorderProps {
  big?: boolean;
  hasError?: boolean;
  hasSuccess?: boolean;
}

export const InputBorder = styled.div<InputBorderProps>`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  
  &:before,
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 13px;
    
    ${(props) => props.big && `
      border-radius: 18px;
    `};
  }

  &:before {
    border: 1px solid ${(props) => props.theme.colors.black16};
  }

  &:after {
    border: 2px solid ${(props) => props.theme.colors.primary};
    opacity: 0;
    transition: opacity ${(props) => props.theme.transition}, border-color ${(props) => props.theme.transition};
    
    ${(props) => props.hasError && `
      border-color: ${props.theme.colors.error};
      border-width: 2px;
      opacity: 1;
    `}
    
    ${(props) => props.hasSuccess && `
      border-color: ${props.theme.colors.primary};
      border-width: 2px;
      opacity: 1;
    `}
  }

  ${StyledInput}:hover + &,
  ${StyledInput}:focus + & {
    &:after {
      opacity: 1;
      border-color: ${(props) => props.theme.colors.primary};
    }
  }
  
  ${StyledInput}:disabled + & {
    opacity: 0.5;
    
    &:after {
      opacity: 0;
      border-color: ${(props) => props.theme.colors.primary};
    }
  }
`;

interface InputErrorProps {
  errorAbsolute?: boolean;
}

export const InputError = styled.div<InputErrorProps>`
  padding: 4px 12px;
  font-size: 12px;
  line-height: 14px;
  font-weight: 500;
  color: ${(props) => props.theme.colors.error};
  
  ${(props) => props.errorAbsolute && `
    position: absolute;
    top: 100%;
  `}
`;

interface InputSuccessProps {
  successAbsolute?: boolean;
}

export const InputSuccess = styled.div<InputSuccessProps>`
  padding: 4px 12px;
  font-size: 12px;
  line-height: 14px;
  font-weight: 500;
  color: ${(props) => props.theme.colors.primary};
  
  ${(props) => props.successAbsolute && `
    position: absolute;
    top: 100%;
  `}
`;
