import styled, {
  css, CSSObject, SimpleInterpolation, FlattenSimpleInterpolation,
} from 'styled-components';

export enum Breakpoint {
  SMALL = 'SMALL',
  MEDIUM = 'MEDIUM',
  LARGE = 'LARGE',

  AUTH_LARGE = 'AUTH_LARGE',
  AUTH_MEDIUM = 'AUTH_MEDIUM',

  PAGES_LARGE = 'PAGES_LARGE',
}

type RWDFunction = (
  arg: CSSObject | TemplateStringsArray, ...interpolations: SimpleInterpolation[]
) => FlattenSimpleInterpolation;

export interface RWDObject {
  [Breakpoint.SMALL]: RWDFunction;
  [Breakpoint.MEDIUM]: RWDFunction;
  [Breakpoint.LARGE]: RWDFunction;

  [Breakpoint.AUTH_LARGE]: RWDFunction;
  [Breakpoint.AUTH_MEDIUM]: RWDFunction;

  [Breakpoint.PAGES_LARGE]: RWDFunction;
}

const sizes = {
  [Breakpoint.SMALL]: '576px',
  [Breakpoint.MEDIUM]: '768px',
  [Breakpoint.LARGE]: '992px',

  [Breakpoint.AUTH_MEDIUM]: '1024px',
  [Breakpoint.AUTH_LARGE]: '1200px',

  [Breakpoint.PAGES_LARGE]: '1200px',
};

/**
 * Helper rwd object to handler media breakpoints in styled components
 * @example
 * styled.div`
 *  width: 100%;
 *
 *  ${rwd[Breakpoint.MEDIUM]}`
 *    width: 50%;
 *  `
 * `
 * @type {RWDObject}
 */
const rwd = Object.keys(sizes).reduce<RWDObject>((acc, label) => {
  acc[(label as Breakpoint)] = (...args) => css`
    @media (min-width: ${sizes[(label as Breakpoint)]}) {
      ${css(...args)}
    }
  `;

  return acc;
}, ({} as RWDObject));

interface DisplayOnBreakpointProps {
  down?: boolean;
  up?: boolean;
  breakpoint: Breakpoint;
}

export const DisplayOnBreakpoint = styled.div<DisplayOnBreakpointProps>`
  ${(props) => props.down && css`
    display: block;

    ${rwd[props.breakpoint]`
      display: none;
    `}
  `}

  ${(props) => props.up && css`
    display: none;

    ${rwd[props.breakpoint]`
      display: block;
    `}
  `}
`;

export default rwd;
