import React from 'react';
import { GridWrapper } from '@noisli/app/dist/components/Grid/Grid.styled';
import {
  Wrapper, Title, SignUpLink, LogInButton,
} from './Onboarding.styled';
import DiscoverPlaylists from './Sections/DiscoverPlaylists';
import EnjoyYourFavorites from './Sections/EnjoyYourFavorites';
import AdvancedTimer from './Sections/AdvancedTimer';
import preloadImages from '../../utils/preloadImages';

export interface OnboardingSectionProps {
  image?: HTMLImageElement;
  setSectionId: (sectionId: number) => void;
}

const Onboarding = () => {
  const [sectionId, setSectionId] = React.useState(0);
  const [images, setImages] = React.useState<HTMLImageElement[]>([]);
  const timeoutId = React.useRef<number>(0);

  const switchToNextStep = (step: number) => {
    timeoutId.current = setTimeout(() => {
      if (step === 3) {
        setSectionId(0);
        switchToNextStep(1);
        return;
      }
      setSectionId(step);
      switchToNextStep(step + 1);
    }, 5000);
  };

  React.useEffect(() => {
    setImages(preloadImages([
      '/images/discover-playlists.png',
      '/images/enjoy-your-favorites.png',
      '/images/advanced-timer.png',
    ]));

    switchToNextStep(1);

    return () => {
      clearTimeout(timeoutId.current);
    };
  }, []);

  return (
    <Wrapper>
      <Title marginBottom>Welcome to Noisli!</Title>
      { sectionId === 0 && (
        <DiscoverPlaylists
          image={images[0]}
          setSectionId={setSectionId}
        />
      )}

      { sectionId === 1 && (
        <EnjoyYourFavorites
          image={images[1]}
          setSectionId={setSectionId}
        />
      )}

      { sectionId === 2 && (
        <AdvancedTimer
          image={images[2]}
          setSectionId={setSectionId}
        />
      )}

      <GridWrapper halfSpace>
        <LogInButton as="a" href={`${process.env.REACT_APP_PAGE_URL}/login`} target="_blank">Log In</LogInButton>
      </GridWrapper>
      <SignUpLink href={`${process.env.REACT_APP_PAGE_URL}/signup`} target="_blank">Sign Up for Free</SignUpLink>
    </Wrapper>
  );
};

export default Onboarding;
