import axios from './AxiosInstance';
import {
  CreateSharedComboDto,
  SharedCombo,
} from '@noisli/common';

/**
 * Static class that have all connection to API regarding Share module
 */
export default class ShareApi {
  static apiUrl = `${process.env.REACT_APP_API_URL}/share`;

  static async getSingle(uuid: string) {
    const response = await axios.get<SharedCombo>(`${ShareApi.apiUrl}/${uuid}`);
    const { data } = response;

    return data;
  }

  static async create(payload: CreateSharedComboDto) {
    const response = await axios.post<SharedCombo>(`${ShareApi.apiUrl}/create`, payload, {
      withCredentials: true,
    });
    const { data } = response;

    return data;
  }
}
