// eslint-disable-next-line import/no-extraneous-dependencies
import { AnyAction } from 'redux';
// eslint-disable-next-line import/no-extraneous-dependencies
import { ThunkAction } from 'redux-thunk';
import { IncomingMessage, OutgoingHttpHeaders } from 'http';
import { setCookie } from 'nookies';
import dayjs from 'dayjs';
import {
  LoginUserDto,
  CreateUserDto,
  ProfileUpdateDto,
  PreferencesUpdateDto,
  UserIncludes, UserRO, UserDetails, WithToken, WithMessage,
} from '@noisli/common';

import { RootState } from 'app/redux';
import UsersApi from 'app/Api/Users.api';
import {
  setIsLoading,
  setUser,
  setUserDetails,
  resetUser,
} from '.';
import {
  setShuffleTime, setShufflingState, setMasterPlayerIsMuted, setMasterPlayerVolume, stopAllPlayers,
} from '../players';
import { setFavourites } from '../favourites';
import { setAuthHeader } from '../../../components/withAuth/withAuth';
import { setSubscription, setUserWasSubscriber } from '../subscriptions';
import { setOscillationThunk } from '../players/players.thunks';
import { setText } from '../texts';
import { stopTimerThunk, setTimerConfigThunk } from '../timer/timer.thunks';
import { setHighRiskTransactionThunk } from '../subscriptions/subscriptions.thunks';

/**
 * Thunk that is responsible for setting data pulled from backend
 * @param userData
 * @returns {(dispatch, getState) => void}
 */
export const setUserDataWithIncludesThunk = (
  userData?: UserRO,
): ThunkAction<void, RootState, null, AnyAction> => (dispatch, getState) => {
  const { base, timer } = getState();
  const { isMobile } = base;

  if (!userData) {
    return;
  }
  dispatch(setUser(userData.user));

  if (userData.userDetails) {
    dispatch(setUserDetails(userData.userDetails));

    dispatch(setShuffleTime({
      time: userData.userDetails.shuffleTime,
    }));

    dispatch(setOscillationThunk({
      isOscillating: !isMobile && userData.userDetails.oscillation,
    }));

    dispatch(setShufflingState({
      isShuffling: !isMobile && userData.userDetails.shuffle,
    }));

    if (!timer.isActive) {
      dispatch(setMasterPlayerIsMuted({
        isMuted: userData.userDetails.masterVolumeMuted,
      }));

      dispatch(setMasterPlayerVolume({
        volume: userData.userDetails.masterVolume,
      }));
    }
  }

  if (userData.favourites) {
    dispatch(setFavourites(userData.favourites));
  }

  if (userData.subscription) {
    dispatch(setSubscription({
      subscription: userData.subscription,
    }));
  }

  if (userData.text) {
    dispatch(setText({
      text: userData.text,
    }));
  }

  if (userData.timerConfig) {
    dispatch(setTimerConfigThunk(userData.timerConfig));
  }

  if (userData.highRiskTransaction) {
    setHighRiskTransactionThunk(userData.highRiskTransaction);
  }

  if (userData.wasSubscriber) {
    dispatch(setUserWasSubscriber({
      wasSubscriber: userData.wasSubscriber,
    }));
  }
};

/**
 * Thunk used to login and set auth credentials of user
 * @param data
 * @returns {(dispatch) => Promise<void>}
 */
export const loginUserThunk = (
  data: WithToken<UserRO>,
): ThunkAction<Promise<void>, RootState, null, AnyAction> => async (
  dispatch,
) => {
  dispatch(setUserDataWithIncludesThunk(data));

  setCookie({}, 'jwt', data.token.accessToken, {
    expires: dayjs().add(data.token.expiresIn, 'second').toDate(),
    httpOnly: false,
    secure: true,
    path: '/',
  });

  setAuthHeader(data.token.accessToken);
};

export const logoutUserThunk = (

// eslint-disable-next-line @typescript-eslint/no-explicit-any
): ThunkAction<Promise<any>, RootState, null, AnyAction> => async (dispatch) => {
  dispatch(stopTimerThunk(true));
  dispatch(stopAllPlayers());
};

export const registerUserThunk = (
  payload: CreateUserDto,
): ThunkAction<Promise<void>, RootState, null, AnyAction> => async (dispatch) => {
  dispatch(setIsLoading({
    isLoading: true,
  }));

  try {
    await UsersApi.register(payload);
  } finally {
    dispatch(setIsLoading({
      isLoading: false,
    }));
  }
};

export const profileUpdateThunk = (
  { message, data }: WithMessage<UserRO>,
): ThunkAction<
  Promise<{ message: string; data: UserRO }>, RootState, null, AnyAction
> => async (dispatch) => {
  dispatch(setUser(data.user));

  if (data.userDetails) {
    dispatch(setUserDetails(data.userDetails));
    dispatch(setShuffleTime({
      time: data.userDetails.shuffleTime,
    }));
  }

  return {
    message,
    data,
  };
};

export const preferencesUpdateThunk = (
  payload: PreferencesUpdateDto,
): ThunkAction<Promise<void>, RootState, null, AnyAction> => async (dispatch) => {
  dispatch(setIsLoading({
    isLoading: true,
  }));

  try {
    const data = await UsersApi.preferencesUpdate(payload);

    dispatch(setUser(data.user));
    if (data.userDetails) {
      dispatch(setUserDetails(data.userDetails));
      dispatch(setShuffleTime({
        time: data.userDetails.shuffleTime,
      }));
    }
  } finally {
    dispatch(setIsLoading({
      isLoading: false,
    }));
  }
};

/**
 * Thunk that handles unauthorized error (401)
 * @returns {(dispatch) => Promise<void>}
 */
export const handleUserUnauthorizedThunk = (

): ThunkAction<void, RootState, null, AnyAction> => async (dispatch) => {
  dispatch(resetUser());
  window.location.replace('/logout/passthrough');
};
