// eslint-disable-next-line import/no-extraneous-dependencies
import createSelector from 'selectorator';
import { RootState } from 'app/redux';
import { getFavourites } from '.';
import { FavouritesState, Favourite } from './favourites.interfaces';

// eslint-disable-next-line import/prefer-default-export
export const getActiveFavourite = createSelector<RootState, Favourite | undefined>(
  [getFavourites],
  ({ favourites }: FavouritesState) => favourites.find((favourite) => favourite.isPlaying),
);
