import axios from './AxiosInstance';

/**
 * Static class that have all connection to API regarding Playlists Stats module
 */
export default class PlaylistsStatsApi {
  static apiUrl = `${process.env.REACT_APP_API_URL}/playlists-stats`;

  static async saveStat(name: string) {
    await axios.get(`${PlaylistsStatsApi.apiUrl}/${name}`, {
      withCredentials: true,
    });
  }
}
