import React from 'react';
import ArrowTooltip from '@noisli/app/dist/components/Tooltip/Tooltip';
import { useSelector } from 'react-redux';
import { getFavourites } from '@noisli/app/dist/redux/modules/favourites';
import { getUsers } from '@noisli/app/dist/redux/modules/users';
import { getIsUserFree } from '@noisli/app/dist/redux/modules/users/users.selectors';
import { Container } from './FavoritesSaveButton.styled';
import { MessageText } from '../../Layout/Header/Header.styled';

const limitReachedFreeUserText = (
  <MessageText>
    <a
      href={`${process.env.REACT_APP_PAGE_URL}/billing/change-plan?plan=Pro&cycle=year`}
      target="_blank"
      rel="noreferrer noopener"
    >
      Upgrade
    </a>
    {' to '}
    <strong>Pro</strong>
    {' to save'}
    <br />
    more Combos.
  </MessageText>
);

const FavoritesSaveButton = () => {
  const { favourites } = useSelector(getFavourites);
  const { user } = useSelector(getUsers);
  const isUserFree = useSelector(getIsUserFree);

  const [isTooltipOpen, setIsTooltipOpen] = React.useState(false);

  const reachedLimit = React.useMemo(() => {
    let reachedLimitLocal = false;

    if (isUserFree && favourites.length >= 5) {
      reachedLimitLocal = true;
    } else if (favourites.length >= 10) {
      reachedLimitLocal = true;
    }

    return reachedLimitLocal;
  }, [user, favourites]);

  const handleOnClick = () => {
    if (reachedLimit && isUserFree) {
      window.open(`${process.env.REACT_APP_PAGE_URL}/billing/change-plan?plan=Pro&cycle=year`, '_blank');
      return;
    }
    window.open(`${process.env.REACT_APP_PAGE_URL}/favorites`, '_blank');
  };

  const handleMouseEnter = () => {
    if (reachedLimit && isUserFree) {
      setIsTooltipOpen(true);
    }
  };

  if (reachedLimit && !isUserFree) {
    return <></>;
  }

  return (
    <span
      onMouseEnter={handleMouseEnter}
      onMouseLeave={() => setIsTooltipOpen(false)}
    >
      <ArrowTooltip
        white
        placement="top"
        open={isTooltipOpen}
        title={limitReachedFreeUserText}
      >
        <Container onClick={handleOnClick}>
          Create a Combo
        </Container>
      </ArrowTooltip>
    </span>
  );
};

export default FavoritesSaveButton;
