import {
  interval, concat, of,
} from 'rxjs';
import {
  mergeMap, debounce, map, catchError, tap,
} from 'rxjs/operators';
import { Epic, ofType } from 'redux-observable';
import { RootState } from 'app/redux';
// eslint-disable-next-line import/no-extraneous-dependencies
import { PayloadAction } from 'redux-starter-kit';
import { fromPromise } from 'rxjs/internal-compatibility';
import dayjs, { Dayjs } from 'dayjs';
import { setIsLoading, setText } from '.';
import TextApi from '../../../Api/Text.api';
import { SetTextPayload } from './texts.interfaces';
import catchAuthError from '../../utils/catchAuthError';

let writingTimestamp: Dayjs | null = null;

/**
 * Epic that saves text if text is changed
 * First we set writing timestamp if timestamp is null
 * it is debounced with set period 500ms, to prevent constant saving of text
 * If action is not debounced, calculate timestamp then send request to server to save text
 * after save, remove timestamp and loading
 * @param action$
 */
const saveTextEpic: Epic<
// eslint-disable-next-line @typescript-eslint/no-explicit-any
  PayloadAction<SetTextPayload, string>, any, RootState
> = (action$) => action$.pipe(
  ofType(setText.toString()),
  tap(() => {
    if (writingTimestamp === null) {
      writingTimestamp = dayjs();
    }
  }),
  debounce(() => interval(500)),
  mergeMap((action) => {
    let timeDiff = 0;
    if (writingTimestamp) {
      timeDiff = dayjs().diff(writingTimestamp, 'minute', true);
    }
    const apiCall$ = fromPromise(TextApi.saveText({
      text: action.payload.text,
      writingTime: timeDiff,
    })).pipe(
      tap(() => { writingTimestamp = null; }),
      map(() => setIsLoading({
        isLoading: false,
      })),
      catchError(catchAuthError),
    );

    return concat(
      of(setIsLoading({
        isLoading: true,
      })),
      apiCall$,
    );
  }),
);

export default [
  saveTextEpic,
];
