// eslint-disable-next-line import/no-extraneous-dependencies
import {
  MiddlewareAPI, Dispatch, Action,
} from 'redux';
import { setUser } from '@noisli/app/dist/redux/modules/users';
import { stopAllPlayers } from '@noisli/app/dist/redux/modules/players';
import { PayloadAction } from 'redux-starter-kit';
import { User } from '@noisli/common';
import { RootState } from '../../background';

const UserAccountChangeObserver = (
  { dispatch, getState }: MiddlewareAPI<Dispatch, RootState>,
) => (next: Dispatch) => (action: Action) => {
  const state = getState();
  const actualUser = state.users.user;

  switch (action.type) {
    case (setUser.toString()): {
      const { payload } = (action as PayloadAction<User>);
      if (actualUser.id && actualUser.accountType !== payload.accountType) {
        // eslint-disable-next-line no-undef
        dispatch(stopAllPlayers());
      }
      next(action);
      break;
    }

    default:
      next(action);
      break;
  }
};

export default UserAccountChangeObserver;
