import React from 'react';
import MuiTooltip, { TooltipProps } from '@material-ui/core/Tooltip';
import { Arrow, TooltipMessage } from './Tooltip.styled';

export interface ArrowTooltipProps extends TooltipProps {
  useHomepage?: boolean;
  white?: boolean;
  disablePortal?: boolean;
  hideTooltip?: boolean;
  halfSpaceTop?: boolean;
}

function ArrowTooltip({
  useHomepage, hideTooltip, white, title, halfSpaceTop, disablePortal, ...props
}: ArrowTooltipProps) {
  const [arrowRef, setArrowRef] = React.useState<HTMLSpanElement | null>(null);
  const classes: TooltipProps['classes'] = {};

  if (useHomepage) {
    classes.tooltip = 'MuiTooltip-tooltip--homepage';
  }

  if (white) {
    classes.tooltip = 'MuiTooltip-tooltip--white';
  }

  if (hideTooltip) {
    classes.tooltip += ' MuiTooltip-tooltip--hidden';
  }

  if (halfSpaceTop) {
    classes.tooltip += ' MuiTooltip-tooltip--halfSpaceTop';
  }

  return (
    <MuiTooltip
      enterTouchDelay={0}
      classes={classes}
      placement="right"
      PopperProps={{
        disablePortal,
        popperOptions: {
          modifiers: {
            flip: {
              enabled: false,
            },
            arrow: {
              enabled: Boolean(arrowRef),
              element: arrowRef,
            },
          },
        },
      }}
      {...props}
      title={(
        <>
          <TooltipMessage useHomepage={useHomepage} white={white}>{title}</TooltipMessage>
          <Arrow ref={setArrowRef} />
        </>
      )}
    />
  );
}

export default ArrowTooltip;
