import { createSlice } from 'redux-starter-kit';
import state from './playlists.state';
import {
  unmarkActivePlaylistComboReducer,
  markActivePlaylistComboReducer,
  setPlaylistComboIsShufflingReducer,
  markActiveComboReducer,
  unmarkActiveComboReducer,
  setPlaylistsReducer,
  setPlaylistsSliderPositionReducer,
  setPlaylistBackgroundReducer,
} from './playlists.reducers';

const playlistsSlice = createSlice({
  slice: 'playlists',

  initialState: state,

  reducers: {
    unmarkActivePlaylistCombo: unmarkActivePlaylistComboReducer,
    markActivePlaylistCombo: markActivePlaylistComboReducer,
    setPlaylistComboIsShuffling: setPlaylistComboIsShufflingReducer,
    markActiveCombo: markActiveComboReducer,
    unmarkActiveCombo: unmarkActiveComboReducer,
    setPlaylists: setPlaylistsReducer,
    setPlaylistsSliderPosition: setPlaylistsSliderPositionReducer,
    setPlaylistBackground: setPlaylistBackgroundReducer,
  },
});
// Extract the action creators object and the reducer
const { actions, reducer, selectors } = playlistsSlice;
// Extract and export each action creator by name
export const {
  unmarkActivePlaylistCombo,
  markActivePlaylistCombo,
  setPlaylistComboIsShuffling,
  markActiveCombo,
  unmarkActiveCombo,
  setPlaylists,
  setPlaylistsSliderPosition,
  setPlaylistBackground,
} = actions;

export const { getPlaylists } = selectors;
// Export the reducer, either as a default or named export
export default reducer;
