/**
 * Thunk that gets sessions made by user today
 * and add them to session completed for day
 * Also gets all sessions completed of user
 * @returns {(dispatch, getState) => Promise<void>}
 */
export const getSessionCountForDayThunk = (
): ThunkAction<Promise<void>, RootState, null, AnyAction> => async (dispatch, getState) => {
  const { timer } = getState();
  // if (timer.sessionsForDay !== undefined) {
  //   return;
  // }

  const data = await TimerApi.getSessionCountForDay({
    day: dayjs().format('YYYY-MM-DD'),
  });

  dispatch(setSessionsForDay({
    sessionsForDay: data.sessionCount,
  }));

  dispatch(setAllSessionsCompleted({
    allSessionsCompleted: data.allTimeSessionCount,
  }));
};

export const startTimerThunk = (
): ThunkAction<Promise<void>, RootState, null, AnyAction> => async (dispatch, getState) => {
  const state = getState();

  if (!state.timer.timerConfig.wasTimerRun) {
    dispatch(updateTimerConfig({
      timerConfig: {
        wasTimerRun: true,
      },
    }));
  }

  dispatch(startTimer());
  dispatch(setTimerIsActive({
    isActive: true,
  }));
};

export const pauseTimerThunk = (
): ThunkAction<void, RootState, null, AnyAction> => (dispatch, getState) => {
  const { users, players, timer } = getState();

  if (!players.masterController.isMuted) {
    dispatch(setMasterPlayerIsMutedThunk({
      isMuted: true,
      mutedByTimer: true,
    }));
  }

  dispatch(pauseTimer());

  if (users.user.accountType !== AccountType.FREE && timer.timerConfig.showTimerOnBrowserTab) {
    dispatch(setTimerTitle({
      title: 'Paused',
    }));
  }
};

export const resumeTimerThunk = (
): ThunkAction<void, RootState, null, AnyAction> => (dispatch, getState) => {
  const { players } = getState();

  if (players.masterController.isMuted) {
    dispatch(setMasterPlayerIsMutedThunk({
      isMuted: false,
      mutedByTimer: true,
    }));
  }

  dispatch(resumeTimer());
};

/**
 * This thunk is similar in construction as {createSharedComboThunk}
 * @returns {(dispatch, getState) => (any[])}
 */
export const setActiveComboInTimerThunk = (
): ThunkAction<ComboItem[], RootState, null, AnyAction> => (dispatch, getState) => {
  const state = getState();
  const activeFavourite = getActiveFavourite(state);
  const activePlaylist = getActivePlaylist(state);
  let timerCombo: TimerCombo = {} as unknown as TimerCombo;

  const items = state.players.players.filter((player) => player.isPlaying).map((player) => ({
    id: player.id,
    volume: player.volume,
  }));

  if (!items.length) {
    return [];
  }

  if (!state.timer.timerConfig.wasTimerRun) {
    return items;
  }

  if (activeFavourite) {
    timerCombo = {
      type: ShareType.FAVOURITE,
      content: {
        favourite: {
          id: activeFavourite.id,
          name: activeFavourite.name,
        },
        items: activeFavourite.items,
      },
    };
  } else if (activePlaylist) {
    const activePlaylistCombo = getActivePlaylistCombo(state);

    if (activePlaylistCombo) {
      timerCombo = {
        type: ShareType.PLAYLIST,
        content: {
          items: activePlaylistCombo.items,
          playlist: {
            name: activePlaylist.name,
            id: activePlaylist.id,
            comboId: activePlaylistCombo.id,
          },
        },
      };
    }
  } else {
    timerCombo = {
      type: ShareType.CUSTOM_COMBO,
      content: {
        items,
      },
    };
  }

  dispatch(setActiveTimerCombo({
    timerCombo,
  }));

  return items;
};

/**
 * This thunk is similar in behavior as {setSharedComboPlayingThunk}
 * @returns {(dispatch, getState) => void}
 */
export const setTimerComboPlayingThunk = (

): ThunkAction<void, RootState, null, AnyAction> => (dispatch, getState) => {
  const { timer, playlists, users } = getState();
  const { activeCombo } = timer;

  if (!activeCombo) {
    return;
  }

  if (activeCombo.type === ShareType.PLAYLIST) {
    const playlistId = (activeCombo.content.playlist as SharedComboPlaylist).id;
    const playlistToPlay = playlists.playlists.find((playlist) => playlist.id === playlistId);

    if (playlistToPlay && playlistToPlay.pro && users.user.accountType === AccountType.FREE) {
      dispatch(setShowSoundsLockedModal({
        showSoundsLockedModal: true,
      }));
      dispatch(setActiveTimerCombo({
        timerCombo: undefined,
      }));
      return;
    }

    if (playlistToPlay) {
      dispatch(setPlaylistsSliderPosition({
        position: playlists.playlists.indexOf(playlistToPlay),
      }));
    }
  }

  dispatch(stopAllPlayers());

  activeCombo.content.items.forEach((item) => {
    dispatch(setPlayerVolume({
      id: item.id,
      volume: item.volume,
    }));
    dispatch(setPlayerPlayingThunk({
      id: item.id,
      isPlaying: true,
    }));
  });

  switch (activeCombo.type) {
    case ShareType.PLAYLIST:
      dispatch(markActivePlaylistCombo({
        playlistId: (activeCombo.content.playlist as SharedComboPlaylist).id,
        comboId: (activeCombo.content.playlist as SharedComboPlaylist).comboId,
      }));

      break;

    case ShareType.FAVOURITE:
      dispatch(markActiveFavourite({
        id: (activeCombo.content.favourite as SharedComboFavourite).id,
      }));
      break;

    default:
      break;
  }
};

/**
 * Large thunk that is responsible for handling all state changes that goes after timer is stopped
 *
 * @param isCancelled
 * @param isSkipped
 * @returns {(dispatch, getState) => void}
 */
export const stopTimerThunk = (
  isCancelled = false,
  isSkipped = false,
): ThunkAction<void, RootState, null, AnyAction> => (dispatch, getState) => {
  const {
    timer, users, players,
  } = getState();
  const { user } = users;
  const { masterController } = players;
  const { timerConfig, sessionsCompleted, activeCombo, isPaused } = timer;
  const {
    time, longBreak, shortBreak, workInSession,
  } = timerConfig;
  const isUserFree = user.accountType === AccountType.FREE;
  const previousTimerType = timer.currentTimerType;

  let currentTimerType = TimerType.Session;

  if (isCancelled) {
    dispatch(setActiveComboInTimerThunk());
    dispatch(setTimerIsActive({
      isActive: false,
    }));

    if (isPaused) {
      dispatch(stopAllPlayers());
    }
  }

  const isLongBreakSkipped = timer.currentTimerType === TimerType.LongBreak && isSkipped;

  // if user is not free and work in session is active, change timer type
  if (!isUserFree && timerConfig.workInSession && !isCancelled) {
    if (timer.currentTimerType === TimerType.Session) {
      if (sessionsCompleted + 1 === timerConfig.sessionsCount) {
        currentTimerType = TimerType.LongBreak;
      } else {
        currentTimerType = TimerType.ShortBreak;
      }
    } else {
      currentTimerType = TimerType.Session;
    }
  }

  dispatch(setCurrentTimerType({
    currentTimerType,
  }));

  // get current time of timer type from config
  let actualTime = formatActualTime(time.hours, time.minutes, time.seconds);

  if (currentTimerType === TimerType.ShortBreak) {
    actualTime = formatActualTime(shortBreak.hours, shortBreak.minutes, shortBreak.seconds);
  } else if (currentTimerType === TimerType.LongBreak) {
    actualTime = formatActualTime(longBreak.hours, longBreak.minutes, longBreak.seconds);
  }

  dispatch(stopTimer({
    isCancelled,
    isLongBreakSkipped,
  }));

  dispatch(setActualTime({
    actualTime,
  }));

  // if session was not cancelled, save it
  if (!isCancelled) {
    dispatch(setSessionsForDay({
      sessionsForDay: timer.sessionsForDay ? timer.sessionsForDay + 1 : 1,
    }));

    dispatch(incrementAllSessionsCompleted());
  }

  // if work in session is enabled, save session as completed or reset if type was long break
  if (!isCancelled && !isUserFree && workInSession) {
    if (previousTimerType === TimerType.Session) {
      dispatch(setSessionsCompleted({
        sessionsCompleted: sessionsCompleted + 1,
      }));
    }

    if (previousTimerType === TimerType.LongBreak) {
      dispatch(setSessionsCompleted({
        sessionsCompleted: 0,
      }));
    }
  }

  // if work in session and autostart is enabled, run next session
  if (!isCancelled && !isUserFree && timerConfig.workInSession
    && timerConfig.autoStart && previousTimerType !== TimerType.LongBreak
  ) {
    dispatch(startTimerThunk());

    // if next session is of type session, play last played combo
    if (currentTimerType === TimerType.Session && !!activeCombo && !masterController.isPlaying) {
      dispatch(setTimerComboPlayingThunk());
    }
  }

  // if user set time end notification, play it now
  if (!isCancelled && !isUserFree && timerConfig.timeEndNotification
    && previousTimerType === TimerType.Session
  ) {
    playWebAudioTimeEndNotification().then((sound) => sound?.start(0));
  }

  // if user set break end notifiaction, play it now
  if (!isCancelled && !isUserFree && timerConfig.breakEndNotification
    && previousTimerType !== TimerType.Session
  ) {
    playWebAudioBreakEndNotification().then((sound) => sound?.start(0));
  }

  // if user set web notifications, push it now
  if (!isCancelled && !isUserFree && timerConfig.webNotification && Push.Permission.has()) {
    sendPushNotification(previousTimerType);
  }

  // if user set show timer on browser tab and does not use autostart, set title now
  if (!isUserFree && timerConfig.showTimerOnBrowserTab && !timerConfig.autoStart) {
    dispatch(setTimerTitle({
      title: setTimerPageTitle(
        currentTimerType,
        actualTime,
        true,
      ),
    }));
  }

  // if session that was stopped right now was not cancelled, save completed session for today
  if (!isCancelled && previousTimerType === TimerType.Session) {
    TimerApi.saveCompletedSession({
      day: dayjs().format('YYYY-MM-DD'),
    });
  }

  // if user set show timer on browser tab and is not working in session
  if (!isUserFree && timerConfig.showTimerOnBrowserTab && !isCancelled
    && (!timerConfig.workInSession || previousTimerType !== TimerType.Session)
  ) {
    dispatch(setTimerTitle({
      title: formatSessionTime(actualTime),
    }));
  }

  // set title if timer was cancelled
  if (!isUserFree && timerConfig.showTimerOnBrowserTab && isCancelled) {
    dispatch(setTimerTitle({
      title: formatSessionTime(actualTime),
    }));
  }

  // update config if timer was not cancelled
  if (!isCancelled) {
    const curState = getState();
    dispatch(updateTimerConfig({
      timerConfig: {
        savedSessionState: {
          sessionCount: curState.timer.sessionsCompleted,
          sessionType: curState.timer.currentTimerType,
        },
      },
    }));
  }
};

export const dismissSessionThunk = (
): ThunkAction<void, RootState, null, AnyAction> => async (dispatch, getState) => {
  const { players } = getState();

  dispatch(stopTimerThunk(true));

  if (players.masterController.isMuted) {
    setTimeout(() => {
      dispatch(setMasterPlayerIsMutedThunk({
        isMuted: false,
      }));
    }, 500);
  }

  dispatch(setSessionsCompleted({
    sessionsCompleted: 0,
  }));

  dispatch(resetSavedSessionState());
};

/**
 * Thunk that sets timer config recived from db
 * It respects if timer is running or not and drop update if timer is runnig
 * @param timerConfig
 * @returns {(dispatch, getState) => Promise<void>}
 */
export const setTimerConfigThunk = (
  timerConfig: TimerConfig,
): ThunkAction<void, RootState, null, AnyAction> => async (dispatch, getState) => {
  dispatch(setTimerConfig({
    timerConfig,
  }));

  const state = getState();
  const { isActive } = state.timer;
  let { actualTime } = state.timer;

  if (!isActive) {
    if (timerConfig.savedSessionState?.sessionType !== undefined) {
      dispatch(setCurrentTimerType({
        currentTimerType: timerConfig.savedSessionState.sessionType,
      }));
      dispatch(setSessionsCompleted({
        sessionsCompleted: timerConfig.savedSessionState.sessionCount,
      }));

      const { time, shortBreak, longBreak } = timerConfig;

      actualTime = formatActualTime(time.hours, time.minutes, time.seconds);

      if (timerConfig.savedSessionState.sessionType === TimerType.ShortBreak) {
        actualTime = formatActualTime(shortBreak.hours, shortBreak.minutes, shortBreak.seconds);
      } else if (timerConfig.savedSessionState.sessionType === TimerType.LongBreak) {
        actualTime = formatActualTime(longBreak.hours, longBreak.minutes, longBreak.seconds);
      }

      dispatch(setActualTime({
        actualTime,
      }));
    }

    if (!isActive && timerConfig.showTimerOnBrowserTab) {
      dispatch(setTimerTitle({
        title: formatSessionTime(actualTime),
      }));
    }
  }
};

export const updateTimerConfigThunk = (
  timerConfig: Partial<TimerConfig>,
): ThunkAction<void, RootState, null, AnyAction> => async (dispatch, getState) => {
  dispatch(updateTimerConfig({
    timerConfig,
  }));

  const state = getState();
  const { isRunning, actualTime } = state.timer;

  if (!isRunning && timerConfig.showTimerOnBrowserTab) {
    dispatch(setTimerTitle({
      title: formatSessionTime(actualTime),
    }));
  }
};

export const reloadTimerConfigThunk = (
  timerConfig: Partial<TimerConfig>,
): ThunkAction<void, RootState, null, AnyAction> => async (dispatch, getState) => {
  dispatch(reloadTimerConfig({
    timerConfig,
  }));

  const state = getState();
  const { isRunning, actualTime } = state.timer;

  if (!isRunning && timerConfig.showTimerOnBrowserTab) {
    dispatch(setTimerTitle({
      title: formatSessionTime(actualTime),
    }));
  }
};

export const saveReplayCount = (): ThunkAction<void, RootState, null, AnyAction> => async (dispatch) => {
  const timerConfig = await TimerApi.getConfig();
  dispatch(updateTimerConfigThunk({
    replayCount: timerConfig.replayCount + 1,
  }));
};
