/**
 * Generator of new color for background shuffler
 * @returns {Generator<string, void, unknown>}
 */
export function* colorCycler(): IterableIterator<string> {
  while (true) {
    const index = random(0, CyclingBackgroundColors.length - 1);
    yield CyclingBackgroundColors[index];
  }
}
