import React from 'react';
import { OscillationIcon } from '@noisli/icons';
import { useSelector } from 'react-redux';
import { getPlayers } from 'app/redux/modules/players';
import { AccountType } from '@noisli/common';
import { getBase } from 'app/redux/modules/base';
import { getUsers } from '../../redux/modules/users';
import ArrowTooltip from '../Tooltip/Tooltip';
import { OscillationButton } from './TopMenu.styled';
import useEnterLeaveEventListeners from '../../utils/hooks/useEnterLeaveEventListeners';

const baseFreeUserMessage = (
  <span>
    {'Oscillation is a '}
    <strong>Pro</strong>
    {' feature.'}
    <br />
    Upgrade to enjoy it.
  </span>
);

interface Props {
  onClick: React.MouseEventHandler;
  freeUserMessage?: React.ReactNode;
  toTop?: boolean;
}

/**
 * Oscillation button wrapper
 * Props can be used to change behavior - used in chrome extension
 * @param onClick
 * @param toTop
 * @param freeUserMessage
 * @returns {any}
 * @constructor
 */
const TopMenuOscillationButton: React.FC<Props> = ({
  onClick, toTop, freeUserMessage = baseFreeUserMessage,
}) => {
  const { masterController } = useSelector(getPlayers);
  const { user } = useSelector(getUsers);
  const { isMobile } = useSelector(getBase);

  const [isTooltipOpen, setIsTooltipOpen] = React.useState(false);

  const buttonWrapper = React.useRef<HTMLSpanElement>(null);

  const handleOnMouseEnter = () => {
    if (user.accountType === AccountType.FREE || isMobile) {
      setIsTooltipOpen(true);
    }
  };

  const handleOnMouseLeave = () => {
    setIsTooltipOpen(false);
  };

  useEnterLeaveEventListeners<HTMLSpanElement>(
    buttonWrapper,
    handleOnMouseEnter,
    handleOnMouseLeave,
    handleOnMouseLeave,
  );

  const tooltipText = React.useMemo(() => {
    if (user.accountType === AccountType.FREE) {
      return freeUserMessage;
    }

    if (isMobile) {
      return (
        <span>
          Oscillation is not available
          <br />
          on mobile devices.
        </span>
      );
    }

    return <></>;
  }, [user.accountType, isMobile]);

  return (
    <span
      ref={buttonWrapper}
      onClick={handleOnMouseEnter}
      onTouchEnd={handleOnMouseEnter}
      style={{ width: 30, display: 'inline-flex' }}
    >
      <ArrowTooltip
        open={isTooltipOpen}
        title={tooltipText}
        placement={toTop ? 'top' : 'bottom'}
        white
        disablePortal={toTop}
      >
        <span>
          <OscillationButton
            as="span"
            active={masterController.isOscillating}
            disabled={user.accountType === AccountType.FREE || isMobile}
            iconButton
            title="oscillation"
            onClick={onClick}
          >
            <OscillationIcon />
          </OscillationButton>
        </span>
      </ArrowTooltip>
    </span>
  );
};

export default TopMenuOscillationButton;
