import Hls from 'hls.js';

type OnManifestParse = () => void;

/**
 * HLS.js wrapper designed to work with react players
 */
export default class HlsInstance {
  mediaEl: HTMLMediaElement | null;

  hls: Hls | null = null;

  onManifestParse: OnManifestParse;

  /**
   * Class constructor that set up hls instance
   * @param mediaEl
   * @param url
   * @param onManifestParse
   */
  constructor(mediaEl: HTMLMediaElement | null, url: string, onManifestParse: OnManifestParse) {
    this.mediaEl = mediaEl;
    this.onManifestParse = onManifestParse;
    this.initializeHls(url);
    this.setErrorListeners();
  }

  /**
   * Initialize hls instance with player url
   * Choose to go with Hls.js or native hls (ios)
   * @param url
   */
  private initializeHls(url: string) {
    if (Hls.isSupported()) {
      this.hls = new Hls({
        enableWorker: true,
        maxBufferSize: 10,
        maxMaxBufferLength: 30,
      });
      this.hls.loadSource(url);
      if (this.mediaEl) {
        this.hls.attachMedia(this.mediaEl as HTMLVideoElement);
        this.hls.on(Hls.Events.MANIFEST_PARSED, this.onManifestParse);
      }
    } else if (this.mediaEl && this.mediaEl.canPlayType('application/vnd.apple.mpegurl')) {
      this.mediaEl.src = url;
      this.mediaEl.addEventListener('loadedmetadata', this.onManifestParse);
    }
  }

  private setErrorListeners() {
    if (!this.hls) {
      return;
    }

    this.hls.on(Hls.Events.ERROR, (event, data) => {
      if (!this.hls) {
        return;
      }
      if (data.fatal) {
        switch (data.type) {
          case Hls.ErrorTypes.NETWORK_ERROR:
            this.hls.startLoad();
            break;
          case Hls.ErrorTypes.MEDIA_ERROR:
            this.hls.recoverMediaError();
            break;
          default:
            this.hls.destroy();
            break;
        }
      }
    });
  }

  destroy() {
    if (!this.hls) {
      return;
    }

    this.hls.detachMedia();
  }
}
